      this.$nextTick(() => {
        var vm = this;
        this.mescroll = new MeScroll("mescroll", {//第一个参数"mescroll"对应上面布局结构div的id
          down: {
            callback: vm.refresh, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
          },
          // up: {
          // callback: this.refresh() //上拉加载回调,简写callback:function(page){upCallback(page);}
          // }
