        if(this.isFirst){
          this.isFirst = false;
          return;
        }
        this.refreshing = true
        if (this.isDebug) {
          //测试刷新后的数据
          this.refreshDevicesLists([
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 32513,
              "id": 294,
              "address": "5_5",
              "devViewCode": 0,
              "status": "{\n\t\"status\":0\n}",
              "sceneIds": "",
              "name": "下拉刷新"
            },
          ]),
