              this.mescroll.endSuccess();//无参
              // this.$refs.loadmore.onTopLoaded();
            }, 2000)
        } else {
          console.log("gmj ++++刷新")
          //调用自定义的验证插件
          Cordova.exec(this.refreshDevStatusSuccessFunction, this.refreshDevStatusFailFunction, "SmartHomePlugins",
