          var deviceAddress = this.isOperateDeviceAdress;
          var type = 2;
          var oper = -1;

          var inC = typeof(this.innerState) == 'undefined' ? -1 : this.innerState;
          var out = typeof(this.outterState) == 'undefined' ? -1 : this.outterState;
          console.log("++++++操作双控窗帘++++++" + " deviceAddress " + deviceAddress + "  type " + type + " oper " + oper + "  inC " + inC + " out " + out);
          //操作双控窗帘
          Cordova.exec(this.operateSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
            "operateDCurtain", [deviceAddress, type, oper, inC, out]);
        } else {
          var deviceAddress = this.isOperateDeviceAdress;
          var oper = -1;
          var status = this.state;
          console.log("++++++操作单控窗帘++++++" + " deviceAddress " + deviceAddress + " status " + status + " oper " + oper);
          //调用自定义的验证插件
          Cordova.exec(this.operateSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
            "operateSCurtain", [deviceAddress, oper, status]);
        }

      },

      operateSuccessFunction: function (data) {
        this.showNativeToast("操作成功");
      },

      operateFailFunction: function (data) {
        this.showNativeToast("失败：" + data);
      },


      changeCurtainValue: function (data) {
        let status = eval("(" + data.status + ")")
        this.isToChangeZindex = true;
        this.isToPop = true;
        this.curtainName = data.name;
        this.isOperateDeviceAdress = data.address;
        if (data.deviceTypeCode == 0x0009) {
          console.log("单控")
          this.isDoubleCurtain = false;
          //刷新数据显示
          this.state = status.status
          //双控
        } else if (data.deviceTypeCode == 0x0013) {
          //刷新数据显示
          this.isDoubleCurtain = true;
          this.innerState = status.in
          this.outterState = status.out
        }

      },
      /*************************窗帘控制  the end *******************************/


      /*************************灯控制 *******************************/
      clickToChangeLightStatus: function (device) {
        console.log(" 控制前  " + JSON.stringify(device));
        var deviceAddress = device.address;
        var sendOper = eval("(" + device.status + ")").status == 1 ? 0 : 1;
        if (this.isDebug) {
          device.status = eval("(" + device.status + ")").status == 1 ? 0 : 1;
          sendOper = device.status;
          console.log("  +++++++++++++发送控制命令+++++++++  " + " deviceAddress " + deviceAddress + " Oper " + sendOper);
          //更新界面显示
          this.refreshControlDeviceState({
            "hostId": 8,
            "region": 1,
            "deviceTypeCode": 32513,
            "id": 294,
            "address": "5_5",
            "devViewCode": "{\n\t\"devViewCode\":1\n}",
            "status": "{\n\t\"status\":1\n}",
            "sceneIds": "",
            "name": "测试"
          });
          this.refreshListsByActions();
        } else {
          console.log("  +++++++++++++发送控制命令+++++++++  " + " deviceAddress " + deviceAddress + " oper " + sendOper);
          Cordova.exec(this.operateSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
            "operateLight", [deviceAddress,sendOper]);
        }
      },
      /*************************灯控制 the end *******************************/

      /*************************按照设备类型筛选设备*******************************/
      //在下拉框中点击某类图片
      selectDeviceType: function (index) {
        var item;
        if (index == "1") {
          item = {name: '灯', deviceTypeCode: 0x7F01};
        } else if (index == "2") {
          item = {name: "空调", deviceTypeCode: 77777777}
        } else if (index == "3") {
          item = {name: '锁', deviceTypeCode: 0x000B};
        } else if (index == "4") {
          item = {name: '窗帘', deviceTypeCode: 88888888};
        } else if (index == "8") {
          item = {name: '排气扇', deviceTypeCode: 0x7F08};
        } else {
          this.showNativeToast("无此类型设备")
          return
        }

        this.refreshCurrentDeviveTypeObj(item);
        this.refreshListsByActions()
        this.toggle();
      },

      //点击设备类型文字
      clickTypeItem: function (item) {
        this.refreshCurrentDeviveTypeObj(item);
        this.refreshListsByActions()
      },
      /*************************按照设备类型筛选设备   the end *******************************/


      /*************************下拉框*******************************/
      toggle() {
        this.open = !this.open;
        if (this.open) {
          //展开
          this.moreDeviceTypeImage = require("../../../static/image/cancel.png");
        } else {
          this.moreDeviceTypeImage = require("../../../static/image/more_deviceType.png");
        }
      },
      handleClose(e) {
        this.open = false
      },
      /*************************下拉框 the end *******************************/
      // 长按修改名字-------------------start
      changeDevName:function (index) {
        this.devIndex = index;
        this.startX = event.targetTouches[0].pageX;
        this.startY = event.targetTouches[0].pageY;
        // 再次清理定时器，防止重复注册定时器
        window.clearInterval(this.Loop);
        this.Loop = window.setInterval(this.IntervalDev,800);
      },

      IntervalDev:function () {
        var _this = this;
        this.clickStatus = false;
        MessageBox.prompt("",{
          title:'更改名称',
          message:'请输入新的设备名',
          showConfirmButton:true,
          showCancelButton:true,
          closeOnClickModal:true,
          showInput:true,
          inputValue:this.devicesList[this.devIndex].name
        }).then((val)=>{
          // 这里调用更改名称接口:val.value
          _this.devicesList[_this.devIndex].name = val.value;
          this.refreshControlDeviceState(_this.devicesList[_this.devIndex]);
          Cordova.exec(this.getUserDeviceListSuccess, this.getUserDeviceListFail,"SmartHomePlugins","modifyDeviceName",[val.value,this.devicesList[this.devIndex].address]);
        })
      },

      clearDevLoop:function () {
        window.clearInterval(this.Loop);
      },

      clearDete:function (event) {
        let DvaX = event.targetTouches[0].pageX - this.startX;
        let DvaY = event.targetTouches[0].pageY - this.startY;
        if (DvaX != 0 || DvaY != 0) {
          // 取消删除事件
          console.log("不触发事件");
          this.isShowPop = false;
          window.clearInterval(this.Loop);
        }
      }
      // 长按修改名字-------------------end

    }
  }
</script>


