<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div style="height: 100%;position: relative">
    <!--引入标题栏-->
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :is-left-click="true" :back-src="backSrc" @LeftBttonClick='backControllerList'></titleBar>
    <img class="WifiImage" v-on:click="clickToWiFiConfig" src="../../../../static/image/ic_wifi.png"/>

    <!--内容页面-->
    <div class="udpConfigWrapper">
    <mu-divider/>
      <!--搜索主机-->
      <div style="flex: 1;display: flex;flex-direction: column;background-color: white">
        <!--UTSmartHome-->
        <p class="wifiHint">当前网络: UTSmartHome 正常</p>
        <div style="flex: 1;display: flex;flex-direction: column;position: relative">
          <div style="flex: 1;margin-top: 20px">
            <!--进度条-->
            <mu-circular-progress mode="determinate" :size="60" :strokeWidth="5" :value="value" color="lightgreen"/>
          </div>

          <div class="progress">
            <!--进度数值-->
            <p class="progressValue">正在搜索主机 {{value}}%</p>
          </div>
        </div>
      </div>

      <!--主机列表内容-->
    <div class="controllerItemDiv_udpConfig">
      <div class="addControllerhint">发现设备</div>
      <mu-divider/>
      <!--主机列表-->
      <div class="demo-grid">
        <!--主机列表-->
        <mu-row>
          <mu-col class="controllerItem" v-for="(device,index) in devicesList" width="100" tablet="100" desktop="100">
            <div class="blueToothClass">
              <mu-list-item :title="device.name">
                <mu-icon v-if="device.isCurrentHost==1" v-on:click="addController(index)" slot="right" value=" "
                         class="checkIconUrl"/>
                <mu-icon v-else="" v-on:click="addController(index)" slot="right" value=" " class="localImageUrl"/>
              </mu-list-item>
            </div>
            <mu-divider/>
          </mu-col>
        </mu-row>
      </div>

    </div>
    <mu-dialog :open="showDialog" title="验证主机密码">
      <mu-text-field type="password" v-model="pwdValue" hintText="请输入密码" underlineClass="line"/>
      <!--<p class="hint2">原始密码是什么？</p>-->
      <mu-flat-button slot="actions" @click="close" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="confirm" label="确定"/>
    </mu-dialog>

    <mu-dialog :open="showAddingConfigProgress" title="正在加载配置文件">
      <mu-linear-progress color="blue"/>
      <p style="text-align: center">正在加载配置文件 {{addingConfigFileValue}}%</p>
    </mu-dialog>

  </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: '添加主机',
        isTitleBarBackImageVisible: true,
        isSaveVisible: false,
        backSrc: "static/image/close.png",
        //查询到的数据
        devicesList: [],
        //原始数据
        OriginDeviceList: [],
        value: 0,
        addingConfigFileValue: 0,
        showDialog: false,
        showAddingConfigProgress: false,
        addingConfigFile: false,
        //点击某一条
        clickControllerIndex: 0,
        pwdValue: '',
        //
        num: 0,
      }
    },
    mounted() {
      this.timer = setInterval(() => {
        this.value += 10;
        //10s 搜索主机
        if (this.value > 90) clearInterval(this.timer)
      }, 1000)

      window.onReceivePushData = this.onReceivePushData;
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";

    },
    beforeDestroy() {
      clearInterval(this.timer)
    },

    created() {
      if (this.isDebug) {
        //临时数据
        this.devicesList = [{
          "mac": "00-04-A3-E4-62-EA", "name": "智能控制主机1", "status": {"status": 0},
          "id": "", "ip": "192.168.1.101", "password": "", "isCurrentHost": 0, "port": "5001"
        },
          {
            "mac": "00-04-A3-E4-62-EA",
            "name": "智能控制主机2",
            "status": {"status": 0},
            "id": "",
            "ip": "192.168.1.101",
            "password": "",
            "isCurrentHost": 0,
            "port": "5001"
          }
        ];

      } else {
        Cordova.exec(this.searchHostSuccess, this.searchHostFail, "SmartHomePlugins",
          "searchHost", []);
      }
    },

    methods: {
      searchHostSuccess: function () {
        console.log("+++++++++++开始搜索主机+++++++++++")
      },
      searchHostFail: function () {
        console.log("+++++++++++开始搜索主机++++ 失败+++++++")
      },

      backControllerList: function () {
        this.$router.push(
          {
            path: '/myControllerList',
            query: {
              fromPageName: this.$route.query.fromPageName,
            }
          }
        );
      },

      //跳转到主机设置页面
      clickToWiFiConfig: function () {
        this.$router.push(
          {
            path: '/wifiConfig'
          }
        );
      },
      //添加主机
      addController: function (index) {
        console.log("添加主机")
        this.clickControllerIndex = index;
        this.showDialog = true;
      },
      confirm() {
        //关闭弹窗
        this.showDialog = false;
        if (this.isDebug) {

        } else {
          //添加主机
          var mac = this.devicesList[this.clickControllerIndex].mac
          console.log("验证主机mac " + mac + "验证密码" + this.pwdValue);
          Cordova.exec(this.addHostSuccess, this.addHostFail, "SmartHomePlugins",
            "addHost", [mac, this.pwdValue]);
        }


//        this.showAddingConfigProgress = true;
//        this.timer2 = setInterval(() => {
//          this.addingConfigFileValue += 10;
//
//          if (this.addingConfigFileValue > 20) {
//            this.showAddingConfigProgress = false;
//            //加载成功此条显示对号
//            this.devicesList[this.clickControllerIndex].isCurrentHost = true;
//            //
//            //加载完毕关闭窗口
//            this.addingConfigFile = true;
//          }
//
//        }, 1000)

      },

      addHostSuccess: function () {
        console.log("验证主机密码成功 ");
        this.devicesList[this.clickControllerIndex].isCurrentHost = 1;
        this.showNativeToast("添加主机成功")

      },
      addHostFail: function (msg) {
        console.log("登录主机失败 ");
        this.showNativeToast(msg)
      },

      close() {
        //关闭弹窗
        this.showDialog = false
      },


      onReceivePushData: function (data) {
        if (data instanceof Object) {
          var code = data.code;
          if (code == "200") {
            console.log("+++++++++++++收到设备状态推送消息：+++++++++++++" + data.data.deviceTypeCode);
          } else if (code == "101") {

            console.log("+++++++++++++收到主机状态推送消息：+++++++++++++" + data.data);
          } else if (code == "102") {
            //"data":{"mac":"00-04-A3-E4-62-EA","name":"智能控制主机","status":{"status":0},
            // "id":"","ip":"192.168.1.101","password":"","isCurrentHost":0,"port":"5001"}}
            console.log("+++++++++++++搜索到主机数据++++++++++++：" + JSON.stringify(data.data));
            this.devicesList[this.num] = data.data;
            this.num++;
            console.log("+++++++++++++第几条++++++++++++：" + this.num);
          }
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  .udpConfigWrapper{
    margin-top: 64px;
    height: calc(100% - 64px);
    overflow: scroll;
    background-color: #f9f9f9;
    display: flex;
    flex-direction: column;
  }
  .imageBackControllerList {
    visibility: visible;
    position: absolute;
    float: left;
    z-index: 999999;
    width: 40px;
    height: 40px;
    background-size: auto 40px;
    background-image: url(../../../../static/image/back_white_icon.png);
    top: 10px;
  }

  .controllerItemDiv_udpConfig {
    flex: 3;
  }

  .mu-dialog-title {
    padding-top: 24px;
    padding-bottom: 24px;
    margin-left: 17%;
    /*text-align: center;*/
  }

  .mu-text-field-focus-line {
    background-color: rgba(0, 0, 0, .12)
  }

  .line {
    background-color: rgba(0, 0, 0, .12);
    height: 1px;
  }

  .blueToothClass {
    position: relative;
    background-color: white

  }

  .blueToothClass .material-icons.localImageUrl {
    width: 24px;
    height: 24px;
    background-size: auto 24px;
    background-image: url(../../../../static/image/add_icon.png)
  }

  .blueToothClass .material-icons.checkIconUrl {
    width: 30px;
    height: 30px;
    background-size: auto 30px;
    background-image: url(../../../../static/image/check_icon.png)
  }

  .progressValue {
    flex: 1;
    float: left;
    font-size: small;
    margin-bottom: 20px;
  }

  .progress {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .wifiHint {
    text-align: left;
    margin-left: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .searchProgress {
    position: fixed;
    align-items: center;
    width: 100%;
    flex-direction: column;
    background-color: lightgrey;
    display: flex;
  }

  .progressValue {
    font-size: medium;
    vertical-align: middle;
  }

  .demo-grid {
    flex: 2;
  }

  .WifiImage {
    display: none;
    position: absolute;
    top: 0;
    right: 0;
    width: 40px;
    height: 40px;
    margin-right: 8px;
    z-index: 999999;
    margin-top: 10px;
  }

  .demo-grid div[class*="col-"] {
    margin-bottom: 0px;
    padding: 0px;
  }

  .controllerItem {
    background-color: white;
    width: 100%;
  }

  .no_fix_height_wapper {
    background-color: lightgrey;
  }

  .addControllerhint {
    font-size: 16px;
    color: black;
    opacity: 0.5;
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .hint2 {
    font-size: small;
    opacity: 0.7;
    height: 20px;
    margin-bottom: 0px;
    margin-left: 20px;
    text-align: right;
  }

  html, body, wrapper {
    height: 100%;
    /*overflow:hidden;*/
  }

  /*更改样式，使行内文字左对齐;*/
  .mu-item.show-right {
    width: 100%;
    text-align: left;
  }

  .mu-item {
    text-align: left;
  }
</style>



