<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="wapper" id="wrapper">
    <!-- 引入标题 -->
    <div id="title_top">
      <userTitle :isImg=false titleName="登录"></userTitle>
    </div>
    <!-- 页面部分 -->
    <div id="wrapper_paper">
      <!-- 登录框 -->
      <div id="user_login">
        <div class="user_text">
          <div class="item_left">
            <img class="items_img" src="static/image/user_icon.png">
          </div>
          <div class="items_input">
            <input id="userName" type="tel" v-model="userPhone" placeholder="请输入手机号" autocomplete="off"
                   />
          </div>
          <div class="login-deletemobile" style="display: none" @click="reset"></div>
        </div>
        <div class="user_text" id="user_pwd">
          <div class="item_left">
            <img class="items_img" src="static/image/password_icon.png">
          </div>
          <div class="items_input">
            <input id="userPwd" type="password" v-model="userPwd" placeholder="6位以上数字和字母组合密码"
                   autocomplete="off" />
          </div>
          <div class="login-concealment" style="display: none" @click="showPwd"></div>
        </div>
        <div class="login_button" @click="login">
          <span class="tipText">登录</span>
        </div>
        <div class="otherOp">
          <div class="otherOpL" @click="toRegiste">
            <span>新用户注册</span>
          </div>
          <div class="otherOpR" @click="toGetPwd">
            <span>忘记密码?</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        // userData: {
          userPhone: '',
          userPwd: ''
        // }
      }
    },
    created(){
      let account = this.$route.query.account;
      let pwd = this.$route.query.password;
      if((typeof(account) != 'undefined') && (typeof(pwd) != 'undefined')){
        console.log("11account="+account+" pwd="+pwd);
        this.userPhone = account;
        this.userPwd = pwd;
      }
    },
    watch:{
      userPhone:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("login-deletemobile")[0].style.display = "none";
        }else{
          document.getElementsByClassName("login-deletemobile")[0].style.display = "";
        }
        if (!/^\d+$/.test(newVal) && newVal != ""){
           this.userPhone = oldVal;
        }
      },
      userPwd:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("login-concealment")[0].style.display = "none";
        }else{
          document.getElementsByClassName("login-concealment")[0].style.display = "";
        }
      }
    },

    mounted() {
      window.onLoginReturn = this.onLoginReturn;

    },

    methods: {
      onLoginReturn(message) {
        if (message == 1) {
          console.log("登录成功");
          //获取当前主机
          Cordova.exec(this.getCurrentHostSuccess, this.getCurrentHostFail, "SmartHomePlugins",
            "getCurrentHost", []);
        } else {

        }
      },

      toRegiste() {
        this.$router.push('/register');
      },
      toGetPwd() {
        this.$router.push('/getPwd')
      },
      login() {
        //调试模式
        if (this.isDebug) {
//          this.$router.push({
//            path: '/myControllerList',
//            query: {
//              fromPageName:"userLogin"
//            }
//          })
          this.$router.push({
            path: '/indexHome_page',
          })
          return;
        }


        let userPhone = this.userPhone.trim();
        let userPwd = this.userPwd.trim();
        let regUser = /^1[3|4|5|7|8][0-9]{9}$/;
        regUser = new RegExp(regUser);
        let regPwd = /(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6}/;
        regPwd = new RegExp(regPwd);
        console.log(userPhone + "  " + userPwd);
        if (userPhone.length == 0 || userPhone == "") {

          console.log("用户名不能为空");
          this.showNativeToast("手机号不能为空");
        } else if (userPwd == "" || userPwd.length == 0) {
          console.log("密码不能为空");
          this.showNativeToast("密码不能为空");
        } else if (!regUser.test(userPhone)) {
          this.showNativeToast("手机号码格式不正确，请确认");
          // } else if (!regPwd.test(userPwd)) {
          //   console.log("密码验证不通过");
          //   this.showNativeToast("密码验证不通过");
        } else {
          //  执行登录操作
          console.log("登录吧");
          if (this.isDebug) {
            this.$router.push('/indexHome_page')
          } else {
            //调用自定义的验证插件
            console.log('iscordova' + '账号：' + userPhone + '密码：' + userPwd)
            Cordova.exec(this.loginAppSuccessFunction, this.loginAppFailFunction, "SmartHomePlugins",
              "loginApp", [userPhone, userPwd]);
          }
        }
      }
      ,

      //登录成功
      loginAppSuccessFunction: function (message) {
        this.showNativeToast("登录成功");
        var obj = eval("(" + message + ")");
        //获取当前主机
        Cordova.exec(this.getCurrentHostSuccess, this.getCurrentHostFail, "SmartHomePlugins",
          "getCurrentHost", []);

      }
      ,
      loginAppFailFunction: function (data) {
        this.showNativeToast("登录失败:"+data);
        console.log("登录失败 ");
      }
      ,

      getCurrentHostSuccess: function () {
        console.log("++++++++++获取当前主机成功++++++++++");
        this.$router.push('/indexHome_page');
      }
      ,

      getCurrentHostFail: function () {
        console.log("++++++++++获取当前主机失败++++++++++");
        // this.$router.push('/myControllerList');

        this.$router.push({
          path: '/myControllerList',
          query: {
            fromPageName: "userLogin"
          }
        })
      }
      ,

      showToast() {
        this.toast = true
        if (this.toastTimer) clearTimeout(this.toastTimer)
        this.toastTimer = setTimeout(() => {
          this.toast = false
        }, 2000)
      }
      ,

      reset() {
        //  涉及到了vue的虚拟dom与生命周期
        this.userPhone = "";
        this.$nextTick(() => {
          let username = document.getElementById("userName");
          username.value = "";
          document.getElementsByClassName("login-deletemobile")[0].style.display = "none";
        })
      }
      ,

      showPwd() {
        let userPwd = document.getElementById("userPwd");
        if (userPwd.getAttribute("type") == "text") {
          userPwd.setAttribute("type", "password");
        } else {
          userPwd.setAttribute("type", "text");
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  * {
    margin: 0px;
    padding: 0px;

  #wrapper {
    height: 100%;
    width: 100%;

  #title_top {
    height: 60px;
  }

  }
  }

  #wrapper_paper {
    background-color: #f9f9f9;
    height: 100%;
    /* 解决样式重叠
       父div使用
     */
    overflow: hidden;
  }

  #user_login {
    height: 45%;
    margin-top: 7%;
    overflow: hidden;
    width: 100%;
  }

  .user_text {
    border: 1px solid #dfdfdf;
    height: 16%;
    margin: 8px auto;
    width: 80%;
    background-color: white;
    display: inline-flex;
    align-items: center;
  }

  .item_left {
    width: auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center
  }

  .items_img {
    width: 35%;
    // margin-top: 25%;
  }

  .items_input {
    width: 80%;
    height: 100%;

  input {
    width: 100%;
    height: 100%;
    border: 0px;
    font-size: 14px;
  }

  }

  .login-concealment {
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/concealment_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 5px;
  }

  .login-deletemobile {
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/delete_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 5px;
  }

  .login_button {
    width: 80%;
    // height: 15%;
    background-color: #5bc8ff;
    font-family: PingFangLight;
    margin: 18px auto 10px;
    overflow: hidden;
    line-height: 50px;
  }

  .tipText {
    color: white;
    font-size: medium;
  }

  .otherOp {
    width: 80%;
    margin: 0 auto;
    margin-top: 5px;
    display: inline-block;

  .otherOpL {
    float: left;
    color: #666666;
    font-family: PingFangLight;
    font-size: small;
  }

  .otherOpR {
    float: right;
    color: #666666;
    font-family: PingFangLight;
    font-size: small;
  }

  }

  @font-face {
    font-family: PingFangLight;
    src: url('../../../../static/font/PingFangLight.ttf');
  }

</style>



