<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar v-bind:title-name="name" v-bind:is-visible="isTitleBarBackImageVisible" :back-src="backSrc"></titleBar>
    <div class="searchProgress">
      <mu-circular-progress mode="determinate" :size="60" :strokeWidth="5" :value="value" color="lightgreen"/>
      <div class="progress">
        <p class="progressValue">正在搜索蓝牙 {{value}}%</p>
      </div>
    </div>

    <div class="controllerItemDiv">
      <p class="hint">发现蓝牙设备</p>
      <mu-divider/>
      <!--主机列表-->
      <div class="demo-grid">
        <!--主机列表-->
        <mu-row>
          <mu-col class="controllerItem" v-for="(device,index) in devicesList" width="100" tablet="100" desktop="100">
            <div class="blueToothClass">
              <mu-list-item title=blueTooth>
                <mu-icon v-if="device.isAddedController" v-on:click="addController(index)" slot="right" value=" "
                         class="checkIconUrl"/>
                <mu-icon v-else="" v-on:click="addController(index)" slot="right" value=" " class="localImageUrl"/>
              </mu-list-item>
            </div>
            <mu-divider />
          </mu-col>
        </mu-row>
      </div>
    </div>
    <mu-dialog :open="showDialog" title="通过xxxx按键激活蓝牙">
      <img class='configImage' src="../../../../static/image/wifi_config_image.jpg"/>
      <div class="Dialog">
        <mu-raised-button v-on:click="close" label="不在提醒" class="leftButton" primary/>
        <mu-raised-button v-on:click="confirm" label="已经按键激活" class="rightButton" primary/>
      </div>
    </mu-dialog>

    <mu-dialog :open="connecting" title="正在连接蓝牙设备">
      <mu-linear-progress color="blue"/>
      <p style="text-align: center">正在连接 {{connectingValue}}%</p>
    </mu-dialog>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        name: '添加蓝牙',
        isTitleBarBackImageVisible: true,
        //查询到的数据
        devicesList: [],
        //提示
        showDialog: true,
        //原始数据
        OriginDeviceList: [],
        value: 0,
        connectingValue: 0,
        connecting: false,
        //点击某一条
        clickControllerIndex: 0,
        backSrc:"./../../../static/image/back_white_icon.png",
      }
    },
    mounted() {

    },
    beforeDestroy() {
      clearInterval(this.timer)
    },

    created() {

    },

    methods: {

      //添加主机
      addController: function (index) {
        console.log("添加主机")
        this.clickControllerIndex = index;
        //显示连接中
        this.connecting = true;
        this.timer2 = setInterval(() => {
          this.connectingValue += 10;

          if (this.connectingValue > 20) {
            this.connecting = false;

            //加载成此条显示对号
            this.devicesList[this.clickControllerIndex].isAddedController = true;
            //加载完毕关闭窗口
          }
        }, 1000)
      },
      confirm() {
        //关闭弹窗
        this.showDialog = false;
        //搜索蓝牙设备
        this.timer = setInterval(() => {
          //console.log("timer")
          this.value += 10;
          //10s 搜索主机
          if (this.value > 90)
            clearInterval(this.timer)
        }, 1000)

      },
      close() {
        //关闭弹窗
        this.showDialog = false;
        //搜索蓝牙设备
        this.timer = setInterval(() => {
          //console.log("timer")
          this.value += 10;
          //10s 搜索主机
          if (this.value > 90)
            clearInterval(this.timer)
        }, 1000),
          //临时数据
          this.OriginDeviceList = [
            {isAddedController: false, deviceType: '开关', name: '开关', state: '0'},
            {isAddedController: false, deviceType: '蓝牙锁', name: '蓝牙锁', state: '1'},
            {isAddedController: false, deviceType: '开关', name: '开关', state: '0'},
            {isAddedController: false, deviceType: '蓝牙锁', name: '蓝牙锁', state: '1'},

          ],
          //更新数据展示
          this.devicesList = this.OriginDeviceList;

      },

    },

  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .controllerItemDiv {
    background-color: lightgrey;
    margin-top: 264px;
    font-size: large;
    height: calc(100% - 200px - 64px);
  }

  .Dialog {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
  }

  .leftButton {
    flex: 1;
    margin-right: 5px;
    background-color: #4cc6ff;
  }

  .rightButton {
    flex: 1;
    background-color: #4cc6ff;
  }

  .mu-dialog-title {
    margin-left: 18px;
  }

  .mu-text-field-focus-line {
    background-color: rgba(0, 0, 0, .12)
  }

  .line {
    background-color: rgba(0, 0, 0, .12);
    height: 1px;
  }

  .blueToothClass {
    position: relative;
    background-color: white

  }

  .blueToothClass .material-icons.localImageUrl {
    width: 30px;
    height: 30px;
    background-size: auto 30px;
    background-image: url(../../../../static/image/add_icon.png)
  }

  .blueToothClass .material-icons.checkIconUrl {
    width: 30px;
    height: 30px;
    background-size: auto 30px;
    background-image: url(../../../../static/image/check_icon.png)
  }

  .configImage {
    height: 150px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center 0;
  }

  .progressValue {
    flex: 1;
    float: left;
    font-size: small;
  }

  .progress {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .wifiHint {
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 10px;
    margin-top: 8px;
  }

  .searchProgress {
    margin-top: 64px;
    position: fixed;
    align-items: center;
    flex: 1;
    width: 100%;
    height: 200px;
    flex-direction: column;
    background-color: white;
    padding: 15%;
  }

  .progressValue {
    font-size: medium;
    vertical-align: middle;
  }

  .demo-grid {
    flex: 2;
  }

  .demo-grid div[class*="col-"] {
    margin-bottom: 0px;
    padding: 0px;
  }

  .controllerItem {
    background-color: white;
    width: 100%;
  }

  .no_fix_height_wapper {
    background-color: lightgrey;
  }

  .hint {
    font-size: small;
    opacity: 0.7;
    height: 20px;
    margin-bottom: 0px;
    margin-left: 20px;
    text-align: left;
  }

  .hint2 {
    font-size: small;
    opacity: 0.7;
    height: 20px;
    margin-bottom: 0px;
    margin-left: 20px;
    text-align: right;
  }

  html, body, wrapper {
    height: 100%;
    /*overflow:hidden;*/
  }

  /*更改样式，使行内文字左对齐;*/
  .mu-item.show-right {
    width: 100%;
    text-align: left;
  }

  .mu-item {
    text-align: left;
  }
</style>



