<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper" id="wapper">
    <!--  引入标题栏  -->
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              @clickRightButton="clickPowerBtn" :isImg="isImg" :src="srcImg" :DesWord="DesWord"
              :back-src="backSrc"></titleBar>
    <div class="wrapperPage">
      <div class="slideButton">
        <div class="slide" :style="slide_backgrand">
          <canvas id="canvas">
          </canvas>
          <div id="slide_text_des">
            <div :style="font_color">
              <span class="slide_text_word" :class="{temFontSize:(InitialValue.printTemp >16)}">{{ InitialValue.printTemp >16 ? "未知":16 + InitialValue.printTemp }}</span>
              <span class="slide_text_fu" v-show="InitialValue.printTemp <= 16"><sup>°C</sup></span>
            </div>
            <div id="slide_text_low">
              <div id="slide_text_img"><img src="../../../../static/image/temp_meter.png"/></div>
              <div id="slide_text_img_text">室温:&nbsp; {{ InitialValue.indoorTem }}<span><sup>°C</sup></span></div>
            </div>
            <div id="slide_text_two_temp" :style="font_color">
              <div id="temp1">16</div>
              <div id="temp2">32</div>
            </div>
          </div>
        </div>
        <div class="SButton" :style="SButton_backgrand">
          <div class="LitButton" :id="index+1+'LitButton'" v-bind:class="{addId:(clickModeItem-1 == index)}"
               @click="ChangeStatus(index+1,InitialValue.Switch,$event)"
               v-for="(item,index) in Fiveitems">
            <div class="LitButton_img">
              <img v-if="InitialValue.Switch" :src="item.imgPic1">
              <img v-else :src="item.imgPic2">
            </div>
            <div class="LitButton_word">
              <span v-if="InitialValue.Switch" style="color: white">{{item.name}}</span>
              <span v-else style="color:#827e7e">{{item.name}}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="adjustButton">
        <div class="adButtonDown" @click="turnDown(InitialValue.Switch)">
          <img src="../../../../static/image/minus_icon.png" v-if="InitialValue.Switch"
               style="width: 30px;height: 30px;margin-top: 13%">
          <img src="../../../../static/image/minus_icon.png" v-else style="width: 30px;height: 30px;margin-top: 13%">
        </div>
        <div class="adButtonUp" @click="turnUp(InitialValue.Switch)">
          <img src="../../../../static/image/add_icon.png" v-if="InitialValue.Switch"
               style="width: 30px;height: 30px;margin-top: 13%">
          <img src="../../../../static/image/add_icon.png" v-else style="width: 30px;height: 30px;margin-top: 13%">
        </div>
      </div>
      <div class="controlButton">
        <div class="cLButton" @click.shop="ChangeSixofOne(1,InitialValue.Switch,$event,devCode)">
          <div class="cLBimg">
            <img :src="this.SixItems[0].imgPic1" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.sleepTurn">
            <img :src="this.SixItems[0].imgPic2" v-else>
          </div>
          <div class="cLBword">睡眠</div>
        </div>
        <div class="cLButton" @click.shop="ChangeSixofOne(2,InitialValue.Switch,$event,devCode)">
          <div class="cLBimg">
            <img :src="this.SixItems[1].imgPic1" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windLTR">
            <img :src="this.SixItems[1].imgPic2" v-else>
          </div>
          <div class="cLBword">左右扫风</div>
        </div>
        <div class="cLButton" @click.shop="ChangeSixofOne(3,InitialValue.Switch,$event,devCode)">
          <div class="cLBimg">
            <img :src="this.SixItems[2].imgPic1" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windUTD">
            <img :src="this.SixItems[2].imgPic2" v-else>
          </div>
          <div class="cLBword">上下扫风</div>
        </div>
        <div class="cLButton" @click.shop="ChangeSixofOne(4,InitialValue.Switch,$event,devCode)">
          <div class="cLBimg" v-if="devCode == 0x0005">
            <img :src="this.SixItems[3].imgPic1" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0">
            <img :src="this.SixItems[3].imgPic2"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1">
            <img :src="this.SixItems[3].imgPic3"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 2">
            <img :src="this.SixItems[3].imgPic4"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 3">
            <img :src="this.SixItems[3].imgPic5" v-else>
          </div>
          <div class="cLBimg" v-else-if="devCode == 0x0006">
            <img :src="this.SixItems[3].imgPic2" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0">
            <img :src="this.SixItems[3].imgPic3"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1">
            <img :src="this.SixItems[3].imgPic4" v-else>
          </div>
          <div class="cLBimg" v-else-if="devCode == 0x0011">
            <img :src="this.SixItems[3].imgPic1" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0">
            <img :src="this.SixItems[3].imgPic2"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1">
            <img :src="this.SixItems[3].imgPic3"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 2">
            <img :src="this.SixItems[3].imgPic4"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 3">
            <img :src="this.SixItems[3].imgPic5"
                 v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 4">
            <img :src="this.SixItems[3].imgPic6" v-else>
          </div>
          <div class="cLBword">风速</div>
          <!-- 大金 -->
          <div v-if="devCode == 0x0005">
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0" :style="ErrCol">
              超低速
            </div>
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1" :style="ErrCol">
              低速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 2" :style="ErrCol">
              中速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 3" :style="ErrCol">
              高速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 4" :style="ErrCol">
              超高速
            </div>
          </div>
          <!-- 格力1拖2 -->
          <div v-if="devCode == 0x0006">
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0" :style="ErrCol">
              低速
            </div>
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1" :style="ErrCol">
              中速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 2" :style="ErrCol">
              高速
            </div>
          </div>
          <!-- 格力1拖1 -->
          <div v-if="devCode == 0x0011">
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 0" :style="ErrCol">
              超低速
            </div>
            <div class="clBEr" v-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 1" :style="ErrCol">
              低速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 2" :style="ErrCol">
              中速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 3" :style="ErrCol">
              高速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed == 4" :style="ErrCol">
              超高速
            </div>
            <div class="clBEr" v-else-if="InitialValue.Switch&&isModeHorR&&InitialValue.windSpeed== 5" :style="ErrCol">
              自动
            </div>
          </div>
        </div>
        <div class="cLButton" @click.shop="false && ChangeSixofOne(5,InitialValue.Switch,$event,devCode)"><!--暂时关闭这个功能-->
          <div class="cLBimg">
            <img :src="this.SixItems[4].imgPic1" v-if="InitialValue.Switch&&InitialValue.timing != 0">
            <img :src="this.SixItems[4].imgPic2" v-else>
          </div>
          <div class="cLBword">定时</div><!--暂时关闭这个功能-->
          <!--<div class="clBEr" v-if="InitialValue.Switch&&InitialValue.timing == 0" :style="ErrCol">关闭</div>-->
          <!--<div class="clBEr" v-else-if="InitialValue.Switch&&InitialValue.timing == 0.5" :style="ErrCol">00:30</div>-->
          <!--<div class="clBEr" v-else-if="InitialValue.Switch&&InitialValue.timing == 1" :style="ErrCol">01:00</div>-->
          <!--<div class="clBEr" v-else-if="InitialValue.Switch&&InitialValue.timing == 1.5" :style="ErrCol">01:30</div>-->
          <!--<div class="clBEr" v-else-if="InitialValue.Switch&&InitialValue.timing == 2" :style="ErrCol">02:00</div>-->
          <!--<div v-else></div>-->
        </div>
        <div class="cLButton" @click.shop="ChangeSixofOne(6,InitialValue.Switch,$event,devCode)">
          <div class="cLBimg">
            <img :src="this.SixItems[5].imgPic1" v-if="InitialValue.Switch">
            <img :src="this.SixItems[5].imgPic2" v-else>
          </div>
          <div class="cLBword">更多</div>
        </div>
      </div>
    </div>

    <!-- 定时弹窗 -->
    <div class="popupDiv">
      <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="isSelectFrequency">
        <mu-appbar title="定时功能">
          <mu-icon-button slot="left" @click='closeFrequency' class="mu-icon-button-close"/>
          <mu-flat-button color="black" label="确定" slot="right" @click='confirmFrequency'/>
        </mu-appbar>
        <mu-divider/>
        <mu-picker :slots="addressSlots" :visible-item-count="5" @change="addressChange" :values="frequencyNumber"/>
        <div class="frequencyTxt">小时</div>
      </mu-popup>
    </div>
  </div>
</template>

<script>

  // 用于动画的绘制
  class Draw {
    constructor(el) {
      this.el = el;
      this.canvas = document.getElementById(this.el);
      this.canvas.width = document.getElementsByClassName("slide")[0].offsetWidth;
      this.canvas.height = document.getElementsByClassName("slide")[0].offsetHeight;
      this.ctx = this.canvas.getContext('2d');
      //  图层轨迹初始颜色
      this.slide_color = "#12b0ff";
      //  定义画笔的颜色(白色)
      this.color = "white";
      //  获取图层的宽高
      this.width = this.canvas.width;
      this.height = this.canvas.height;
    }

    drawProint(x, y, r, s, e, color) {
      this.ctx.beginPath();
      this.ctx.lineWidth = 10;
      this.ctx.strokeStyle = color;
      this.ctx.arc(x, y, r, s, e, false);
      this.ctx.lineCap = "round";
      this.ctx.stroke();
    }

    init(print, color, colorBack) {
      this.clear();
      this.drawProint(this.canvas.width / 2, this.canvas.height / 2 + 15, this.canvas.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, colorBack);
      this.drawProint(this.canvas.width / 2, this.canvas.height / 2 + 15, this.canvas.height * 0.45, 136 * Math.PI / 180, (136 + 16.9 * print) * Math.PI / 180, color);
    }

    clear(btn) {
      this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    }

  }

  export default {
    data() {
      return {
        isModeHorR: false,
        clickModeItem: 1,
        name: this.$route.query.titleName,
        devAddress: this.$route.query.devAddress,
        devCode:this.$route.query.deviceTypeCode,
        isTitleBarBackImageVisible: true,
        isSaveVisible: true,
        operateType: {tem: 0, status: 1, mode: 2, wind: 3, swingLR: 4, swingUDB: 5, sleep: 6},//操作的类型
        airMode: {cryogen: 0, heating: 1, dehumidify: 2, offer: 3, auto: 4},//空调模式
        // isFirst: true,//
        //  定义数据各个初始信号量（先判断开关，再判断处于哪个状态）
        InitialValue: {
          //  定义总开关的信号量
          Switch: true,
          // 判断空调当前状态[1:制冷、2:制热、3:抽湿、4:送风、5:自动]
          airStatus: 3,
          //室内温度
          indoorTem: 24,
          //  定义空调当前的温度（度量）
          printTemp: 5,
          //  定义初始风速
          windSpeed: 1,
          // 定义睡眠开关
          sleepTurn: true,
          // 定义左右扫风开关,
          windLTR: true,
          // 定义上下扫风开关
          windUTD: true,
          // 定义定时状态
          timing: 0,

        },

        // 定义初始化样式
        // 控制图片的输入
        isImg: true,
        srcImg: "static/image/powoff.png",
        backSrc: "static/image/back_white_icon.png",
        // 控制文字输入
        DesWord: "保存",
        //  准备把图层定义到vue
        Canvas_Obj: "",
        Fiveitems: [
          {name: "制冷", imgPic1: "static/image/toCool.png", imgPic2: "static/image/toCoolOff.png"},
          {name: "制热", imgPic1: "static/image/toWarm.png", imgPic2: "static/image/toWarmOff.png"},
          {name: "除湿", imgPic1: "static/image/arefaction.png", imgPic2: "static/image/arefactionOff.png"},
          {name: "送风", imgPic1: "static/image/blowing-in.png", imgPic2: "static/image/blowing-inOff.png"},
          {name: "自动", imgPic1: "static/image/auto.png", imgPic2: "static/image/autoOff.png"}
        ],
        SixItems: [
          {name: "睡眠", imgPic1: "static/image/sleep_press.png", imgPic2: "static/image/sleep_normal.png"},
          {name: "左右扫风", imgPic1: "static/image/LRturn_press.png", imgPic2: "static/image/LRturn_normal.png"},
          {name: "上下扫风", imgPic1: "static/image/upDown_press.png", imgPic2: "static/image/upDown_normal.png"},
          {
            name: "风速",
            imgPic1: "static/image/ultra-low-speed2x.png",
            imgPic2: "static/image/low-speed2x.png",
            imgPic3: "static/image/mid-speed2x.png",
            imgPic4: "static/image/high-speed2x.png",
            imgPic5: "static/image/ultra-high-speed2x.png",
            imgPic6: "static/image/auto-speed2x.png"
          },
          {name: "定时", imgPic1: "static/image/timing_press.png", imgPic2: "static/image/timing_normal.png"},
          {name: "更多", imgPic1: "static/image/moreThing.png", imgPic2: "static/image/moreThing.png"}
        ],
        //  定义主题
        slide_backgrand: "background:linear-gradient(to left,#12b0ff,#5bc8ff)",
        SButton_backgrand: "background-color:#12b0ff",
        font_color: "color:white",
        ErrCol: "background-color: #ff3b30",
        //  定义暂时变量，保存状态
        temp_style_color: "",
        //  弹窗开始定义
        isSelectFrequency: false,
        frequencyNumber: ["0"],
        addressSlots: [{
          width: '90%',
          values: ["0", "0.5", "1", "1.5", "2"]
        }],
      }
    },
    created() {


    },

    mounted() {
      window.onReceivePushData = this.onReceivePushData;
      document.getElementById("imgTurn").style.width = "60px";
      document.getElementById("imgTurn").style.height = "60px";
      // 1  首先是图层的绘制
      var draw = new Draw('canvas');
      //  把图层绑定到Vue
      this.Canvas_Obj = draw;
      if (this.InitialValue.Switch) {
        this.Canvas_Obj.color = "white";
      } else {
        this.Canvas_Obj.color = "";
      }
      this.Canvas_Obj.init(this.InitialValue.printTemp, this.Canvas_Obj.color, this.Canvas_Obj.slide_color);


      var status = this.$route.query.status;
      if (this.isDebug) {
        status = {"indoorTem": 0, "mode": 0, "sleep": 0, "status": 0, "swingLR": 0, "swingUD": 0, "tem": 32, "wind": 0};
      }
      this.initStatus(status);

      // 2  判断状态判断属性
      let getId = "";
      switch (this.InitialValue.airStatus) {
        case 1:
          // 绑定一个id到指定
          document.getElementById("1LitButton").setAttribute("id", "addId");
          break;
        case 2:
          // getId = document.getElementById("2LitButton");// 绑定一个id到指定
          document.getElementById("2LitButton").setAttribute("id", "addId");
          break;
        case 3:
          // 绑定一个id到指定
          document.getElementById("3LitButton").setAttribute("id", "addId");
          break;
        case 4:
          document.getElementById("4LitButton").setAttribute("id", "addId");
          ;
          break;
        case 5:
          // 绑定一个id到指定
          document.getElementById("4LitButton").setAttribute("id", "addId");
          break;
        default:
          // 绑定一个id到指定
          document.getElementById("5LitButton").setAttribute("id", "addId");
          break;
      }
    },
    computed: {
      //  定义总开关的信号量
      SwitchStatus: {
        get: function () {
          console.log("get Switch:" + this.InitialValue.Switch);
          return this.InitialValue.Switch;
        },
        set: function (newValue) {
          console.log("set Switch:" + newValue);
          this.InitialValue.Switch = newValue;
          if (!newValue) {
            console.log("关机");
            this.srcImg = "static/image/powon.png";
            this.slide_backgrand = "background:linear-gradient(to left,#3b434e,#535e6e)";
            this.SButton_backgrand = "background-color:#3b434e";
            this.font_color = "color:#6d757f";
            this.ErrCol = "background-color: rgba(255, 59, 48, 0.59)";
            this.resDrawPic("#3b434e", "#6d757f");
            document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to right,#535e6e,#3b434e)";
          } else {
            console.log("开机");
            this.srcImg = "static/image/powoff.png";
            this.slide_backgrand = "background:" + this.temp_style_color.sl_bk;
            this.SButton_backgrand = "background-color:" + this.temp_style_color.sb_bk;
            this.resDrawPic(this.temp_style_color.sb_bk, "white");
            this.font_color = "color:white";
            this.ErrCol = "background-color: #ff3b30";
            document.getElementsByClassName("TitleBar")[0].style.background = this.temp_style_color.sl_bk;
          }
        }
      },
      TempMode: {
        get: function () {
          console.log("get airStatus:" + this.InitialValue.airStatu);
          return this.InitialValue.airStatus;
        },
        set: function (newValue) {
          this.InitialValue.airStatus = newValue;
          let sl_bk = '';
          this.clickModeItem = newValue;
          switch (newValue) {
            case 1:
              console.log("制冷");
              this.isModeHorR = true;
              sl_bk = "linear-gradient(to left,#12b0ff,#5bc8ff)";
              this.slide_backgrand = "background:" + sl_bk;
              this.SButton_backgrand = "background-color:#12b0ff";
              this.Canvas_Obj.slide_color = "#12b0ff";
              this.temp_style_color = {sl_bk: sl_bk, sb_bk: "#12b0ff"};
              document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to left,#12b0ff,#5bc8ff)";
              break;
            case 2:
              console.log("制热");
              this.isModeHorR = true;
              sl_bk = "linear-gradient(to left,#eb5738,#ff733c)";
              this.slide_backgrand = "background:" + sl_bk;
              this.SButton_backgrand = "background-color:#eb5738";
              this.Canvas_Obj.slide_color = "#eb5738";
              this.temp_style_color = {sl_bk: sl_bk, sb_bk: "#eb5738"};
              document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to left,#eb5738,#ff733c)";
              break;
            case 3:
              console.log("除湿");
              this.isModeHorR = false;
              sl_bk = "linear-gradient(to left,#5677c2,#7f9ee8)";
              this.slide_backgrand = "background:" + sl_bk;
              this.SButton_backgrand = "background-color:#5677c2";
              this.Canvas_Obj.slide_color = "#5677c2";
              this.temp_style_color = {sl_bk: sl_bk, sb_bk: "#5677c2"};
              document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to left,#5677c2,#7f9ee8)";
              break;
            case 4:
              console.log("送风");
              this.isModeHorR = false;
              sl_bk = "linear-gradient(to left,#0cb289,#0dc49f)";
              this.slide_backgrand = "background:" + sl_bk;
              this.SButton_backgrand = "background-color:#0cb289";
              this.Canvas_Obj.slide_color = "#0cb289";
              this.temp_style_color = {sl_bk: sl_bk, sb_bk: "#0cb289"};
              document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to left,#0cb289,#0dc49f)";
              this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
              break;
            case 5:
              console.log("自动");
              this.isModeHorR = false;
              sl_bk = "linear-gradient(to left,#3b434e,#535e6e)";
              this.slide_backgrand = "background:" + sl_bk;
              this.SButton_backgrand = "background-color:#3b434e";
              this.Canvas_Obj.slide_color = "#3b434e";
              this.temp_style_color = {sl_bk: sl_bk, sb_bk: "#3b434e"};
              document.getElementsByClassName("TitleBar")[0].style.background = "linear-gradient(to left,#3b434e,#535e6e)";
              this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
              break;
          }
        }
      },
    },
    methods: {

      clickPowerBtn() {
        this.operateAC((data) => {
          console.log("clickPowerBtn")
          if (!this.isDebug) {
            this.showNativeToast("操作成功");
          }
          this.SwitchStatus = !this.SwitchStatus;
        }, (data) => {
          this.showNativeToast("操作失败:" + data);
        }, this.operateType.status, this.InitialValue.Switch ? 0 : 1);
      },

      turnDown(Switch) {
        if (Switch && this.isModeHorR) {
          console.log("这里是减小:" + this.InitialValue.printTemp);
          var sendValue = this.InitialValue.printTemp + 15;
          if (sendValue < 16) {
            sendValue = 16;
          }
          this.operateAC((data) => {
            if (!this.isDebug) {
              this.showNativeToast("操作成功");
            }
            // if (this.InitialValue.printTemp > 0) {
            this.InitialValue.printTemp = sendValue - 16;
            this.resDrawPic(this.Canvas_Obj.slide_color, "white");
            // }
          }, (data) => {
            this.showNativeToast("操作失败:" + data);
          }, this.operateType.tem, sendValue);
        }
      },

      turnUp(Switch) {
        if (Switch && this.isModeHorR) {
          console.log("这里是增大");
          var sendValue = this.InitialValue.printTemp + 17;
          if (sendValue > 32) {
            sendValue = 32;
          }
          this.operateAC((data) => {
            if (!this.isDebug) {
              this.showNativeToast("操作成功");
            }
            // if (this.InitialValue.printTemp < 16) {
            this.InitialValue.printTemp = sendValue - 16;
            this.resDrawPic(this.Canvas_Obj.slide_color, "white");
            // }
          }, (data) => {
            this.showNativeToast("操作失败:" + data);
          }, this.operateType.tem, sendValue);
        }
      },

      // 空调更改出风状态
      ChangeStatus(key, Switch, event) {
        if (Switch) {
          // 先判断有没有id
          // if (document.getElementById("addId")) {
          //   // 对已经存在的移除
          //   document.getElementById("addId").removeAttribute("id");
          // }
          // // 获取点击选中的div元素
          // var getDiv = event.currentTarget;
          // // 绑定一个id到指定
          // getDiv.setAttribute("id", "addId");
          // this.Canvas_Obj.clear();

          console.log("===switch:" + Switch + " key:" + key);
          switch (key) {
            case 1:
              console.log("制冷");
              this.operateAC((data) => {
                if (!this.isDebug) {
                  this.showNativeToast("操作成功");
                }
                this.TempMode = key;
                // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
                this.resDrawPic(this.Canvas_Obj.slide_color, "white");
              }, (data) => {
                this.showNativeToast("操作失败:" + data);
              }, this.operateType.mode, this.airMode.cryogen);

              break;
            case 2:
              console.log("制热");
              this.operateAC((data) => {
                if (!this.isDebug) {
                  this.showNativeToast("操作成功");
                }
                this.TempMode = key;
                // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
                this.resDrawPic(this.Canvas_Obj.slide_color, "white");
              }, (data) => {
                this.showNativeToast("操作失败:" + data);
              }, this.operateType.mode, this.airMode.heating);
              break;
            case 3:
              console.log("除湿");
              this.operateAC((data) => {
                if (!this.isDebug) {
                  this.showNativeToast("操作成功");
                }
                this.TempMode = key;
                // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
                this.resDrawPic(this.Canvas_Obj.slide_color, "white");
              }, (data) => {
                this.showNativeToast("操作失败:" + data);
              }, this.operateType.mode, this.airMode.dehumidify);
              break;
            case 4:
              console.log("送风");
              this.operateAC((data) => {
                if (!this.isDebug) {
                  this.showNativeToast("操作成功");
                }
                this.TempMode = key;
                // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);
                this.resDrawPic(this.Canvas_Obj.slide_color, "white");
              }, (data) => {
                this.showNativeToast("操作失败:" + data);
              }, this.operateType.mode, this.airMode.offer);
              break;
            case 5:
              console.log("自动");
              this.operateAC((data) => {
                if (!this.isDebug) {
                  this.showNativeToast("操作成功");
                }
                this.TempMode = key;
                this.resDrawPic(this.Canvas_Obj.slide_color, "white");
                // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, this.Canvas_Obj.slide_color);

              }, (data) => {
                this.showNativeToast("操作失败:" + data);
              }, this.operateType.mode, this.airMode.auto);
              break;
            default:
              console.log("无效")
              break;
          }
          // this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, (136 + 16.9 * this.InitialValue.printTemp) * Math.PI / 180, "white")
        }
      },

      //  更改空调状态
      ChangeSixofOne(key, Switch, event,code) {
        if (code == 0x0005) {
          console.log("当前空调是大金");
          // 大金空调
          if (Switch && this.isModeHorR) {
            switch (key) {
              case 1:
                break;
              case 2:
                break;
              case 3:
                break;
              case 4:
                console.log("风速");
                let getDiv = event.currentTarget;
                var tempWindSpeed = this.InitialValue.windSpeed;
                console.log(tempWindSpeed+"    show");
                if (tempWindSpeed > 5) {
                  tempWindSpeed = 0;
                }
                console.log("tempWindSpeed=" + tempWindSpeed);
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windSpeed++;
                  if (this.InitialValue.windSpeed >= 5) {
                    this.InitialValue.windSpeed = 0;
                  }
                  console.log("windSpeed=" + this.InitialValue.windSpeed);
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.wind, tempWindSpeed);
                break;
              case 5:
                console.log("定时");
                //  调用组件
                this.isSelectFrequency = true;
                break;
              case 6:
                console.log("更多");
                break;
              default:
                console.log("未定义");
                break;
            }
          }
        } else if (code == 0x0006) {
          // 格力空调1拖2
          console.log("当前空调是格力1拖2");
          if (Switch && this.isModeHorR) {
            switch (key) {
              case 1:
                console.log("睡眠");
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.sleepTurn = !this.InitialValue.sleepTurn;
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.sleep, this.InitialValue.sleepTurn ? 0 : 1);
                break;
              case 2:
                console.log("左右");
                break;
              case 3:
                console.log("上下");
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windUTD = !this.InitialValue.windUTD;
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.swingUDB, this.InitialValue.windUTD ? 0 : 1);
                break;
              case 4:
                console.log("风速");
                let getDiv = event.currentTarget;
                var tempWindSpeed = this.InitialValue.windSpeed;
                if (tempWindSpeed > 3) {
                  tempWindSpeed = 0;
                }
                console.log("tempWindSpeed=" + tempWindSpeed);
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windSpeed++;
                  if (this.InitialValue.windSpeed >= 3) {
                    this.InitialValue.windSpeed = 0;
                  }
                  console.log("windSpeed=" + this.InitialValue.windSpeed);
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.wind, tempWindSpeed);
                break;
              case 5:
                console.log("定时");
                //  调用组件
                this.isSelectFrequency = true;
                break;
              case 6:
                console.log("更多");
                break;
              default:
                console.log("未定义");
                break;
            }
          }
        } else if (code == 0x0011) {
          // 格力空调1拖1
          console.log("当前空调是格力1拖1");
          if (Switch && this.isModeHorR) {
            switch (key) {
              case 1:
                console.log("睡眠");
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.sleepTurn = !this.InitialValue.sleepTurn;
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.sleep, this.InitialValue.sleepTurn ? 0 : 1);
                break;
              case 2:
                console.log("左右");
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windLTR = !this.InitialValue.windLTR;
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.swingLR, this.InitialValue.windLTR ? 0 : 1);
                break;
              case 3:
                console.log("上下");
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windUTD = !this.InitialValue.windUTD;
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.swingUDB, this.InitialValue.windUTD ? 0 : 1);
                break;
              case 4:
                console.log("风速");
                let getDiv = event.currentTarget;
                var tempWindSpeed = this.InitialValue.windSpeed;
                if (tempWindSpeed > 6) {
                  tempWindSpeed = 0;
                }
                console.log("tempWindSpeed=" + tempWindSpeed);
                this.operateAC((data) => {
                  if (!this.isDebug) {
                    this.showNativeToast("操作成功");
                  }
                  this.InitialValue.windSpeed++;
                  if (this.InitialValue.windSpeed >= 6) {
                    this.InitialValue.windSpeed = 0;
                  }
                  console.log("windSpeed=" + this.InitialValue.windSpeed);
                }, (data) => {
                  this.showNativeToast("操作失败:" + data);
                }, this.operateType.wind, tempWindSpeed);
                break;
              case 5:
                console.log("定时");
                //  调用组件
                this.isSelectFrequency = true;
                break;
              case 6:
                console.log("更多");
                break;
              default:
                console.log("未定义");
                break;
            }
          }
        }
      },

      //  重新绘制图层的封装
      resDrawPic(BumColor, topColor) {
        this.Canvas_Obj.clear();
        this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, 406 * Math.PI / 180, BumColor);
        this.Canvas_Obj.drawProint(this.Canvas_Obj.width / 2, this.Canvas_Obj.height / 2 + 15, this.Canvas_Obj.height * 0.45, 136 * Math.PI / 180, (136 + 16.9 * (this.InitialValue.printTemp > 16 ? 16 : this.InitialValue.printTemp)) * Math.PI / 180, topColor)
      },

      //  弹窗定义方法区域——start
      //关闭重复次数
      closeFrequency: function () {
        this.isSelectFrequency = false;
      },

      //点击重复次数
      selectFrequency() {
        this.isSelectFrequency = true
      },

      //保存重复次数
      confirmFrequency: function () {
        this.isSelectFrequency = false
      },
      //  定时回调
      addressChange(value, index) {
        this.frequencyNumber = [value];
        this.InitialValue.timing = value;
        console.log("定时为:   " + value)
      },

      //  弹窗定义方法区域——end
      operateAC(funcSuccess, funcFailed, type, value) {
        switch (type) {
          case this.operateType.mode://模式
            this.operate(funcSuccess, funcFailed, -1, -1, value, -1, -1, -1, -1);
            break;
          case this.operateType.sleep://睡眠
            this.operate(funcSuccess, funcFailed, -1, -1, -1, -1, -1, -1, value);
            break;
          case this.operateType.swingLR://左右扫风
            this.operate(funcSuccess, funcFailed, -1, -1, -1, -1, value, -1, -1);
            break;
          case this.operateType.swingUDB://左右扫风
            this.operate(funcSuccess, funcFailed, -1, -1, -1, -1, -1, value, -1);
            break;
          case this.operateType.wind://风速
            this.operate(funcSuccess, funcFailed, -1, -1, -1, value, -1, -1, -1);
            break;
          case this.operateType.tem://温度
            this.operate(funcSuccess, funcFailed, value, -1, -1, -1, -1, -1, -1);
            break;
          case this.operateType.status://温度
            this.operate(funcSuccess, funcFailed, -1, value, -1, -1, -1, -1, -1);
            break;
        }
      },
      operate(funcSuccess, funcFailed, tem, status, mode, wind, swingLR, swingUDB, sleep) {
        console.log("tem:" + tem + " status:" + status + " mode:" + mode + " wind:" + wind + " swingLR:" + swingLR + " swingUDB:" + swingUDB + " sleep:" + sleep);
        if (this.isDebug) {
          funcSuccess("dn");
          // } else if (this.isFirst) {
          //   this.isFirst = false;
          //   funcSuccess("1");
        } else {
          Cordova.exec(funcSuccess, funcFailed, "SmartHomePlugins",
            "operateAirConditioner", [this.devAddress, tem, status, mode, wind, swingLR, swingUDB, sleep]);
        }

      },

      initStatus(status) {//初始化状态
        this.TempMode = status.mode + 1;
        this.InitialValue.indoorTem = status.indoorTem;
        this.InitialValue.printTemp = status.tem >= 16 ? (status.tem - 16) : status.tem;
        console.log("this.InitialValue.printTemp:" + this.InitialValue.printTemp)
        this.InitialValue.windSpeed = status.wind;
        this.InitialValue.sleepTurn = status.sleep == 1 ? true : false;
        this.InitialValue.windLTR = status.swingLR == 1 ? true : false;
        this.InitialValue.windUTD = status.swingUD == 1 ? true : false;
        this.SwitchStatus = status.status == 1 ? true : false;
        console.log("====InitialValue:" + JSON.stringify(this.InitialValue));
        console.log("====status:" + JSON.stringify(status));
      },
      //收到状态数据
      onReceivePushData: function (data) {
        if (data instanceof Object) {
          var code = data.code;
          if (code == "200") {
            if (this.devAddress == data.data.address) {
              console.log("+++++++++++++刷新：+++++++++++++deviceAddress==data.data.address");
              const status = eval("(" + data.data.status + ")");
              this.initStatus(status);
            }
          }
        }
      },
    },
  }
</script>

<style lang="scss">
  html, body, wrapper {
    height: 100%;
    overflow: hidden;
    .wrapperPage {
      margin-top: 64px;
      font-size: large;
      height: calc(100% - 64px);
    }
  }

  /* 事件效果需要——start */
  .addId {
    background: rgba(0, 0, 0, 0.1);
    opacity: 0.5;
  }

  /* 事件效果_end */

  /* 这里是对于滑钮部分的布局 -start */
  .slideButton {
    width: auto;
    height: 50%;
    flex-wrap: nowrap;
    .slide {
      width: auto;
      height: 80%;
      /* 正中间的文字显示效果 */
      #slide_text_des {
        position: relative;
        bottom: 75%;
        color: white;
        .slide_text_word {
          font-size: 90px;
        }
        .slide_text_word.temFontSize {
          font-size: 70px;
        }
        .slide_text_fu {
          font-size: 24px;
        }
        /* 中间部分下半部，室温显示 */
        #slide_text_low {
          overflow: auto;
          width: 30%;
          height: 30px;
          margin: -5% auto 0;
          font-size: 16px;
          div {
            float: left;
          }
          /* 小温度计的图标 */
          #slide_text_img {
            width: 19%;
            height: 70%;
            img {
              width: 100%;
              height: 100%;
            }
          }
          #slide_text_img_text {
            margin-left: 4px;
            width: auto;
            height: inherit;
          }
        }
        /*定义温度标志*/
        #slide_text_two_temp {
          width: 32%;
          overflow: auto;
          margin: 1% auto;
          font-size: 15px;
          #temp1 {
            float: left;
          }

          #temp2 {
            float: right;
          }
        }
      }
    }
    #canvas {
      margin: 0 auto;
      /* 使得canvas 铺满 */
      display: block;
    }
  }

  /* 滑钮部分5个小模块的定义 */
  .SButton {
    display: flex;
    width: auto;
    height: 20%;
    .LitButton {
      flex: auto;
      flex-direction: column;
      justify-content: center;
      display: inline-flex;
      align-items: center;
      width: 20%;
      height: 100%;
      text-align: center;
      /*  滑块图片定义 */
      img {
        margin-top: 5px;
        width: 33%;
        height: 33%;
      }
      /*滑块文字说明*/
      .LitButton_word {
        margin-top: 4px;
        height: 42%;
        width: 42%;
        font-size: 80%;
      }

    }
  }

  /* 滑块部分_end */
  /* 中部两个模块---start */
  .adjustButton {
    width: auto;
    height: 16%;
    background-color: #f4f4f4;
    justify-content: center;
    display: flex;
    align-items: center;
    .adButtonDown, .adButtonUp {
      width: 48%;
      height: 90%;
      background: linear-gradient(#f3f3f3, white);
      margin: 0 1% 0 1%;
      border-radius: 8px;
    }
  }

  /* 中部两个模块---end */
  /* 底部6个小模块尽兴布局----start */
  .controlButton {
    width: auto;
    height: 37%;
    .cLButton {
      display: inline-flex;
      align-items: center;
      width: 32%;
      height: 47%;
      flex: auto;
      flex-direction: column;
      position: relative;
      /* 定位红色特殊标志 */
      .clBEr {
        position: absolute;
        width: 32%;
        height: 18%;
        top: 4px;
        right: 4px;
        // font-size: 1px;
        color: white;
        border-radius: 12px;
      }
      .cLBimg {
        width: 25%;
        height: 25%;
        position: relative;
        top: 20%;
        img {
          width: 25px;
          height: 25px;
        }
      }
      .cLBword {
        width: auto;
        height: auto;
        position: relative;
        top: 30%;
        // font-size:2px;
        color: #9e9e9e;
      }
    }
  }

  /* 底部6个小模块尽兴布局----end */

  /* 弹窗的定义_start */
  .popupDiv {
    text-align: center;
    display: none;
  }

  .demo-popup-bottom {
    min-width: 100%;
  }

  .mu-icon-button-close {
    background-image: url(/static/image/close.png);
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    background-position: 1% 50%;
    background-size: 25px 25px;
  }

  .mu-appbar {
    background-color: #ffffff;
    color: #000;
  }

  .mu-appbar-title {
    text-align: center;
  }

  .frequencyTxt {
    position: absolute;
    top: calc(50% + 20px);
    right: calc(40% - 20px);
    font-size: medium;
  }
</style>



