<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="left-delete" @click.stop="clickItem(index[0])">
    <div class="move" @touchstart="_touchStart" @touchmove="_touchMove" @touchend="_touchEnd" :style="txtStyle">
      <slot></slot>
    </div>
    <div class="deleteIcon" :style="zIndex"  @click.stop="deleteItem(index)">
      <img class="imageIcon" src="../../../static/image/deleteScene.png"/>
    </div>

    <div class="editIcon" :style="zIndex"  @click.stop="editItem(index)">
      <img class="imageIcon" src="../../../static/image/editScene.png"/>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["index"],
    data() {
      return {
        startX: 0,       //触摸位置
        moveX: 0,       //滑动时的位置
        disX: 0,       //移动距离
        txtStyle: '',
        delWidth: 700,
        top: '',
        zIndex: 'z-index:-1',
      }
    },
    methods: {
      _touchStart: function(ev) {
        ev = ev || event;
        if(ev.touches.length == 1){
          // 手指按下的时候记录按下的位置
          this.startX = ev.touches[0].clientX;
          //console.log("_touchStart "+this.startX)
        }
      },
      _touchMove: function(ev) {
        ev = ev || event;
        //console.log("_touchMove ")
        if(ev.touches.length == 1) {
          // 滑动过程中的实时位置
          this.moveX = ev.touches[0].clientX
          // 滑动过程中实时计算滑动距离
          this.disX = this.startX - this.moveX;
          // console.log('disX==>',this.disX)
          // 如果是向右滑动或者只是点击，不改变滑动位置
          if(this.disX < 0 || this.disX == 0) {
            //console.log('右移动');
            this.txtStyle = "transform:translateX(0rem)";
            this.zIndex = "z-index:" + "-1";
          }else if (this.disX > 0) {
            //console.log('左移动');
            //如果是向左滑动，则实时给这个根元素一个向左的偏移-left，当偏移量到达固定值delWidth时，固定元素的偏移量为 delWidth
            this.txtStyle = "transform:translateX(-" + this.disX/100 + "rem)";
            if (this.disX >= this.delWidth/100) {
              this.txtStyle = "transform:translateX(-" + this.delWidth/100 + "rem)";
            }
            this.zIndex = "z-index:" + "0";
          }
        }
      },
      _touchEnd: function(ev) {
        if (event.changedTouches.length == 1) {
          this.startX = 0;
          this.zIndex = "z-index:" + "0";
          //console.log(event.changedTouches[0].clientX)
          // 手指移动结束后的水平位置
          let endX = event.changedTouches[0].clientX;
          // 触摸开始与结束,手指移动的距离
          this.disX = this.startX - endX;
          //如果距离小于删除按钮的1/2，不显示删除按钮
          this.zIndex = "z-index:" + 10 + "rem";
        }
      },
      deleteItem: function(index) {
        this.$emit('deleteItem', index);
        //console.log("index[0]为该条场景内的id "+index[0]+ "index[1]为界面需要删除的id"+ index[1])
        this.txtStyle = "transform:translateX(0" + "rem)";
        this.zIndex = "z-index:" + "-1";
      },
      editItem: function(index) {
        this.$emit('editItem', index);
        //console.log("编辑该条场景内的id "+index)
        this.txtStyle = "transform:translateX(0" + "rem)";
        this.zIndex = "z-index:" + "-1";
      },
      clickItem: function(index) {
       // console.log("执行该条场景内的id "+index)
        this.$emit('clickItem', index);
      }
    }
  }
</script>

<style>
  .line{
    width: 1px;
    height: 100%;
    background-color: lightgrey;
    right:0;
    top:0;
    margin-right: 40px;
  }
  .left-delete{
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: center;
    width:100%;
    height:100%;
    position:relative;
  }
  .move{
    flex: 1;
    position: relative;
  }

  .deleteIcon{
    background-color: #ff3b30;
    width: 60px;
    height:100%;
    position: absolute;
    right:0;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .editIcon{
    background-color: #ff3b30;
    width: 60px;
    height:100%;
    position: absolute;
    right:61px;
    background-repeat: no-repeat;
    background-size: contain;
  }


  .imageIcon{
    width: 23px;
    position: absolute;
    margin: auto;
    top: 0; left: 0; bottom: 0; right: 0;
  }

</style>



