const state = {
  // initial state
  regionLists: [],
  currentRegion: {
    //刷选区域列表需传入ordinal  -1表示全部区域下设备
    "ordinal": -1,
    //设备列表显示的名字
    "name": "全部区域",
    //值唯一，用于在列表中匹配当前选中的区域
    'id': -1,
  },
  isFromRegionPage: false,
  //在列表中的第几个位置
  selectedRegionIndex: '0',
}


// getters
const getters = {}

// actions
const actions = {
  //传入在list中的id,刷新当前区域对象
  refreshCurrentRegionObjByActions({commit, state}, id) {

    //-1表示全部区域下设备
    if (id == -1) {
      commit('refreshCurrentRegionObj', {"ordinal": -1, "name": "全部区域", 'id': -1,})
      return
    }

    commit('refreshCurrentRegionObj', state.regionLists[id])
  }

}


// mutations
const mutations = {
  //由刷新当前区域对象获取在list中的id，与refreshCurrentRegionObjByActions刚好相反
  computedRegionIdInList: function (state) {

    if(state.currentRegion.id==-1){
      //重置state.selectedRegionIndex
      state.selectedRegionIndex=-1
      return
    }

    state.regionLists.findIndex(function (element, index, array) {
      if (element.id == state.currentRegion.id) {
        state.selectedRegionIndex = index
        console.log("序列 "+  state.selectedRegionIndex)
      }
    })

  },

  refreshIsFromRegionPage(state, payload) {
    state.isFromRegionPage = payload
  },
  //
  refreshLists(state, payload) {
    state.regionLists = payload;
    console.log("当前区域列表更新 ++++++++++" + JSON.stringify(state.regionLists))
  },
  //
  refreshCurrentRegionObj(state, payload) {
    state.currentRegion = payload;
    console.log("当前区域对象更新 ++++++++++" + JSON.stringify(state.currentRegion))
  },


}


export default {
  state,
  getters,
  actions,
  mutations
}



