<template>
  <div class="com-calendar">
    <div class="dp-container">
      <div class="dp-header">
        <div class="dp-item dp-left" @click="hiddenCalendar">取消</div>
        <div class="dp-item dp-right" @click="choiceDate">下一步</div>
      </div>


      <div class="dp-content">
        <div class="dp-content">
          <div class="dp-item">
            <com-date-scroll :select="status" :startTime="startTime" :dType="'year'"
                             :endTime="endTime"></com-date-scroll>
          </div>
          <div class="dp-item">
            <com-date-scroll :select="status" :dType="'month'"></com-date-scroll>

          </div>
          <div class="dp-item">
            <com-date-scroll :select="status" :dType="'day'"></com-date-scroll>
          </div>

        </div>
      </div>
    </div>
  </div>

</template>


<script>
  import dateScroll from './dateScroll.vue';

  require('../css/calendar.scss');
  export default {
    data: function () {
      return {
        // status:false,
        year: '',
        month: '',
        day: ''
      }
    },
    props: {
      startTime: {
        type: Number,
        default: 1900
      },
      endTime: {
        type: Number,
        default: 2050
      },
      //传入是否是开始，或者结束的控件
      isStart: false
    },
    components: {
      comDateScroll: dateScroll
    },
    computed: {
      status: function () {
        return this.$store.getters.getCalendarOk;
      }
    },
    methods: {
      choiceDate: function () {
        //console.log(" choiceDate "+ this.isStart )
        this.$store.dispatch('calendarOk', [true, this.isStart])
        //父组件接收回调，下一步弹出小时分钟时间选择组件
        this.$emit('editTimer');
      },
      hiddenCalendar: function () {
        this.$store.dispatch('calendarStatus', false);
      }
    }
  }


</script>



