import Vue from 'vue'
import Router from 'vue-router'
import VueSwiperLayout from 'vue-swiper-layout'

import device_page from '@/page/vue/device_page'
import mine_page from '@/page/vue/mine_page'
import about_page from '@/page/vue/about_page'
import security_page from '@/page/vue/security_page'
import scene_page from '@/page/vue/scene_page'
import indexHome_page from '@/page/vue/indexHome_page'
import indexHomeAdd_page from '@/page/vue/indexHomeAdd_page'
import airConditionerPage from '@/page/vue/singleDevicePage/airConditionerPage'
import settings from '@/page/vue/controller/settings'
import myRegionList_page from '@/page/vue/region/myRegionList_page'
import deviceMsg from '@/page/vue/msg/deviceMsg'
import udpConfig from '@/page/vue/controller/udpConfig'
import myControllerList from '@/page/vue/controller/myControllerList'
import wifiConfig from '@/page/vue/controller/wifiConfig'
import addScene from '@/page/vue/scene/addScene'
import editScene from '@/page/vue/scene/editScene'
import scene_timer from '@/page/vue/scene/scene_timer'
import addMoreDeviceForScene from '@/page/vue/scene/addMoreDeviceForScene'
import addBlueToothDevice from '@/page/vue/blueTooth/addBlueToothDevice'
import blueToothHome from '@/page/vue/blueTooth/blueToothHome'
import blueToothManagePage from '@/page/vue/blueTooth/blueToothManagePage'
import blueToothHistory from '@/page/vue/blueTooth/blueToothHistory'
import blueToothSet from '@/page/vue/blueTooth/setting/blueToothSet'
import blueToothModifyPwd from '@/page/vue/blueTooth/setting/blueModifyPwd'
import AddAuthorization from '@/page/vue/blueTooth/AuthorizingMgmt/AddAuthorization'
import AuthorizationManagePage from '@/page/vue/blueTooth/AuthorizingMgmt/AuthorizationManagePage'

import login from '@/page/vue/user/userLogin'
import register from '@/page/vue/user/userRegister'
import getPwd from '@/page/vue/user/userGetPwd'

//导入自定义组件
import titleBar from '@/components/js/titleBar'
import bottomNav from '@/components/js/bottomNav'
import lightCardView from '@/components/js/lightCardView'
import sceneCardView from '@/components/js/sceneCardView'
import commonDeviceCardView from '@/components/js/commonDeviceCardView'
import selectedField from '@/components/js/selectedField'
import sliderView from '@/components/js/sliderView'
import sliderTxt from '@/components/js/sliderTxt'
import timerSwiper from '@/components/js/timerSwiper'

//用户定义组件
import userTitle from '@/components/js/userTitle'
//zpy新引入滑块
import SliderCom from '@/components/js/SliderCom'

//导入全局样式
import bottomNavCss from '@/style/common/bottomNav.css'
import header from '@/style/common/pageHeader.css'
import wapper from '@/style/common/pageWapper.css'
import titleBarHeightBlock from '@/style/common/titleBarHeightBlock.css'
import blueHeightBlock from '@/style/common/blueHeightBlock.css'
import blueButton from '@/style/common/blueButton.css'

//导入全局方法
import showNativeToast from '@/page/js/toast.js'
import computeImageInList from '@/page/js/changeImageByType.js'
import parseTimePeriod from '@/page/js/parseTimeStamp.js'

import fadingCircle from '@/components/vue/fadingCircle'
Vue.component('fadingCircle',fadingCircle);


Vue.use(Router)

//注册全局组件
Vue.use(titleBar)
Vue.use(bottomNav)
Vue.use(lightCardView)
Vue.use(commonDeviceCardView)
Vue.use(sceneCardView)
Vue.use(selectedField)
Vue.use(sliderView)
Vue.use(sliderTxt)
Vue.use(timerSwiper)
Vue.use(showNativeToast);
Vue.use(computeImageInList);
Vue.use(parseTimePeriod);
Vue.use(VueSwiperLayout)
// 用户定义组件
Vue.use(userTitle);
Vue.use(SliderCom);

Vue.prototype.isDebug = false;

export default new Router({
  routes: [
    {
      path:'/indexHome_page',
      name:'indexHome_page',
      component:indexHome_page,
    },
    {
      path:'/indexHomeAdd_page',
      name:'indexHomeAdd_page',
      component:indexHomeAdd_page
    },
    {
      path: '/scene_page',
      name: 'scene_page',
      component: scene_page
    },
    {
      path: '/device_page',
      name: 'device_page',
      component: device_page
    },
    {
      path: '/about_page',
      name: 'about_page',
      component: about_page
    },
    {
      path: '/mine_page',
      name: 'mine_page',
      component: mine_page
    },
    {
      path: '/security_page',
      name: 'security_page',
      component: security_page
    },

    /*具体设备控制页面*/
    {
      path: '/airConditionerPage',
      name: 'airConditionerPage',
      component: airConditionerPage
    },
    /*主机相关页面*/
    {
      path: '/myControllerList',
      name: 'myControllerList',
      component: myControllerList
    },
    {
      path: '/wifiConfig',
      name: 'wifiConfig',
      component: wifiConfig
    },
    {
      path: '/udpConfig',
      name: 'udpConfig',
      component: udpConfig
    },
    {
      path: '/settings',
      name: 'settings',
      component: settings
    },

    /*区域相关页面*/
    {
      path: '/myRegionList_page',
      name: 'myRegionList_page',
      component: myRegionList_page
    },
    /*场景相关页面*/
    {
      path: '/addScene',
      name: 'addScene',
      component: addScene
    },
    // {
    //   path: '/editScene',
    //   name: 'editScene',
    //   component: editScene
    // },
    {
      path: '/scene_timer',
      name: 'scene_timer',
      component: scene_timer
    },
    {
      path: '/addMoreDeviceForScene',
      name: 'addMoreDeviceForScene',
      component: addMoreDeviceForScene
    },
    /*蓝牙相关页面*/
    {
      path: '/addBlueToothDevice',
      name: 'addBlueToothDevice',
      component: addBlueToothDevice
    },
    {
      path: '/blueToothHome',
      name: 'blueToothHome',
      component: blueToothHome
    },
    {
      path: '/blueToothManagePage',
      name: 'blueToothManagePage',
      component: blueToothManagePage
    },
    {
      path: '/blueToothHistory',
      name: 'blueToothHistory',
      component: blueToothHistory
    },
    {
      path: '/blueToothSet',
      name: 'blueToothSet',
      component: blueToothSet
    },
    {
      path: '/blueToothModifyPwd',
      name: 'blueToothModifyPwd',
      component: blueToothModifyPwd
    },
    /*蓝牙授权相关页面*/
    {
      path: '/AddAuthorization',
      name: 'AddAuthorization',
      component: AddAuthorization
    },
    {
      path: '/AuthorizationManagePage',
      name: 'AuthorizationManagePage',
      component: AuthorizationManagePage
    },

    /*消息相关页面*/
    {
      path: '/deviceMsg',
      name: 'deviceMsg',
      component: deviceMsg
    },


    /*登录注册页面*/
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path:'/register',
      name:'register',
      component:register
    },
    {
      path:'/getPwd',
      name:'getPwd',
      component:getPwd
    },
  ]
})



