<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar title-name="关于" :is-visible="true"  :back-src="bac_imgsrc"></titleBar>

    <div class="aboutInPage">

      <img class='img_logo' src="../../../static/image/logo.png"/>

      <p class="text_version">version:{{version}}</p>


    </div>
  </div>
</template>


<script>

  export default {
    data() {
      return {
        version:"1.0",
        bac_imgsrc:"static/image/close.png"
      }
    },
    created(){
      Cordova.exec((data)=>{
        this.version = data;
      }, ()=>{

      }, "SmartHomePlugins", "getVersion", []);
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },

    methods: {
    },
  }
</script>


<style>
  .aboutInPage {
    background-color: #f9f9f9;
    font-size: large;
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
    padding-bottom: 50%;
  }

  .img_logo {
    width: 60px;
    height: 60px;
    margin-bottom: 20px;
  }

  .text_version{
    text-align: center;
    font-size: medium;
    font-weight: bold;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }
</style>



