<template xmlns:v-bind="http://www.w3.org/1999/xhtml" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div id="text_desc">
    <div v-for="(option,index) in ArrayNumber" :key="index" class="text_desc">
      <div class="text_temp">
        <span class="text_temp_1">{{ option }}</span>
        <span class="text_temp_2">|</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name:'SliderDescCompent',
        data(){
          return{}
        },
        props:['ArrayNumber'],

        mounted(){
          console.log("传递来"+this.ArrayNumber);
        },
    }
</script>

<style scoped>
  #text_desc{
    align-items: center;
  }

  .text_desc{
    display: inline-flex;
    width: 20%;
    height: 100%;
  }

  .text_temp{
    margin: 0 auto;
    margin-top: 8px;
  }

  .text_temp span{
    display: block;
    line-height: normal;
    text-align: center;
  }
</style>



