<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar title-name="设置" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              @clickRightButton="save" :src="srcImg" :DesWord="DesWord" :back-src="backSrc"></titleBar>

    <div class="settingsInPage">
      <mu-divider/>

      <div class="settingsDiv pdSetTopAndpdBottom">
        <!--门锁名称-->
        <div class="settingsRowDiv">
          <p class="setDataItemName">门锁名称</p>
          <p class="setDataItemName value">{{name}}</p>
        </div>

      </div>
      <mu-divider/>

      <div class="settingsDivNoMargion pdSetTopAndpdBottom">
        <!--管理员密码-->
        <div class="settingsRowDiv">
          <p class="setDataItemName" @click="toModifyPwd">点击修改密码</p>
          <!--<mu-text-field :type="pwdType" v-model="adminPsw" hintTextClass='settingPageHintClasss' inputClass="settingPageInputClass" hintText="请输入管理员密码" underlineClass="line"/>-->
          <img class='numberVisible' v-on:click="changePwdType" src="../../../../../static/image/icon_rigt_grey.png"/>
        </div>
      </div>
      <mu-divider/>
      <div class="settingsDivNoMargion pdSetTopAndpdBottom">
        <!--管理员密码-->
        <div class="settingsRowDiv">
          <p class="setDataItemName" @click="toModifyPwd">是否自动开锁</p>
          <!--<mu-text-field :type="pwdType" v-model="adminPsw" hintTextClass='settingPageHintClasss' inputClass="settingPageInputClass" hintText="请输入管理员密码" underlineClass="line"/>-->
          <mu-switch slot="right" :value="openInBackground"
                     v-on:change="changeOpenInBackground()" style="margin-right: 20px;"></mu-switch>
        </div>
      </div>
      <mu-divider/>

      <div class="settingsDiv pdSetTopAndpdBottom">
        <!--电量-->
        <div class="powValueDiv">
          <p class="powNameTxt">电量</p>
          <p class="setDataItemName value">{{electricity}}</p>
        </div>
      </div>
      <mu-divider/>


    </div>


  </div>
</template>


<script>

  export default {
    data() {
      return {
        //标题栏有关属性
        devAddress: this.$route.query.devAddress,
        name: this.$route.query.name,
        electricity: this.$route.query.electricity,
        isSaveVisible: false,
        isTitleBarBackImageVisible: true,
        isImg: false,
        DesWord: "",
        backSrc: "static/image/close.png",
        srcImg: "static/image/add_icon1.png",
        //标题栏有关属性  the end

        settings: '',
        pwdType: "password",
        // pwdTypeImage: require("../../../../static/image/pswNumber.png"),

        //管理员密码
        adminPsw: "",
        openInBackground: false,
      }
    },
    created() {
      if (this.electricity == "") {
        this.electricity = "未知"
      }
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
      Cordova.exec((data) => {
        this.openInBackground = (data=="1"?true:false);
        },
        (data) => {
        }, "LockPlugins",
        "checkIsOpenLockInbg",
        []);
    },


    computed: {},

    methods: {
      changeOpenInBackground() {
        if (this.openInBackground) {
          this.openInBackground = false;
        } else {
          this.openInBackground = true;
        }
        if (this.isDebug) {
          console.log("openInBackground:"+this.openInBackground);
        } else {
          Cordova.exec((data) => {
            },
            (data) => {
            }, "LockPlugins",
            "changeOpenInbgState",
            [this.openInBackground?"1":"0"]);
        }

      },
      changePwdType() {
        if (this.pwdType == "password") {
          this.pwdType = "text"
          this.pwdTypeImage = "../../../../static/image/pswNumberBlue.png"
        } else {
          this.pwdType = "password"
          this.pwdTypeImage = "../../../../static/image/pswNumber.png"
        }
      },
      clearPsw() {
        this.origionPwd = ""

      },
      toModifyPwd() {
        this.$router.push({
          path: '/blueToothModifyPwd',
          name: 'blueToothModifyPwd',
          query: {devAddress: this.devAddress},
        })
      },
      //保存
      save() {
        //
        if (this.isDebug) {
          this.$router.go(-1)
        }
      }
    },
  }
</script>


<style>
  .powNameTxt {
    flex: 1;
    text-align: left;
    font-size: small;
    margin-left: 20px;
    font-weight: bold;
  }

  .powValueDiv {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 5px;
  }

  .setDataItemName {
    flex: 1;
    text-align: left;
    font-size: small;
    margin-left: 20px;
    font-weight: bold;
  }

  .setDataItemName.value {
    text-align: right;
    opacity: 0.8;
    font-weight: normal;
    margin-right: 20px;
    font-size: 15px;
  }

  /***********临时密码输入框样式 ************/

  .TempPswRowInfo .tempPswsettingPageHintClasss {
    text-align: right;
    padding-right: 20px;
    font-size: medium;
  }

  .TempPswRowInfo .mu-text-field {
    margin-bottom: 0px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .TempPswRowInfo .mu-text-field-line {
    display: none;
  }

  .TempPswRowInfo .mu-text-field-focus-line {
    margin-left: 20px;
    background-color: #5bc8ff;
    display: none;
  }

  .TempPswRowInfo .mu-text-field-content {
    padding: 0px;
  }

  /***********临时密码输入框样式 the end ************/

  /***********密码输入框样式 ************/
  .settingPageHintClasss {
    text-align: right;
    margin-right: 20px;
    font-size: medium;
  }

  .line {
    margin-left: 20px;
  }

  .settingsRowDiv .mu-text-field-focus-line {
    margin-left: 20px;
    background-color: #5bc8ff;
    display: none;
  }

  .settingsRowDiv .mu-text-field-line {
    display: none;
  }

  /*.settingsRowDiv {*/
  /*padding: 5px;*/
  /*}*/

  .tempPswInputClass {
    text-align: right;
    padding-right: 20px;
  }

  .settingPageInputClass {
    text-align: right;
    margin-right: 20px;
  }

  .settingsRowDiv .mu-text-field-content {
    padding: 0px;
  }

  .settingsRowDiv .mu-text-field {
    margin-bottom: 0px;
  }

  /***********密码输入框样式 the end ************/

  .numberVisible {
    width: 20px;
    height: 20px;
    margin-right: 20px;
    margin-left: 10px;
  }

  .deleteText {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    margin-left: 20px;
  }

  .muTextFieldDiv {
    flex: 1;
  }

  .settingsDiv {
    background-color: white;
    width: 100%;
    margin-top: 10px;
    position: relative;

  }

  .settingsDivNoMargion {
    background-color: white;
    width: 100%;
    position: relative;
  }

  .settingsDiv .mu-text-field {
    flex: 1;
    min-height: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .settingsDivNoMargion .mu-text-field {
    flex: 1;
    min-height: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .settingsRowDiv {
    width: 100%;
    display: flex;
    flex-direction: row;
  //justify-content: flex-start; align-items: center; padding: 5px; padding-top: 10px;
    /*padding-bottom: 10px;*/
  }

  .settingsInPage {
    background-color: #f9f9f9;
    /*background-color: red;*/
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
  }

  .pdSetTopAndpdBottom {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }
</style>



