<template xmlns:v-bind="http://www.w3.org/1999/xhtml" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <mu-paper :zDepth="1" class="deviceMargin" :rounded="true">


    <!--设备未知状态-->
    <div v-if="obj.status==999" class="swith_on" v-on:click="unknowStatus">
      <div class="stateImageDiv">
        <img class="rightError" src="../../../static/image/errorImage.png"/>
        <!--排气扇-->
        <img v-if="deviceData.deviceTypeCode == 0x7F08" class="devicestatus" src="../../../static/image/exhaust Fan_offline.png"/>
        <!--灯-->
        <img v-else class="devicestatus" src="../../../static/image/light_error.png"/>
      </div>
      <p class="device_Name">{{deviceData.name}}</p>
      <!--<p class="device_status_error">未知状态</p>-->
      <!--<div class="bootRegion">-->
        <!--<img class="bootImage" src="../../../static/image/power_offline.png"/>-->
    </div>

    <!--&lt;!&ndash;设备离线&ndash;&gt;-->
    <div v-else-if="obj.status == -1" class="swith_on" v-on:click="offLine">
      <div class="stateImageDiv">
        <img class="rightError" src="../../../static/image/errorImage.png"/>
        <!--排气扇-->
        <img v-if="deviceData.deviceTypeCode == 0x7F08" class="devicestatus" src="../../../static/image/exhaust Fan_offline.png"/>
        <!--灯-->
        <img v-else class="devicestatus" src="../../../static/image/light_error.png"/>
      </div>
      <p class="device_Name">{{deviceData.name}}</p>
      <!--<p class="device_status_error">离线</p>-->
      <!--<div class="bootRegion">-->
        <!--<img class="bootImage" src="../../../static/image/power_offline.png"/>-->
      <!--</div>-->
    </div>


    <!--设备显示-->
    <div v-else v-on:click="clickToChangeStatus" class="swith_on">

      <!--排气扇-->
      <div class="stateImageDiv" v-if="deviceData.deviceTypeCode == 0x7F08">
        <img v-if="obj.status==1" class="devicestatus" src="../../../static/image/exhaust_Fan_open_icon.png"/>
        <img v-if="obj.status==0" class="devicestatus" src="../../../static/image/exhaust_Fan_close_icon.png"/>
      </div>
      <!--灯-->
      <div v-else class="stateImageDiv">
        <img v-if="obj.status==1" class="devicestatus" src="../../../static/image/light_on_image.png"/>
        <img v-if="obj.status==0" class="devicestatus" src="../../../static/image/light_off_image.png"/>
      </div>

      <p class="device_Name">{{deviceData.name}}</p>
      <!--<p v-if="obj.status==1" class="device_status">开启</p>-->
      <!--<p v-else class="device_status">关闭</p>-->

      <!--<div class="bootRegion">-->
        <!--<img v-if="obj.status==1" class="bootImage" src="../../../static/image/power.png"/>-->
        <!--<img v-else-if="obj.status==0" class="bootImage" src="../../../static/image/power_off.png"/>-->
      <!--</div>-->
    </div>


  </mu-paper>
</template>

<script>

  export default {
    data() {
      return {
        sendOper: "",
      }
    },
    //jie
    computed: {
      // 计算属性的 getter
      obj: function () {
        if (this.deviceData.status == "" || this.deviceData.status == " ") {
          this.deviceData.status = "{\n\t\"status\":999\n}";
          return eval("(" + this.deviceData.status + ")")
        }
        return eval("(" + this.deviceData.status + ")")
      }
    },

    //需传入的设备名称，开关机状态
    props: ['deviceData'],
    methods: {
      //开关机点击事件
      clickToChangeStatus: function () {
        this.$emit('ChangeLightStatus', this.deviceData);
      },
      offLine: function () {
        this.showNativeToast("设备离线");
      },
      unknowStatus: function () {
        this.showNativeToast("设备未知状态")
      },

    },
  }
</script>

<style>
  .stateImageDiv {
    /*flex: 1;*/
    position: relative
  }

  .rightError {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-right: -5px;
  }

  .devicestatus {
    margin-top: 20px;
    width: 40px;
    height: 40px;
    background-size: auto 40px;
  }

  .bootImage {
    width: 24px;
    height: 24px;
    margin-top: 50%;
  }

  .bootRegion {
    flex: 1;
    justify-content: center;
    align-items: center;
  }

  .device_Name {
    margin: 0%;
    /*padding-top: 2%;*/
    font-size: large;
    margin-bottom: 10px;
    font-family: PingFangBold;
  }

  .device_status {
    font-size: small;
    margin: 0%;
    padding-bottom: 2%;
    opacity: 0.5;
  }

  .device_status_error {
    font-size: small;
    margin: 0%;
    padding-bottom: 2%;
    opacity: 0.5;
    color: red;
  }

  .swith_on {
    flex-direction: column;
    display: flex;
    position: relative;
    height: 170px;
    background-color: white;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }

  .mu-paper-round {
    border-radius: 5px;
  }

  @font-face {
    font-family: PingFangBold;
    src: url('../../../static/font/PingFangBold.ttf');
  }

  .deviceMargin {
    margin: 5px;
  }
</style>



