      methods:{

        showAdd:function () {
          console.log("显示页面");
          this.$router.push({
            path: '/indexHomeAdd_page',
            name: 'indexHomeAdd_page',
          })
        },

        // 获取设备详细信息
        getDevListSuccess:function (data) {
          var obj = eval("(" + data + ")");
          console.log("获取设备描述信息成功" + JSON.stringify(obj));
          if (obj) {
            if (obj.length == 1){
              // 执行判断
              if (obj[0].type == 32513) {
                this.deviceMes.lock = "当前没有锁挂载";
                if (obj[0].count) {
                  this.deviceMes.light = "当前有"+obj[0].count+"盏"+obj[0].name+"亮着";
                } else {
                  this.deviceMes.light = "当前没有灯亮着";
                }
              } else {
                this.deviceMes.light = "当前没有灯挂载";
                if (obj[0].count) {
                  this.deviceMes.lock = "当前有"+obj[0].count+"把锁打开";
                } else {
                  this.deviceMes.lock = "已上锁";
                }
              }
            } else {
              if (obj[0].type == 32513) {
                // 第一个挂载设备是灯
                if (obj[0].count && obj[1].count) {
                  this.deviceMes.lock = "当前有 "+obj[1].count+" 把锁打开";
                  this.deviceMes.light = "当前有 "+obj[0].count+"盏"+obj[0].name+" 亮着";
                } else if (!obj[0].count && obj[1].count) {
                  this.deviceMes.lock = "当前有"+obj[1].count+"把锁打开";
                  this.deviceMes.light = "当前没有灯亮着";
                } else if (obj[0].count && !obj[1].count) {
                  this.deviceMes.lock = "已上锁";
                  this.deviceMes.light = "当前有 "+obj[0].count+"盏"+obj[0].name+" 亮着";
                } else {
                  this.deviceMes.lock = "已上锁";
                  this.deviceMes.light = "当前没有灯亮着";
                }
              } else {
                //  第一个设备是锁
                if (obj[0].count && obj[1].count) {
                  this.deviceMes.lock = "当前有 "+obj[0].count+" 把锁打开";
                  this.deviceMes.light = "当前有 "+obj[1].count+"盏"+obj[1].name+" 亮着";
                } else if (!obj[0].count && obj[1].count) {
                  this.deviceMes.lock = "已上锁";
                  this.deviceMes.light = "当前有 "+obj[1].count+"盏"+obj[1].name+" 亮着";
                } else if (obj[0].count && !obj[1].count) {
                  this.deviceMes.lock = "当前有"+obj[0].count+"把锁打开";
                  this.deviceMes.light = "当前没有灯亮着";
                } else {
                  this.deviceMes.lock = "已上锁";
                  this.deviceMes.light = "当前没有灯亮着";
                }
              }
            }
          } else {
            this.deviceMes.lock = '锁状态未知';
            this.deviceMes.light = '当前灯状态未知';
          }
        },

        // 横向滑动长按事件
        showDetSenLog:function (key,event) {
          this.nowScene = key;
          this.startX = event.targetTouches[0].pageX;
          this.startY = event.targetTouches[0].pageY;
          // 再次清理定时器，防止重复注册定时器
          clearInterval(this.Loop);
          this.Loop = window.setInterval(this.IntervalSen,800);
        },
        IntervalSen:function () {
          // 这里开始实现自己的逻辑
          this.isShowHori = true;
          console.log("禁用点击");
          this.clickStatus = false;
        },
        clearSenLoop:function (event) {
          window.clearInterval(this.Loop);
        },
        editScene:function (status,key) {
          console.log("++++++++执行场景++++++++index" + key);
          if (status) {
            let sceneId = this.items[key].scene.id;
            console.log(sceneId);
            Cordova.exec(this.getDoneSuccess, this.getDoneFall, "SmartHomePlugins", "executeScene", [sceneId]);
          }
