        // 纵向滑动长按事件
        showDetDevLog:function (key,event) {
          this.nowDevice = key;
          this.startX = event.targetTouches[0].pageX;
          this.startY = event.targetTouches[0].pageY;
          // 再次清理定时器，防止重复注册定时器
          window.clearInterval(this.Loop);
          this.Loop = window.setInterval(this.IntervalDev,800);
        },
        IntervalDev:function () {
          // 这里开始实现自己的逻辑
          this.isShowVor = true;
          console.log("禁用点击");
          this.clickStatus = false;
        },
        // 手指松开执行
        clearDevLoop:function (event) {
          window.clearInterval(this.Loop);
        },

        // 取消默认删除事件
        clearDete:function (event) {
          let DvaX = event.targetTouches[0].pageX - this.startX;
          let DvaY = event.targetTouches[0].pageY - this.startY;
          if (DvaX != 0 || DvaY != 0) {
            // 取消删除事件
            console.log("不触发事件");
            this.isShowVor = false;
            this.isShowHori = false;
            window.clearInterval(this.Loop);
          }
        },

        // 弹框的通用取消方法
        closeCur:function(){
          this.isShowHori = false;
          this.isShowVor = false;
          this.clickStatus = true;
        },

        // 获取到调用返回的数据
        getUserPerListSuccess:function (message) {
          var obj = eval("(" + message + ")");
          console.log("获取用户个人偏好成功" + JSON.stringify(obj));
          this.items = obj.sceneData ? obj.sceneData : false;
          this.itemsVori = obj.deviceData ? obj.deviceData : false;
          console.log("设备"+this.items);
          console.log("场景"+this.itemsVori);
          this.$nextTick(function () {
            document.getElementsByClassName("cube-scroll-content")[0].style.width = (Math.ceil(this.items ? (this.items.length / 2) : 0 ) * 180 + 60) + "px";
            document.getElementsByClassName("cube-scroll-wrapper")[1].style.height = '100%';
            //  控制滑动高度
            document.getElementsByClassName("cube-scroll-content")[1].style.width = '100%';
            document.getElementsByClassName("cube-scroll-content")[1].style.height = (Math.ceil(this.itemsVori ? (this.itemsVori.length / 3) : 0) * 140) + "px";
          })
        },

        getUserPerListFall:function () {
          console.log("获取用户个人偏好失败");
        },

        // 用户操作成功方法
        getDoneSuccess:function () {
          console.log("用户操作成功");
          document.getElementsByClassName("cube-scroll-content")[0].style.width = (Math.ceil(this.items ? (this.items.length / 2) : 0 ) * 180 + 60) + "px";
          document.getElementsByClassName("cube-scroll-wrapper")[1].style.height = '100%';
          //  控制滑动高度
          document.getElementsByClassName("cube-scroll-content")[1].style.width = '100%';
          document.getElementsByClassName("cube-scroll-content")[1].style.height = (Math.ceil(this.itemsVori ? (this.itemsVori.length / 3) : 0) * 140)  + "px";
        },

        // 用户操作失败方法
        getDoneFall:function () {
          console.log("用户操作失败");
        },

        // 删除某个场景
        DetScene:function () {
          console.log("删除场景");
          // 调用删除场景接口
          // 获取数据信息
          var sceneId = this.items[this.nowScene].id;
          this.items.splice(this.nowScene,1);
          document.getElementsByClassName("cube-scroll-content")[0].style.width = (Math.ceil(this.items ? (this.items.length / 2) : 0 ) * 180 + 60) + "px";
          this.closeCur();
          Cordova.exec (this.getDoneSuccess,this.getDoneFall,"SmartHomePlugins","deleteFavorite",[sceneId]);
        },
        // 删除某个设备
        DetDev:function () {
          console.log("删除设备");
          // 调用删除设备接口
          var deviceId = this.itemsVori[this.nowDevice].id;
          this.itemsVori.splice(this.nowDevice,1);
          //  控制滑动高度
          document.getElementsByClassName("cube-scroll-content")[1].style.width = '100%';
          document.getElementsByClassName("cube-scroll-content")[1].style.height = (Math.ceil(this.itemsVori ? (this.itemsVori.length / 3) : 0) * 140)  + "px";
          this.closeCur();
          Cordova.exec (this.getDoneSuccess,this.getDoneFall,"SmartHomePlugins","deleteFavorite",[deviceId]);
        },

        // 更改灯的状态
        changeLight:function (status,key) {
          if (status) {
            console.log("序号为" + key);
            this.nowDevice = key;
            var status = eval("(" + this.itemsVori[key].device.status + ")").status;
            console.log(status);
            var deviceAddress = this.itemsVori[key].device.address;
            if (status == 1) {
              status = 0;
            } else {
              status = 1;
            }
            Cordova.exec(this.operateSingleSuccessFunction, this.operateFailFunction, "SmartHomePlugins", "operateLight", [deviceAddress, status]);
          }
        },

        // 更改风扇状态
        changeFan:function (status,key) {
          if (status) {
            console.log("序号为"+key);
            this.nowDevice = key;
            var status = eval("(" + this.itemsVori[key].device.status + ")").status;
            var deviceAddress = this.itemsVori[key].device.address;
            if (status == 1) {
              status = 0;
            } else {
              status = 1;
            }
            Cordova.exec(this.operateSingleSuccessFunction, this.operateFailFunction, "SmartHomePlugins", "operateLight", [deviceAddress,status]);
          }
        },

        // 更改单控窗帘
        singleCurPop:function (status,key) {
          if(status){
            if (!this.isToPop) {
              this.isToPop = true;
            }
            this.isDoubleCurtain = false;
            this.nowDevice = key;
            var obj = this.dealStr(this.itemsVori[key].device.status);
            this.state = obj.status;
          } else {
            return false;
          }
        },

        // 更改双控窗帘
        douCurPop:function (status,key) {
          if (status) {
            if (!this.isToPop) {
              this.isToPop = true;
            }
            this.isDoubleCurtain = true;
            this.nowDevice = key;
            var obj = this.dealStr(this.itemsVori[key].device.status);
            this.innerState = obj.in;
            this.outterState = obj.out;
          }
        },

        // 控制空调弹窗弹出
        closePop:function () {
          if(this.isAirConditioner){
            this.isAirConditioner = false;
          }
          this.airStatus = this.cloneObj(this.SwitchDetection);
        },

        // 控制窗户弹窗弹出
        closePopCur:function () {
          if (this.isToPop) {
            this.isToPop = false;
          }
        },

        // 双控滑块获取值
        DouInSlider:function (value) {
          this.innerState = value;
        },

        // 双控滑块获取数值
        DouOutSlider:function (value) {
          this.outterState = value;
        },

        // 单控滑块获取数值
        SingleSlider:function (value) {
          this.state = value;
        },

        // 窗帘与服务器交互更改方法
        confirmCur:function () {
          console.log("提交数据");
          this.isToPop = false;
          console.log("当前设备索引"+this.nowDevice);
          var deviceAddress = this.itemsVori[this.nowDevice].device.address;
          if (this.isDoubleCurtain) {
            // 双控接口
            console.log(this.innerState+"       "+this.outterState);
            let type = 2;
            let oper = -1;
            Cordova.exec(this.operateDouCurSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
              "operateDCurtain", [deviceAddress,type,oper,this.innerState, this.outterState]);
          } else {
            // 单控接口
            console.log(this.state);
            let oper = -1;
            Cordova.exec(this.operateSinCurSuccessFunction, this.operateFailFunction, "SmartHomePlugins",
              "operateSCurtain", [deviceAddress,oper,this.state]);
          }
        },

        // 智能门锁的交互
        pushToLock:function (status,key) {
          if (status) {
            this.nowDevice = key;
            var obj = eval("(" + this.itemsVori[this.nowDevice].device.status + ")");
            this.$router.push(
              {
                path: '/blueToothHome',
                name: "blueToothHome",
                query: {
                  titleName: this.itemsVori[this.nowDevice].device.name,
                  devAddress: this.itemsVori[this.nowDevice].device.address,
                  status: obj
                }
              }
            );
          }
        },

        // 与空调的交互
        pushToAir:function (status,key) {
          if (status) {
            this.nowDevice = key;
            var obj =  eval("(" + this.itemsVori[this.nowDevice].device.status + ")");
            // 空调弹框
            this.isAirConditioner = true;
            if (obj.status == 1) {
              this.airStatus.airTurn = true;
            } else {
              this.airStatus.airTurn = false;
            }
            switch (obj.mode + 1) {
              case 0:
                this.airStatus.nowModel = "制冷";
                break;
              case 1:
                this.airStatus.nowModel = "制热";
                break;
              case 2:
                this.airStatus.nowModel = "除湿";
                break;
              case 3:
                this.airStatus.nowModel = "送风";
                break;
              case 4:
                this.airStatus.nowModel = "自动";
                break;
            };
            this.airStatus.mode = obj.mode + 1;
            this.airStatus.airTemp = obj.tem;
            console.log(this.airStatus);
            // 获取空调初始状态
            this.SwitchDetection = this.cloneObj(this.airStatus);
            console.log(obj);
          }
        },

        // 空调的模式选择
        toggle:function () {
          if (this.airStatus.airTurn) {
            this.open = !this.open;
            this.isAirConditioner = false;
            //  获取设备高度
            let Appheight = document.documentElement.clientHeight;
            //  设置弹出的高度
            let marginTop = Appheight * 0.5;
            document.getElementById("show_Model").style.marginTop = marginTop;
          }
        },

        // 空调滑块
        ChangeSlider: function (val) {
          //  这里获取数值
          this.airStatus.airTemp = val;
          console.log(val);
        },

        // 空调模式选择
        closeDrawer(val) {
          this.changeAirMode(val);
          this.open = false;
          this.airStatus.mode = val - 1;
          this.isAirConditioner = true;
          console.log("目前模式为" + this.airStatus.nowModel + " 之前是 " + this.SwitchDetection.nowModel);
        },

        //  空调开关
        ChangeOnTurn() {
          this.airStatus.airTurn = !this.airStatus.airTurn;
          console.log(this.airStatus.airTurn);
        },

        // 模式修改
        changeAirMode(val) {
          switch (val) {
            case 1:
              this.airStatus.nowModel = "制冷";
              this.airStatus.mode = -1;
              break;
            case 2:
              this.airStatus.nowModel = "制热";
              this.airStatus.mode = 0;
              break;
            case 3:
              this.airStatus.nowModel = "除湿";
              this.airStatus.mode = 1;
              break;
            case 4:
              this.airStatus.nowModel = "送风";
              this.airStatus.mode = 2;
              break;
            case 5:
              this.airStatus.nowModel = "自动";
              this.airStatus.mode = 3;
              break;
          }
        },

        confirmAir:function () {
          console.log("提交数据");
          var status = 0;
          if (this.airStatus.airTurn) {
            status = 1;
          } else {
            status = 0;
          }
          Cordova.exec (
            this.operateSingleSuccessFunction,
            this.operateFailFunction,
            "SmartHomePlugins",
            "operateAirConditioner",
            [this.itemsVori[this.nowDevice].device.address,this.airStatus.airTemp,status,this.airStatus.mode,-1,-1,-1,-1]
          );
          this.isAirConditioner = false;
          this.clickStatus = true;
        },


        //收到状态数据
        onReceivePushData: function (data) {
          console.log("收到推送了");
          if (data instanceof Object) {
            var code = data.code;
            if (code == 200) {
              console.log("+++++++++++++收到推送后刷新：+++++++++++++" + JSON.stringify(data.data));
              // 更新单个设备信息
              this.itemsVori = this.itemsVori.map(function (obj, index, array) {
                if (obj.device.address == data.data.address) {
                  console.log("遍历的结果是"+obj);
                  obj.device.status = data.data.status;
                }
                return obj
              });
              console.log("收到推送后处理的状态 ++++++++++" + JSON.stringify(this.itemsVori))
            }
            if (code == "201") {
              console.log("+++++++++++++收到推送后刷新标题状态：+++++++++++++" + JSON.stringify(data.data));
              if(data.data.type == 32513) {
                data.data.count ? this.deviceMes.light = "当前有 "+data.data.count+"盏"+data.data.name+" 亮着" : this.deviceMes.light = "当前没有灯亮着";
              } else {
                data.data.count ? this.deviceMes.lock =  "当前有"+data.data.count+"把"+data.data.name+"打开" : this.deviceMes.lock = "已上锁";
              }
            }
            if (code == "102") {

            }
          }
        },

        // 处理灯等操作
        operateSingleSuccessFunction:function () {
          console.log("用户点击更改状态成功");
        },
        operateFailFunction:function () {
          console.log("用户点击更改状态失败");
        },

        // 处理双控窗帘
        operateDouCurSuccessFunction:function () {
          console.log("用户操作双控成功");

        },
        // 处理单控窗帘
        operateSinCurSuccessFunction:function () {
          console.log("用户操作双控成功");

        },

        // 对象深复制
        cloneObj:function (obj) {
          if(obj === null) return null
          if(typeof obj !== 'object') return obj;
          if(obj.constructor===Date) return new Date(obj);
          if(obj.constructor === RegExp) return new RegExp(obj);
          var newObj = new obj.constructor ();  //保持继承链
          for (var key in obj) {
            if (obj.hasOwnProperty(key)) {   //不遍历其原型链上的属性
              var val = obj[key];
              newObj[key] = typeof val === 'object' ? arguments.callee(val) : val; // 使用arguments.callee解除与函数名的耦合
            }
          }
          return newObj;
        },

        // 用于处理字符串
        dealStr:function (st) {
          return eval("(" + st + ")");
        }
      },
    }

</script>


