<template>
  <div>
    <mu-select-field v-model="item" label=" " :maxHeight="300" :errorText="errorText" underlineFocusClass="underline">
      <mu-menu-item   v-on:click="sentValueToParent(item)"   v-for="item, index in list" :key="index" :title="item" :value="index"/>
    </mu-select-field>
  </div>
</template>

<script>
  export default {
    props: ['listData','errorHint'],
    data () {
      return {
        //默认为空，
        item: '',
        list:this.listData,
        hintText:this.errorHint
      //list: ['SmartHome1', 'SmartHome2', 'SmartHome3', 'SmartHome4', 'SmartHome5', 'SmartHome6']
      }
    },
    computed: {
      errorText () {
        return this.item ? '' : this.hintText
      }
    },

    methods: {
      //父组件传值
      sentValueToParent:function (item) {
        console.log("传值"+item);
        this.$emit("listenerToTouchEvent",item)
      }

    }
  }
</script>

<style>
  .mu-dropDown-menu-text-overflow{
    text-align: -webkit-left;
  }

  .underline{
    background-color: #1eaef9;
  }
  .mu-menu-item-wrapper.active {
    color:black;
  }
  .mu-text-field-label{
    visibility: hidden;
  }

</style>



