<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  components: {

  },
  computed:{
    isShow:function(){
      return true;
    }
  },
  mounted(){
    window.onLoginReturn = this.onLoginReturn;
    console.log("===login mounted");
  },
  methods:{
    onLoginReturn(message) {
        if (message == 1) {
          console.log("首次登录成功");
          //获取当前主机
          Cordova.exec((data)=>{
            console.log("++++++++++获取当前主机成功++++++++++");
            this.$router.push('/indexHome_page');
          }, (data)=>{
            this.$router.push({
            path: '/myControllerList',
            query: {
             fromPageName: "userLogin"
            }
          })
          }, "SmartHomePlugins",
            "getCurrentHost", []);
        }
      },
  }
}
</script>
<style>
  html,body,wrapper{
    width: 100%;
    height:100%;
    overflow: hidden;
  }

#app {
  width: 100%;
  flex-direction: column;
  display: flex;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  background-color:white;
  overflow: hidden;
}
</style>



