<template>
  <div style="height: 100%;">
    <!--引入标题栏-->
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :is-left-click="true"
              @LeftBttonClick='back' @clickRightButton="toSet" :isImg="isImg" :src="srcImg"
              :back-src="backSrc"></titleBar>

    <!--蓝牙连接图标-->
    <div class="blueToothImageDiv">
      <img class="blueToothConnectedImage" :src="blueToothConnectedImage"/>
    </div>

    <!--黑色背景-->
    <div class="blueToothPictureDiv">

      <!--电量低 开锁消息-->
      <div class="blueToothBlackBg" :style="backgroundDiv">

        <div class="blueToothImage" :style="OpenblueToothMsgBgDiv">
          <img class="isLowPowerImage" src="../../../../static/image/lowPowerImage.png" v-show="isShowLowPower"/>
          <!--开锁消息-->
          <div class="OpenblueToothMsgDiv">
            <p class="OpenblueToothMsgTxt">{{OpenMsg}}</p>
          </div>
        </div>
      </div>

      <!--记录 管理 授权-->
      <div class="rowDivForButton">
        <div class="rowItemsDiv" @click="toHistory">
          <img class="IconImage" src="../../../../static/image/recordIcon.png"/>记录
        </div>
        <p class="fengeBlueTooth"></p>
        <div class="rowItemsDiv" @click="toManage">
          <img class="IconImage" src="../../../../static/image/authorizationIcon.png"/>管理
        </div>
        <p class="fengeBlueTooth"></p>
        <div class="rowItemsDiv" @click="toAuthorization">
          <img class="IconImage" src="../../../../static/image/manageIcon.png"/>
          授权
        </div>
      </div>
      <div style="width: 90%;height: auto;position: relative;">
        <mu-divider/>
      </div>

      <!--开锁按钮-->
      <div class="div3">
        <img @click="openBleLock" class="openLockImage" src="../../../../static/image/switch_normal.png"/>
      </div>
    </div>


    <!--验证主机密码弹窗-->
    <mu-dialog :open="showDialog" title="智能锁未校验，请先校验">
      <mu-text-field type="password" v-model="pwdValue" hintText="请输入智能锁管理密码" underlineClass="line"/>
      <mu-flat-button slot="actions" @click="close" primary label="取消"/>
      <mu-flat-button slot="actions" primary @click="confirm" label="确定"/>
    </mu-dialog>
  </div>
</template>


<script>
  import {mapMutations, mapGetters, mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        needOpenLock: false,//是否需要校验成功时开锁，在开锁提示密码错误时使用
        firstStatus: this.$route.query.status,
        isHasChecked: true,//是否已经校验过，未校验过时不能进入授权管理。
        isShowLowPower: false,
        titlemessage: '蓝牙锁',
        title: '蓝牙状态',
        devAddress: this.$route.query.devAddress,
        blueMac: "",
        electricity: "",

        //验证主机密码弹窗
        showDialog: false,
        pwdValue: "",

        //标题栏有关属性
        name: this.$route.query.titleName,
        isSaveVisible: true,
        isTitleBarBackImageVisible: true,
        isImg: true,
        srcImg: "static/image/setting.png",
        backSrc: "static/image/back_white_icon.png",
        //标题栏有关属性  the end
        // 蓝牙是否连接
        isBlueToothConnected: false,
        backgroundDiv: {
          backgroundImage: 'url(' + require('../../../../static/image/blueToothBackgroundImage.png') + ')',
        },
        OpenblueToothMsgBgDiv: {
          backgroundImage: 'url(' + require('../../../../static/image/btLockImage.png') + ')',
        },

        //电量低
        isLowPower: true,
        //消息
        OpenMsg: "密码开锁"

      }
    },
    created() {
      if (this.isDebug) {
        console.log("needCheckVerifyState:" + this.needCheck);
        if (this.needCheck) {
          this.setNeedCheckBleVerfyState(false);
        }
      } else {
        console.log("needCheckVerifyState:" + this.needCheck);
        if (this.needCheck) {
          this.checkBleVerifyState("1");
          this.setNeedCheckBleVerfyState(false);
        } else {
          this.checkBleVerifyState("0");//当不需要再次验证时只设置监听并检查蓝牙连接情况
        }

      }
      console.log("status:" + JSON.stringify(this.firstStatus))
      if (typeof(this.firstStatus) == "object") {
        console.log("this.firstStatus == object")
        this.setStatusTip(this.firstStatus.status + "");
      } else {
        console.log("this.firstStatus != object "+this.lockStatus)
        this.setStatusTip(this.lockStatus);
      }
    },
    mounted() {
      window.onReceivePushData = this.onReceivePushData;
      window.onCheckVerifyCallback = this.onCheckVerifyCallback;
      window.onBleDisconnect = this.onBleDisconnect
      document.getElementsByClassName("TitleBar")[0].style.background = "#2E2F40";
      document.getElementsByClassName("TitleName")[0].style.color = "white";
    }
    ,
    computed: {
      ...mapState({
        needCheck: state => state.blueTooth.needCheckBleVerfyState,
        lockStatus: state => state.blueTooth.lockStatus,
      }),
      blueToothConnectedImage: function () {
        if (this.isBlueToothConnected) {
          return require("../../../../static/image/blueToothIconBlue.png")
        } else {
          return require("../../../../static/image/blueToothIconGrey.png")
        }
      }

    },
    methods: {
      onBleDisconnect(msg) {
        this.isBlueToothConnected = false;
      },
      LeftBttonClick() {
        console.log("点击左边按钮")

        if (this.isDebug) {
          this.$router.go(-1)
        }
      },
      // close() {
      //   //关闭弹窗
      //   this.showDialog = false
      // },
      ...mapMutations([
        'setNeedCheckBleVerfyState', // 将 `this.setNeedCheckBleVerfyState()` 映射为 `this.$store.dispatch('setNeedCheckBleVerfyState')`
        'setLockStatus'
      ]),
      close() {
        //关闭弹窗
        this.showDialog = false;
        this.releaseBle();
        this.isHasChecked = false;
      }
      ,

      confirm() {
        //关闭弹窗
        this.showDialog = false;
        this.verifyBle();
      }
      ,

      toManage() {
        this.$router.push({
          path: '/blueToothManagePage',
          name: 'blueToothManagePage',
          query: {devAddress: this.devAddress, blueMac: this.blueMac}
        })
      }
      ,
      toAuthorization() {
        Cordova.exec((data) => {
            console.log("成功：" + data);
            // this.$router.push({
            //   path: '/AuthorizationManagePage',
            //   name: 'AuthorizationManagePage',
            //   query: {devAddress: this.devAddress}
            // })
            this.$router.push({
              path: '/AddAuthorization',
              name: 'AddAuthorization',
              query: {
               devAddress: this.devAddress,
               fromPageName: "新增授权"
              }
            })
          },
          (data) => {
            console.log("失败：" + data);
            this.showNativeToast("锁未校验，请先输入密码进行校验");
            this.showDialog = true;
          }, "LockPlugins",
          "checkIfBleHasPwd",
          [this.devAddress]);
      }
      ,
      toHistory() {
        this.$router.push({
          path: '/blueToothHistory',
          name: 'blueToothHistory',
          query: {devAddress: this.devAddress}
        })
      }
      ,
      openBleLock() {
        this.needOpenLock = true;
        Cordova.exec((data) => {
            console.log("成功：" + data);
            this.showNativeToast("开锁成功");
            this.needOpenLock = false;
          },
          (data) => {
            console.log("失败：" + data);

            if (data == "-1") {//没有校验过，提示输入密码校验
              this.showNativeToast("锁未校验，请先校验");
              this.showDialog = true;
            } else if (data == "-2") {//没有连接，等待校验成功再去连接
              this.needOpenLock = false;
            } else {
              this.needOpenLock = false;
              this.showNativeToast(data);
            }

          }, "LockPlugins",
          "openBleLock",
          [this.devAddress])
      }
      ,
      toSet() {
        this.$router.push({
          path: '/blueToothSet',
          name: 'blueToothSet',
          query: {devAddress: this.devAddress, name: this.name, electricity: this.electricity}
        })
      },
      back() {
        //关闭弹窗
        this.showDialog = false;
        this.$router.go(-1);
        this.setNeedCheckBleVerfyState(true);
        this.releaseBle();
      }
      ,
      checkBleVerifyState(needCheck) {
        Cordova.exec((data) => {
            console.log("成功：" + data);
            if (needCheck == "0") {//等于0时只设置蓝牙标识
              this.isBlueToothConnected = true;
            }
          },
          (data) => {
            console.log("失败：" + data)
            if (needCheck == "0") {//等于0时只设置蓝牙标识
              this.isBlueToothConnected = false;
            } else {
              this.showNativeToast(data);
              this.showDialog = true;
            }
          }, "LockPlugins",
          "checkBleVerifyState",
          [this.devAddress, needCheck]);
      },
      releaseBle() {
        Cordova.exec((data) => {
            console.log("成功：" + data);

          },
          (data) => {
            console.log("失败：" + data)
          }, "LockPlugins",
          "releaseBleLock",
          []);
      }
      ,
      verifyBle() {
        Cordova.exec((data) => {
            console.log("成功：" + data)
          },
          (data) => {
            console.log("失败：" + data)
          }, "LockPlugins",
          "verifyBle",
          [this.devAddress, this.pwdValue])
      }
      ,
      onCheckVerifyCallback(result) {
        const data = eval("(" + result + ")");
        if (data.code == 0) {//校验结果
          const info = data.data;
          //TODO 界面显示状态信息
          this.electricity = info.elect;
          this.showLockState(info);
          // if (this.needOpenLock) {
          //   this.openBleLock();
          // }
          // this.showNativeToast("校验成功");
          this.isBlueToothConnected = true;
          return;
        }
        if (data.code == 1) {//蓝牙锁状态主动上报
          const info = data.data;
          //TODO 界面显示状态信息
          this.electricity = info.elect;
          this.showLockState(info);
        } else {//失败信息
          if (data.code == -1) {//密码错误
            this.showDialog = true;
          } else {
            this.needOpenLock = false;
          }
          this.showNativeToast(data.data);
        }
      },
      setStatusTip(status) {
        this.setLockStatus(status);
        switch (status) {
          case '0':
            this.OpenMsg = "门锁关闭";
            break;
          case '1':
            this.OpenMsg = "门锁打开";
            break
          case '2':
            this.OpenMsg = "门锁反锁";
            break;

        }
        ;
      },
      //收到状态数据
      onReceivePushData: function (data) {
        if (data instanceof Object && !this.isBlueToothConnected) {
          var code = data.code;
          if (code == "200" && data.data.deviceTypeCode == 0x000B) {
            if(this.isBlueToothConnected)return;
        
            let status = eval("(" + data.data.status + ")");
            this.electricity = eval("(" + data.data.elect + ")");//chenguichun
            if (status.alarm != -1){
               this.showLockState(status); //
            }else{
               this.setStatusTip(status.status + "");
            }
          }
        }
      },
      showLockState(info) {
        if (parseInt(this.electricity) < 10) {
          this.isShowLowPower = true;
        } else {
          this.isShowLowPower = false;
        }
        this.setStatusTip(info.status);
        switch (info.alarm) {
          case 0:
            this.OpenMsg = "暴力破门";
            break;
          case 1:
            this.OpenMsg = "门未上锁";
            break;
          case 2:
            this.OpenMsg = "钥匙未拔";
            break;
          case 3:
            this.OpenMsg = "非法开锁";
            break;
          case 4:
            this.OpenMsg = "门锁被撬";
            break;
          case 5:
            this.OpenMsg = "电量过低";
            break;
          case 6:
            this.OpenMsg = "解除报警";
            break;
        }
      }
    }
  }
</script>


<style>

  .blueToothImageDiv {
    position: relative;
    z-index: 999999;
    height: 64px;
    width: 32px;
    float: right;
    top: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-right: 62px;
  }

  .blueToothConnectedImage {
    z-index: 9999;
    width: 24px;
    height: 24px;
    background-size: auto 24px;
    background-repeat: no-repeat;
    vertical-align: middle;
  }

  .isLowPowerImage {
    position: absolute;
    width: 35%;
    top: 0;
    right: 2%;
  }

  .btLockImage {
    width: 100%;
    height: 100%;
  }

  .OpenblueToothMsgDiv {
    /*position: absolute;*/
    /*bottom: 10%;*/
    color: white;
    /* height: 25%; */
    width: 60%;
    /* background-color: red; */
    background-size: 100% 100%;
    background-repeat: no-repeat;
    margin-top: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../../../../static/image/openMsgBg.png");

  }

  .OpenblueToothMsgImage {
    width: 100%;
  }

  .OpenblueToothMsgTxt {
    /*position: absolute;*/
    /*margin-top: 5%;*/
    margin-top: 10px;
    padding: 5px;
  }

  .openLockImage {
    width: 60%;
    height: auto;
  }

  .IconImage {
    width: 24px;
    height: 24px;
    margin-right: 5px;
  }

  /*蓝牙锁黑色背景*/
  .blueToothBlackBg {
    position: relative;
    background-repeat: no-repeat;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 2;
    background-size: 100% 100%;
  }

  .blueToothImage {
    display: flex;
    background-size: 100% 100%;
    width: 60%;
    /* background-color: burlywood; */
    flex-direction: column;
    align-items: center;
    position: relative;

  }

  /*蓝牙锁黑色背景 the end*/
  .fengeBlueTooth {
    padding: 16px 0px 0px 0px;
    border-left: 1px solid lightgrey;
    font-size: 0;
  }

  .rowDivForButton {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    position: relative;
  }

  .rowItemsDiv {
    flex: 1;
    flex-direction: row;
    padding: 15px;
  }

  .div3 {
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .blueToothPictureDiv {
    width: 100%;
    height: calc(100% - 64px);
    margin-top: 64px;
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

</style>



