<template>
  <div style="height: 100%">
    <!--引入标题栏-->
    <titleBar v-bind:title-name="name"></titleBar>
    <div class="wrapperPage">敬请期待
    </div>
    <div class="bottomNav">
      <mu-divider />
      <bottomNav v-bind:bottom-index="bottomNavIndex"></bottomNav>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name:'安防',
        bottomNavIndex:'安防',
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>

  .wrapperPage{
    margin-top: 64px;
    font-size: large;
    height: calc(100%  - 64px);
  }
  /*控制全屏显示，但是场景与设备列表暂时没有全屏，因为会影响滚动*/
  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



