                        <img src="static/image/unChecked2x.png" v-if="item.isSelect == 0"/>
                        <img src="static/image/checked2x.png" v-else/>
                      </div>
                    </li>
                  </ul>
                  <div v-else>您还没有添加任何场景</div>
                </cube-scroll>
              </div>
              <div v-else>
                <cube-scroll
                  ref="scroll"
                  :data="items">
