      status : {"status":0},
      sceneIds : 1222,
      isAdded : 0,
      isSelect : 1,
    }
    ];

    export default {
      name: "indexHomeAdd_page",
      data(){
        return{
          name:"新增常用",
          isTitleBarBackImageVisible:true,
          isSaveVisible:"visible",
          isImg:false,
          srcImg:"",
          backSrc:"static/image/back_black_icon.png",
          DesWord:"",
          // 选项卡相关配置
          /**
           * changeState 切换对象标识
           * OptionsItmes 遍历对象
           * option 复选选项
           * value 返回值[]
           * sceneCol 样式
           * deviceCol 样式
           * */
          changeState:1,
          OptionsItmes:[],
          scene_options:[],
          device_options:[],
          value_scene:[],
          value_device:[],
          sceneCol:"primary",
          deviceCol:"default",
          items:[]
        }
      },
      created () {
        if (this.isDebug) {
          this.scene_options = options1;
          this.device_options = options2;
          this.OptionsItmes = this.scene_options;
        } else {
          // 请求数据Scene与Device
          Cordova.exec (
            this.getUserSceListSuccess,
            this.getUserListFall,
            "SmartHomePlugins",
            // 获取场景方法
            "getSceneList",
            []
          );
          Cordova.exec (
            this.getUserDevListSuccess,
            this.getUserListFall,
            "SmartHomePlugins",
            // 获取设备方法
            "getUserDeviceList",
            []
          );
        }
      },
      watch:{
        // 监听场景变化
        OptionsItmes:{
          handler:function (oldVal,newVal) {
          },
          deep:true
        }
      },
      mounted () {
        //  修改标题栏目样式
        var title = document.getElementsByClassName("TitleBar")[0];
        title.style.background = "white";
        var titleText = document.getElementsByClassName("TitleName")[0];
        titleText.style.color = "black";
        titleText.style.fontSize = "medium;";
        titleText.style.fontWeight = "bold";
      },
      methods:{
        // 切换场景、设备
        changeContainer:function (key) {
          console.log("场景定义切换");
          if (key == 1) {
            // 场景定义
            this.changeState = 1;
            this.sceneCol = "primary";
            this.deviceCol = "default";
            this.OptionsItmes = this.scene_options;
            document.getElementsByClassName("cube-scroll-content")[0].style.height = this.scene_options.length * 55 + "px";
            console.log(this.OptionsItmes.length+"加载获取场景数量");
          } else {
            //设备定义
            this.changeState = 2;
            this.sceneCol = "default";
            this.deviceCol = "primary";
            this.OptionsItmes = this.device_options;
            document.getElementsByClassName("cube-scroll-content")[0].style.height = this.device_options.length * 55 + "px";
            console.log(this.OptionsItmes.length+"加载获取设备数量");
          }
        },

        testSlider:function (value) {
          console.log(value);
        },

        clickPowerBtn:function () {
          console.log("没有信息");
        },
        // 提交用户选择的场景信息
        handleClick:function () {
          console.log("触发信息");
          // 调用保存方法
          this.value_scene = this.delArrayObj(this.scene_options);
          this.value_device = this.delArrayObj(this.device_options);
          console.log(this.value_scene);
          console.log(this.value_device);
          Cordova.exec (
            this.postListSuccess,
            this.getUserListFall,
            "SmartHomePlugins",
            // 获取方法
            "addFavoriteDatas",
            [this.value_scene,this.value_device]
          );
        },

        // 获取场景
        getUserSceListSuccess:function (message) {
          var obj = eval("(" + message + ")");
          console.log("获取场景成功" + JSON.stringify(obj));
          this.scene_options = obj;
          this.changeContainer(1);
        },

        // 获取设备
        getUserDevListSuccess:function (message) {
          var obj = eval("(" + message + ")");
          console.log("获取设备成功" + JSON.stringify(obj));
          this.device_options = obj;
        },

        postListSuccess:function () {
          this.$router.push('/indexHome_page');
        },
        getUserListFall:function () {
          console.log("获取列表数据失败");
        },

        // 获取场景数组
        checkStates:function (states,index) {
          if (states == 1) {
            // 切换到场景
            let status = this.scene_options[index].isSelect;
            if (status == 0) {
              this.scene_options[index].isSelect = 1;
            } else {
              this.scene_options[index].isSelect = 0;
            }
          } else {
            // 切换到设备
            let status = this.device_options[index].isSelect;
            if (status == 0) {
              this.device_options[index].isSelect = 1;
            } else {
              this.device_options[index].isSelect = 0;
            }
          }
        },

        // 对于新对象的处理方法
        delArrayObj:function (obj) {
          var newArray = [];
          obj.forEach(function(value) {
            if (value.isSelect == 1) {
              newArray.push(value.id);
            }
          })
          return newArray;
        }
      }
    }
</script>

<style lang="less">
  #wapper{
    width:100%;
    height:100%;
    background-color:rgb(249, 249, 249);
  }

  #wapper_head{
