    height:74px;
  }

  #content{
    background-color: rgb(223,223,223);
    height: 100%;
    overflow: hidden;
  }

  .content_head{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 9%;
  }

  .content_head button{
    width: 46%;
  }

  .ul-lists{
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
  }

  .ul-lists-items{
    width: 97%;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bolder;
    font-size: smaller;
    font-family: "PingFang SC";
    color: rgb(51,51,51);
    padding: 1em 0;
    border-top: 1px #dfdfdf solid;
  }

  .ul-item-left{
    display: inline-flex;
    align-items: center;
    margin-left: .6em;
  }

  .ul-item-left img{
    width: 20px;
  }

  .ul-items-text{
    margin-left: 1.7em;
  }

  .ul-items-img img{
    width: 24px;
    margin-right: .4em;
  }

  .content_body{
    overflow: hidden;
    display: flex;
  }

  .content_footer{
    position: absolute;
    bottom: 1em;
    width: 96%;
    left: 2%;
    z-index: 20180719;
  }

   /*滚动模块*/
  #tab-container1{
    width: 98%;
    margin: 0 auto;
  }

  #tab-container2{
    width: 98%;
    margin: 0 auto;
  }

   .horizontal-scroll-list-wrap-vori{
     width:100%;
     height:100%;
     transform: rotate(0deg);
   /*fix 子元素超出边框圆角部分不隐藏的问题*/
     position: relative;
   }

  .horizontal-scroll-list-wrap-vori .cube-scroll-wrapper{
    height: 430px;
   }

  .cube-scroll-content{
    width: 100%;
  }

  /* 单独定义字体 */
  @font-face {
    font-family: "PingFang SC";
    src: url("../../../static/font/PingFangMedium.ttf") format("truetype");
  }
</style>



