<template>
  <div class="wapper">
    <div class="block"></div>
    <!--引入标题栏-->
    <titleBar v-bind:title-name="name" v-bind:is-visible="isTitleBarBackImageVisible"></titleBar>
    <p style="font-size: large;">主机管理</p>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        name:'主机管理',
        isTitleBarBackImageVisible:true,
      }
    },

  }
</script>


<style>
  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



