<template xmlns:v-bind="http://www.w3.org/1999/xhtml" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="stateTextDiv">
    <div  v-for="stateItem in stateArray"  class="stateTextClass">
      <div class="txtPercent">{{stateItem}}</div>
      <p class="fenge1"></p>
    </div>

  </div>
 </template>

<style>
  .txtPercent{
    margin-right: -12px;
    margin-bottom: 0px;
  }
  .stateTextDiv{
    display: flex;
    flex-direction: row;
    text-align: center;
    width:90%;
  }

  .stateTextClass{
    width: calc(100%/4);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
  .fenge1{
    padding:12px 0px 0px 0px;
    border-left: 1px solid grey;
    font-size: 0;
    margin-left: -0.5px;
    width: 1px;
  }

  .fenge0{
    height: 13px;
    padding:12px 0px 0px 0px;
    border-left: 1px solid grey;
    font-size: 0;
  }
  .percentO{
    margin-left: -12px;
    margin-bottom: 0px;
  }
</style>


<script>

  export default {
    data() {
      return {
        stateArray:["25%","50%","75%","100%"]
      }
    },



    created() {
      //console.log(" created  ");
    },
    mounted() {

    },
    methods: {

    }
  }
</script>



