const state = {
  // initial state
  originDeviceLists: "",
  devicesLists: "",
  selectedDeviceTypeObj: {name: "全部", deviceTypeCode: 999999999},
}


// getters
const getters = {
  //按照选择的设备类型，对当前主机列表要显示的数据进行过滤
  getDeviceBySelectedDeviceTypeCode: (state) => (selectedDeviceObj) => {
    state.selectedDeviceTypeObj = selectedDeviceObj;
    //全部
    if (selectedDeviceObj.name == "全部") {
      return state.originDeviceLists
    }
    //筛选单控、双控两种设备
    if (selectedDeviceObj.name == "窗帘") {
      return state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0009 || originDeviceList.deviceTypeCode === 0x0013)
    } else if(selectedDeviceObj.name == "空调"){
      return state.originDeviceList.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0005 || originDeviceList.deviceTypeCode === 0x0006 || originDeviceList.deviceTypeCode == 0x0011)
    } else {
      return state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === selectedDeviceObj.deviceTypeCode)
    }
  }
}

// action
const actions = {
  refreshListsByActions({state,commit}) {
    // console.log(state.selectedDeviceTypeObj.name)
    //全部
    if (state.selectedDeviceTypeObj.name == "全部") {
      commit('refreshFilteredDevicesLists', state.originDeviceLists)
      return
    }
    //筛选单控、双控两种设备
    if (state.selectedDeviceTypeObj.name == "窗帘") {
      commit('refreshFilteredDevicesLists', state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0009 || originDeviceList.deviceTypeCode === 0x0013))
    } else if(state.selectedDeviceTypeObj.name == "空调"){
      commit('refreshFilteredDevicesLists', state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0005 || originDeviceList.deviceTypeCode === 0x0006 || originDeviceList.deviceTypeCode == 0x0011))
    }else {
      commit('refreshFilteredDevicesLists', state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === state.selectedDeviceTypeObj.deviceTypeCode))
    }
  }
}


// 删选数据列表
const mutations = {
  refreshCurrentDeviveTypeObj(state,payload) {
    state.selectedDeviceTypeObj = payload;
    console.log("当前设备类型 ++++++++++" + JSON.stringify(state.selectedDeviceTypeObj))
  },

  refreshFilteredDevicesLists(state,payload) {
    state.devicesLists = payload;
    //console.log(state)
    console.log("筛选列表数据 ++++++++++" + JSON.stringify(state.devicesLists))
  },

  //一般为进入页面查询到的数据或者刷新到的数据
  refreshDevicesLists(state,payload) {
    //payload 最新的数据
    payload.sort((m,n)=>{
      if(m.deviceTypeCode > n.deviceTypeCode){
        return -1;
      }else if(m.deviceTypeCode == n.deviceTypeCode){
        if(m.id < n.id){
          return -1;
        }else{
          return 1;
        }
      }else{
        return 1;
      }
    });
    state.originDeviceLists = payload;
    //根据当前设备类型 处理显示的数据
    if (state.selectedDeviceTypeObj.name == "全部") {
      state.devicesLists = state.originDeviceLists
      return
    }
    if(state.selectedDeviceTypeObj.name == "空调"){
      state.devicesLists = state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0005 || originDeviceList.deviceTypeCode === 0x0006 || originDeviceList.deviceTypeCode == 0x0011)
    }//筛选单控、双控两种设备
    else if (state.selectedDeviceTypeObj.name == "窗帘") {
      state.devicesLists = state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === 0x0009 || originDeviceList.deviceTypeCode === 0x0013)
    } else {
      state.devicesLists = state.originDeviceLists.filter(originDeviceList => originDeviceList.deviceTypeCode === state.selectedDeviceTypeObj.deviceTypeCode)
    }
    console.log("当前设备类型 ++++++++++" + JSON.stringify(state.selectedDeviceTypeObj) + "界面显示的列表数据 ++++++++++" + JSON.stringify(state.devicesLists))
  },

  //根据收到推送的单个设备的状态，更新数据
  refreshControlDeviceState(state,payload) {
    var temp = state.originDeviceLists.map(function (obj, index, array) {
      if (obj.address == payload.address) {
        obj = payload;
      }
      return obj
    })
    state.originDeviceLists = temp;
    state.devicesLists = state.originDeviceLists;
    console.log("收到推送的单个设备的状态 ++++++++++" + JSON.stringify(state.originDeviceLists))
  },
}


export default {
  state,
  getters,
  actions,
  mutations
}



