<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar title-name="修改密码" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              @clickRightButton="save" :src="srcImg" :DesWord="DesWord" :back-src="backSrc"></titleBar>

    <div class="pwdInPage">
      <mu-divider/>

      <div class="pwdDiv pdTopAndpdBottom">
        <div class="pwdRowDiv">
          <p class="pwdItemName">原密码</p>
          <mu-text-field :type="origionPwdType" v-model="origionPwd" hintTextClass='pwdHintClasss'
                         inputClass="pwdInputClass" hintText="原管理员密码" underlineClass="line"/>
          <img class='pwdDeleteText' v-show="origionPwd!=''" v-on:click="clearPsw"
               src="../../../../../static/image/clearPsw.png"/>
          <img class='pwdShowPwd' v-on:click="changeOriginTextType" :src="origionPwdTypeImage"/>
        </div>
      </div>
      <mu-divider/>

      <div class="pwdDiv pdTopAndpdBottom">
        <!--门锁名称-->
        <div class="pwdRowDiv">
          <p class="pwdItemName">新密码</p>
          <mu-text-field :type="newPwdType" v-model="newPwd" hintTextClass='pwdHintClasss'
                         inputClass="pwdInputClass" hintText="新管理员密码" underlineClass="line"/>
          <img class='pwdDeleteText' v-show="newPwd!=''" v-on:click="clearNewPsw"
               src="../../../../../static/image/clearPsw.png"/>
          <img class='pwdShowPwd' v-on:click="changeNewTextType" :src="newPwdTypeImage"/>
        </div>
      </div>
      <mu-divider/>

      <div class="pwdDiv pdTopAndpdBottom" style="margin-top: 0px">
        <!--门锁名称-->
        <div class="pwdRowDiv">
          <p class="pwdItemName">确认密码</p>
          <mu-text-field :type="newComfirmPwdType" v-model="newComfrimPwd" hintTextClass='pwdHintClasss'
                         inputClass="pwdInputClass" hintText="再次输入新管理员密码" underlineClass="line"/>
          <img class='pwdDeleteText' v-show="newComfrimPwd!=''" v-on:click="clearNewComfirmPsw"
               src="../../../../../static/image/clearPsw.png"/>
          <img class='pwdShowPwd' v-on:click="changePwdComfirmTextType" :src="newComfirmPwdTypeImage"/>
        </div>
      </div>
      <mu-divider/>
    </div>
  </div>
</template>


<script>

  export default {
    data() {
      return {
        //标题栏有关属性
        devAddress: this.$route.query.devAddress,
        name: this.$route.query.name,
        electricity: this.$route.query.electricity,
        isSaveVisible: false,
        isTitleBarBackImageVisible: true,
        isImg: false,
        DesWord: "保存",
        backSrc: "static/image/close.png",
        srcImg: "static/image/add_icon1.png",
        //标题栏有关属性  the end

        settings: '',

        origionPwdType: "password",
        origionPwdTypeImage: require("../../../../../static/image/pswNumber.png"),
        newPwdType: "password",
        newPwdTypeImage: require("../../../../../static/image/pswNumber.png"),
        newComfirmPwdType: "password",
        newComfirmPwdTypeImage: require("../../../../../static/image/pswNumber.png"),

        //门锁名称
        origionPwd: "",//原密码
        newPwd: "",//新密码
        newComfrimPwd: ""//确认密码
      }
    },
    created() {
      if (this.electricity == "") {
        this.electricity = "未知"
      }
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },


    computed: {},

    methods: {
      clearPsw() {
        this.origionPwd = ""
      },
      clearNewPsw() {
        this.newPwd = ""
      },
      clearNewComfirmPsw() {
        this.newComfrimPwd = ""
      },
      changeOriginTextType() {
        if (this.origionPwdType == "password") {
          this.origionPwdType = "text"
          this.origionPwdTypeImage = "static/image/pswNumberBlue.png"
        } else {
          this.origionPwdType = "password"
          this.origionPwdTypeImage = "static/image/pswNumber.png"
        }
      },
      changeNewTextType() {
        if (this.newPwdType == "password") {
          this.newPwdType = "text";
          this.newPwdTypeImage = "static/image/pswNumberBlue.png"
        } else {
          this.newPwdType = "password";
          this.newPwdTypeImage = "static/image/pswNumber.png";
        }
      },
      changePwdComfirmTextType() {
        if (this.newComfirmPwdType == "password") {
          this.newComfirmPwdType = "text";
          this.newComfirmPwdTypeImage = "static/image/pswNumberBlue.png"
        } else {
          this.newComfirmPwdType = "password";
          this.newComfirmPwdTypeImage = "static/image/pswNumber.png";
        }
      },
      //保存
      save() {
        //
        var msg = "";
        if (this.origionPwd == "") {
          msg = "原密码不能为空";
          console.log(msg);
          this.showNativeToast(msg);
          return;
        } else if (this.newPwd == "") {
          msg = "新密码不能为空";
          console.log(msg);
          this.showNativeToast(msg);
          return;
        } else if (this.newComfrimPwd != this.newPwd) {
          console.log("两次输入的新密码不一致，请确认");
          this.showNativeToast("两次输入的新密码不一致，请确认");
          return;
        }
        if (this.isDebug) {
          this.$router.go(-1)
        } else {
          Cordova.exec((data) => {
              console.log("成功：" + data);
              this.showNativeToast(data);
              this.$router.go(-1);
            },
            (data) => {
              console.log("失败：" + data);
              this.showNativeToast(data);
            }, "LockPlugins",
            "modifyBluePwdProcess",
            [this.origionPwd, this.newPwd, this.devAddress]);
        }
      }
    },
  }
</script>


<style>

  .pwdItemName {
    flex: 1;
    text-align: left;
    font-size: small;
    margin-left: 20px;
    font-weight: bold;
  }

  .pwdItemName.value {
    text-align: right;
    opacity: 0.8;
    font-weight: normal;
    margin-right: 20px;
    font-size: 15px;
  }

  /***********临时密码输入框样式 ************/

  .TempPswRowInfo .tempPswsettingPageHintClasss {
    text-align: right;
    padding-right: 20px;
    font-size: medium;
  }

  .TempPswRowInfo .mu-text-field {
    margin-bottom: 0px;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .TempPswRowInfo .mu-text-field-line {
    display: none;
  }

  .TempPswRowInfo .mu-text-field-focus-line {
    margin-left: 20px;
    background-color: #5bc8ff;
    display: none;
  }

  .TempPswRowInfo .mu-text-field-content {
    padding: 0px;
  }

  /***********临时密码输入框样式 the end ************/

  /***********密码输入框样式 ************/
  .pwdHintClasss {
    text-align: right;
    margin-right: 20px;
    font-size: 14px;
    text-align: right;
  }

  .line {
    margin-left: 20px;
  }

  .pwdRowDiv .mu-text-field-focus-line {
    margin-left: 20px;
    background-color: #5bc8ff;
    display: none;
  }

  .pwdRowDiv .mu-text-field-line {
    display: none;
  }

  /*.pwdRowDiv {*/
  /*padding: 5px;*/
  /*}*/

  /* .mu-text-field-input.pwdInputClass {
    text-align: right;
  } */

  .pwdInputClass {
    text-align: right;
    margin-right: 20px;
  }

  .pwdRowDiv .mu-text-field-content {
    padding: 0px;
  }

  .pwdRowDiv .mu-text-field {
    margin-bottom: 0px;
  }

  /***********密码输入框样式 the end ************/

  .numberVisible {
    width: 20px;
    height: 20px;
    margin-right: 20px;
    margin-left: 10px;
  }

  .pwdDeleteText {
    width: 20px;
    height: 20px;
    margin-left: 10px;
  }

  .pwdShowPwd {
    width: 20px;
    height: 20px;
    margin-left: 10px;
    margin-right: 20px;
  }

  .pwdDiv {
    background-color: white;
    width: 100%;
    margin-top: 10px;
    position: relative;
    border-top-width: 5px;

  }

  .pwdDiv .mu-text-field {
    flex: 1;
    min-height: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .settingsDivNoMargion .mu-text-field {
    flex: 1;
    min-height: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .pwdRowDiv {
    width: 100%;
    display: flex;
    flex-direction: row;
    /* justify-content:flex-start;  */
    align-items: center;
    padding: 5px;
    /* padding-top: 10px;
    padding-bottom: 10px; */
  }

  .pwdInPage {
    background-color: #f9f9f9;
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
  }

  .pdTopAndpdBottom {
    /* padding-top: 10px;
    padding-bottom: 10px; */
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }
</style>



