<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              @clickRightButton="addNewAuthorization" :isImg="false"  :back-src="backSrc" :DesWord="desWord"></titleBar>

    <div class="listInPage">

    <mu-divider/>
    <div id="mescroll" class="mescroll">
      <div class="AuthorizationDataDiv" id="AuthorizationDataDiv">
      <!-- <mt-loadmore :top-method="refresh"  :bottom-all-loaded="true" ref="loadmore"
                   @top-status-change="handleTopChange"> -->
        <!-- <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/> -->
        
        <swiper-layout-box class="my-layout">
          <swiper-layout
            :key="index" type="right" menuHeight="120px" :menuWidth="'40%' || '100%'"
            v-model="target.open" v-for="(target, index) in list">

            <template slot="content">
              <div @touchstart="changeClose(target, index)" v-on:click="editItem(target, index)">
                <!--授权具体内容-->
                <div class="dataInfo">
                  <img class="AuthorizationIcon" :src="target.src"/>
                  <div class="AuthorizationNameTxtDiv">
                    <p class="AuthorizationNameTxt">{{target.name}}</p>
                  </div>
                </div>
                <mu-divider/>
                <p class="validityDiv">有效期：{{target.timer}}
                  <br/>
                  有效次数：{{target.times == "255"?"不限":target.times}}
                </p>
                <mu-divider/>

              </div>
            </template>

            <template slot="menu">
              <div class="menu-item" @click="editItem(target, index)">编辑</div>
              <div class="menu-item" @click="deleteItem(target, index)">删除</div>
            </template>
          </swiper-layout>
        </swiper-layout-box>
         <!-- <div slot="top" class="mint-loadmore-top">
          <span v-show="refreshing"><fading-circle/></span>
        </div>
      </mt-loadmore> -->
        </div>
    </div>

    </div>
  </div>
</template>


<script>
  import MeScroll from '../../../../mescroll/mescroll.m'
  import '../../../../mescroll/mescroll.min.css'
  export default {
    data() {
      return {
        //地址
        devAddress:this.$route.query.devAddress,
        refreshing:false,
        //下拉弹窗
        list: [],

        //标题栏有关属性
        name: "授权管理",
        isSaveVisible: true,
        isTitleBarBackImageVisible: true,
        // isImg: true,
        srcImg: "static/image/add_icon1.png",
        backSrc: "static/image/close.png",
        //标题栏有关属性  the end
        trigger:null,
        mescroll: {},
        isFirst:true,
        desWord:""
      }
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
      this.trigger = this.$el;
      this.$nextTick(() => {
        var vm = this;
        this.mescroll = new MeScroll("mescroll", {//第一个参数"mescroll"对应上面布局结构div的id
          down: {
            callback: vm.refresh, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
          },
          // up: {
          // callback: this.refresh() //上拉加载回调,简写callback:function(page){upCallback(page);}
          // }
        });
      })
    },
    created() {
      if (this.isDebug) {
        this.list = [{
          "id": "1",
          "keyId": "1",
          "password": "0",
          "type": "1",
          "name": '指纹一',
          'start': 1520411067823,
          'end': 1520411267823,
          "times": 255,
          "authCode": "1",
          "open": false,
        },
          {
            "id": "2",
            "keyId": "0",
            "password": "123",
            "type": "2",
            "name": '指纹二',
            'start': 1520411067823,
            'end': 1520411267823,
            'times': 2,
            "authCode": "2",
            "open": false,
          },
          {
            "id": "3",
            "keyId": "3",
            "password": "0",
            "type": "3",
            "name": '指纹三',
            'start': 1520411067823,
            'end': 1520411267823,
            'times': 3,
            "authCode": "3",
            "open": false,
          },
          {
            "id": "4",
            "keyId": "4",
            "password": "0",
            "type": "4",
            "name": '指纹四',
            'start': 1520411067823,
            'end': 1520411267823,
            'times': 4,
            "authCode": "4",
            "open": false,
          },
          {
            "id": "5",
            "keyId": "5",
            "password": "0",
            "type": "5",
            "name": '指纹五',
            'start': 1520411067823,
            'end': 1520411267823,
            'times': 5,
            "authCode": "5",
            "open": false,
          },
          {
            "id": "5",
            "keyId": "5",
            "password": "0",
            "type": "5",
            "name": '指纹六',
            'start': 1520411067823,
            'end': 1520411267823,
            'times': 5,
            "authCode": "5",
            "open": false,
          },
        ];
        this.computeImageInList(this.list);
        this.parseTimePeriod(this.list);
        this.list.map((obj) => {
          if (obj.type == "5" || obj.type == 5) {
            obj.name = "临时密码";
          }
        });
      } else {
        this.toLoadAuthKeyList();
      }
    },

    methods: {
      loadmore() {
        console.log("loadmore");
      },
      handleTopChange(status) {
        this.topStatus = status;
      },
      refresh(){
        if(this.isFirst){
          this.isFirst = false;
          return;
        }
        this.refreshing = true;
        this.toLoadAuthKeyList();
      },
      //滑块
      changeClose(target, index) {
        for (var i = 0; i < this.list.length; i++) {
          if (index != i) {
            this.list[i].open = false

          }
        }
      },
      deleteItem(target, index) {
        //index[1]为界面需要删除的id  index[0]为该条场景内的id
        if (this.isDebug) {
          this.list.splice(index, 1);
        } else {
          console.log("++++++++删除场景++++++++ " + JSON.stringify(target));
          var address = this.devAddress //在主机中的设备编号
          var id = target.id;
          var authCode = target.authCode;
          Cordova.exec((data) => {
              console.log("成功：" + data)
              this.showNativeToast("删除成功");
              this.toLoadAuthKeyList();
            },
            (data) => {
              console.log("失败：" + data)
              this.showNativeToast("删除失败：" + data);
            }, "LockPlugins",
            "deleteAuthKey",
            [address, id, authCode])
        }

      },
      editItem(target, index) {
        console.log("++++++++编辑授权+++++index+++" + index + JSON.stringify(target)),
          this.$router.push({
            path: '/AddAuthorization',
            name: 'AddAuthorization',
            query: {
              devAddress: this.devAddress,
              fromPageName: "编辑授权",
              authObj: target
            }
          })
      },

      back() {
        this.$router.go(-1)
      },
      addNewAuthorization() {
        this.$router.push({
          path: '/AddAuthorization',
          name: 'AddAuthorization',
          query: {
            devAddress: this.devAddress,
            fromPageName: "新增授权"
          }
        })
      },
      toLoadAuthKeyList() {
        Cordova.exec((data) => {
            let tempList = eval("(" + data + ")");

            this.computeImageInList(tempList);
            this.parseTimePeriod(tempList);
            tempList.map((obj) => {
              if (obj.type == "5" || obj.type == 5) {
                obj.name = "临时密码";
              }
              obj.open = false;
            });
            this.list = tempList;
            console.log("this.list:" + JSON.stringify(this.list));
            if(this.refreshing){
              this.refreshing = false;
              this.mescroll.endSuccess();//无参
              // this.$refs.loadmore.onTopLoaded();
              this.showNativeToast("刷新成功");
            }
          },
          (data) => {
            console.log("失败：" + data)
            if(this.refreshing){
              this.refreshing = false;
              this.mescroll.endSuccess();//无参
              // this.$refs.loadmore.onTopLoaded();
              this.showNativeToast("刷新失败："+data);
            }
          }, "LockPlugins",
          "listAuthKey",
          [this.devAddress]);
      },

    },
  }
</script>


<style>
  /************滑块样式**********/
  .my-layout .swiper-layout-parent .swiper-slide {
    border: none;
    line-height: 120px;
  }

  .my-layout .swiper-layout-parent .swiper-slide.content {
    /*padding-left: 20px;*/
  }

  .my-layout .swiper-layout-item {
    border: none;
    border-bottom: 1px solid #EAEAEB;
  }

  .gury {
    color: #ccc;
  }

  .my-layout .before-menu .menu-item {
    width: 50%;
    float: left;
    text-align: center;
    color: #fff;
  }

  .my-layout .before-menu .menu-item:nth-of-type(1) {
    background-color: #FF9C00;
  }

  .my-layout .before-menu .menu-item:nth-of-type(2) {
    background-color: #FF3B30;
  }

  /************滑块样式 the end**********/

  .backIcon {
    background-image: url(/static/image/close.png);
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    background-position: 1% 50%;
    background-size: 25px 25px;
  }

  .AuthorizationIcon {
    margin-left: 20px;
    width: 30px;
    height: 30px;
    float: left;
  }

  .AuthorizationNameTxt {
    font-size: medium;
    font-weight: bold;
    margin: 16px;
  }

  .AuthorizationNameTxtDiv {
    margin-left: 20px;
    margin-top: 0px;
    flex: 1;
    text-align: left;
    font-size: medium;
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60px;
    line-height: 60px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  .listInPage {
    background-color: #f9f9f9;
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
    /*padding-top: 10px;*/
  }

  .AuthorizationDataDiv {
    background-color: #fcfcfc;
    /*display: flex;*/
    display: inline-block;
    flex-direction: column;
    width: 100%;
    overflow:auto;
    position: relative;
    user-select: none;
    margin-bottom: 10px;
    -webkit-overflow-scrolling: touch;
  }

  .dataInfo {
    background-color: white;
    display: flex;
    flex-direction: row;
    width: 100%;
    /*height: 60px;*/
    /*line-height: 60px;*/
    align-items: center;
    justify-content: center;
  }

  .validityDiv {
    background-color: #fcfcfc;
    text-align: left;
    font-size: small;
    color: #666666;
    padding: 20px;
    margin: 0px;
    height: 100%;
    line-height: 100%;
    vertical-align: middle;
  }

</style>



