<template>
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar v-bind:title-name="name" v-bind:is-visible="isTitleBarBackImageVisible"></titleBar>
    <div class="msgDiv">
      <p class="hint">设备日志：</p>
      <mu-divider/>
    <div v-for="msg in msgList" >
      <mu-list-item :title="msg.description">
        <p class="creatTime">{{msg.createTime}}</p>
      </mu-list-item>
      <mu-divider />
    </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        name:'消息',
        isTitleBarBackImageVisible:true,
        //查询到的消息
        msgList: [{description:"密码开锁",createTime:"2018-1-17 15:30"},
          {description:"密码开锁",createTime:"2018-1-17 15:30"},
          {description:"客厅灯开",createTime:"2018-1-17 18:30"},
          {description:"窗帘开",createTime:"2018-1-17 06:30"},
        ],
      }
    },
    created(){
      Cordova.exec(this.getRecordListByUserSuccess, this.getRecordListByUserFail, "SmartHomePlugins",
      "getRecordListByUser", []);
    },
    methods:{
      getRecordListByUserSuccess:function (message) {
        var obj=eval("("+message+")");
        this.msgList=obj;
      },
      getRecordListByUserFail:function (message) {

      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  .msgDiv{
    margin-top: 64px;
    height: calc(100% - 64px);
    overflow: scroll;
  }
  .creatTime{
    position: absolute;
    top: 0;
    right:0;
    margin-right: 20px;
  }
  /*更改样式，使行内文字左对齐;*/
  .mu-item.show-right{
    width: 100%;
    text-align: left;
  }

  .mu-item{
    text-align: left;
  }
  .logText{
    text-align: left;
    margin: 16px;
    font-size:medium ;
    height: 20px;
    opacity: 0.5;
  }

  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



