<template>
  <div class="wrapper" id="wrapper">
    <!-- 引入标题 -->
    <div id="title_top">
      <userTitle :isImg=true titleName="忘记密码"></userTitle>
    </div>
    <!-- 页面部分 -->
    <div id="wrapper_paper">
      <!-- 登录框 -->
      <div id="user_getPwd">
        <div class="user_text" id="user_name">
          <div class="item_left">
            <img class="items_img" src="static/image/user_icon.png">
          </div>
          <div class="items_input">
            <!--v-model.trim=""-->
            <input type="tel" id="userName" placeholder="请输入手机号" autocomplete="off" v-model="userPhone" ref="userPhone" />
            <div class="icon-deletemobile" style="display:none" @click="reset"></div>
          </div>
        </div>
        <div class="user_text" id="user_check">
          <div class="item_left">
            <img class="items_img" src="static/image/checkSafe.png">
          </div>
          <div class="items_input">
            <!--v-model.trim=""-->
            <input type="text" placeholder="请输入短信验证码" autocomplete="off" v-model="userMes" ref="userMes"/>
            <button ref="MesCode" class="passCheck" value="" style="background-color:#5bc8ff;">获取验证码</button>
          </div>
        </div>
        <div class="user_text" id="user_pwd">
          <div class="item_left">
            <img class="items_img" src="static/image/password_icon.png">
          </div>
          <div class="items_input">
            <input id="userPwd" type="password" placeholder="6位以上数字和字母组合密码" v-model="userPwd" autocomplete="off" ref="userPwd" />
            <div class="icon-concealment" style="display: none" @click="showPwd"></div>
          </div>
        </div>
        <div class="pass_button" ref="pass_button" @click="PassInfor">
          <span class="tipText">提交</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        // data:{
          userPhone:"",
          userPwd:"",
          userMes:""
        // }
      }
    },

    mounted(){
      this.$refs.MesCode.addEventListener("click",this.sendMes,false);
    },
    watch:{
      userPhone:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "none";
        }else{
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "";
        }
        if (!/^\d+$/.test(newVal) && newVal != ""){
           this.userPhone = oldVal;
        }
      },
      userPwd:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("icon-concealment")[0].style.display = "none";
        }else{
          document.getElementsByClassName("icon-concealment")[0].style.display = "";
        }
      }
    },
    methods:{
      reset(){
        //  涉及到了vue的虚拟dom与生命周期
        this.userPhone = "";
        this.$nextTick(()=>{
          let username = document.getElementById("userName");
          username.value = "";
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "none";
        })
      },

      showPwd(){
        let userPwd = document.getElementById("userPwd");
        if(userPwd.getAttribute("type") == "text"){
          userPwd.setAttribute("type","password");
        }else{
          userPwd.setAttribute("type","text");
        }
      },

      sendMes(){
        console.log("发送验证码");

        let userPhone = this.userPhone.trim();
        let regUser = /^1[3|4|5|7|8][0-9]{9}$/;
        regUser = new RegExp(regUser);
        if (userPhone.length == 0 && userPhone == "") {
          console.log("手机号不能为空");
          this.showNativeToast("手机号不能为空");
          return;
        } else if (!regUser.test(userPhone)) {
          console.log("手机验证不通过");
          this.showNativeToast("手机验证不通过");
          return;
        }

        var Mestime = 60;
        //  1：进行数据交互
        //  2: 显示倒计数
        
        let o = this.sendMes;

        //调用自定义的验证插件
        if (!this.isDebug) {
          Cordova.exec((data) => {
            this.showNativeToast("验证码获取成功");
            var TIP = this.$refs.MesCode;
            TIP.removeEventListener("click",this.sendMes,false);
            TIP.style.backgroundColor = "rgba(27, 28, 29, 0.34)";
              function time(o){
                if(Mestime == 0){
                TIP.innerHTML = "发送验证码";
                 TIP.addEventListener("click",o,false);
                 TIP.style.backgroundColor = "#5bc8ff";
              }else{
                TIP.innerHTML = "等待 "+ Mestime+" 秒";
                Mestime--;
                setTimeout(()=>{
                  time(o);
                },1000)
              }
            }
            time(o);
            }, (data) => {
              this.showNativeToast("获取失败：" + data);
            }, "SmartHomePlugins",
            "getVerify", [userPhone]);
        }

        
      },

      PassInfor(){
        let userPhone = this.userPhone.trim();
        let userPwd = this.userPwd.trim();
        let userMes = this.userMes.trim();
        let regUser = /^1[3|4|5|7|8][0-9]{9}$/;
        regUser = new RegExp(regUser);
        let regPwd = /^(?![0-9]*$)(?![a-zA-Z]*$)[0-9A-Za-z]{6,}$/;
        regPwd = new RegExp(regPwd);
        if(userPhone.length == 0 && userPhone == ""){
          console.log("手机号不能为空");
          this.showNativeToast("手机号不能为空");
        }else if(userPwd.length == 0 && userPwd == ""){
          console.log("密码不能为空");
          this.showNativeToast("密码不能为空");
        }else if(userMes == "" && userMes.length == 0){
          console.log("验证码不能为空");
          this.showNativeToast("验证码不能为空");
        }else if(!regUser.test(userPhone)){
          this.showNativeToast("手机号码格式不正确，请确认");
        }else if(!regPwd.test(userPwd)){
          console.log("密码格式不正确");
          this.showNativeToast("密码格式不正确");
        // }else if("test" != userMes){
        //   console.log("验证码不通过");
        }else {
          //调用自定义的验证插件
          Cordova.exec((data) => {
              this.showNativeToast("修改成功");
              this.$router.push({
                  path: '/',
                  name: 'login',
                  query: {
                    account: userPhone,
                    password:userPwd,
                  }
                })
            }, (data) => {
              this.showNativeToast("修改失败:" + data);
            }, "SmartHomePlugins",
            "findPassword", [userPhone, userPwd, userMes]);
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  *{
    margin: 0px;
    padding: 0px;
      #wrapper{
        height:100%;
        width: 100%;
      #title_top{
        height: 60px;
      }
    }
  }

  #wrapper_paper{
    background-color: #f9f9f9;
    height: 100%;
    /* 解决样式重叠
       父div使用
     */
    overflow:hidden;
  }

  #user_getPwd{
    height: 45%;
    margin-top:5%;
    overflow:hidden;
    width: 100%;
  }

  .user_text {
    border: 1px solid #dfdfdf;
    height: 16%;
    margin: 7px auto;
    width: 80%;
    background-color: white;
    display: inline-flex;
    align-items: center;
  }

  .item_left{
    width: auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center 
  }

  .items_img{
    width: 40%;
    // margin-top: 20%;
  }


  .items_input{
    width: 100%;
    height:100%;
    display: inline-flex;
    align-items: center;
    input{
      width: 100%;
      height: 100%;
      border:0px;
      font-size:14px;
    }
  }

  .icon-concealment{
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/concealment_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 10px;
  }

  .icon-deletemobile{
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/delete_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 10px;
  }

  .pass_button{
    width: 80%;
    // height: 15%;
    background-color: #5bc8ff;
    font-family: PingFangLight;
    margin: 18px auto 10px;
    line-height:50px;
  }

  .tipText{
    color: white;
    font-size: medium;
  }

  .otherOp{
    width: 90%;
    margin: 0 auto;
    margin-top: 5px;
    display: inline-block;
  }

  .passCheck{
    background-color: #5bc8ff;
    width: 50%;
    height: 70%;
    color: white;
    border-style: none;
    border-radius: 3px;
    margin: 8px 5px;
  }
  @font-face {
    font-family: PingFangLight;
    src: url('../../../../static/font/PingFangLight.ttf');
  }

</style>



