<template>
  <div class="wrapper" id="wrapper">
    <!-- 引入标题 -->
    <div id="title_top">
      <userTitle :isImg=true titleName="注册"></userTitle>
    </div>
    <!-- 页面部分 -->
    <div id="wrapper_paper">
      <!-- 登录框 -->
      <div id="user_register">
        <div class="user_text" id="user_name">
          <div class="item_left">
            <img class="items_img" src="static/image/user_icon.png">
          </div>
          <div class="items_input">
            <!--v-model.trim=""-->
            <input id="userName" type="tel" placeholder="请输入手机号" v-model="userPhone" autocomplete="off"
                   ref="userPhone" />
            <div class="icon-deletemobile" style="display:none" @click="reset"></div>
          </div>
        </div>
        <div class="user_text" id="user_pwd">
          <div class="item_left">
            <img class="items_img" src="static/image/password_icon.png">
          </div>
          <div class="items_input">
            <input id="userPwd" type="password" placeholder="6位以上数字和字母组合密码" v-model="userPwd"
                   autocomplete="off" ref="userPwd" />
            <div class="icon-concealment" style="display: none" @click="showPwd"></div>
          </div>
        </div>
        <div class="user_text" id="user_check">
          <div class="item_left">
            <img class="items_img" src="static/image/checkSafe.png">
          </div>
          <div class="items_input">
            <!--v-model.trim=""-->
            <input type="text" placeholder="请输入短信验证码" v-model="userMes" autocomplete="off" ref="userMes"/>
            <button ref="MesCode" class="passCheck" value="" style="background-color:#5bc8ff;">获取验证码</button>
          </div>
        </div>
        <div class="register_button" ref="register_button" style="background-color:rgba(0, 0, 0, 0.31);">
          <span class="tipText">注册</span>
        </div>
        <div class="otherOp">
          <input type="checkbox" @change="changeCol" ref="checkBox_one"/>
          &nbsp;&nbsp;
          <span>我已阅读并接受优特物联《许可协议》、《隐私政策》及《法律声明》</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "",
    data() {
      return {
        // data: {
          userPhone: "",
          userPwd: "",
          userMes: ""
        // },
      }
    },

    mounted() {
      this.$refs.MesCode.addEventListener("click", this.sendMes, false);
    },
    watch:{
      userPhone:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "none";
        }else{
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "";
        }
        if (!/^\d+$/.test(newVal) && newVal != ""){
           this.userPhone = oldVal;
        }
      },
      userPwd:function (newVal,oldVal) {
        if(newVal == ""){
          document.getElementsByClassName("icon-concealment")[0].style.display = "none";
        }else{
          document.getElementsByClassName("icon-concealment")[0].style.display = "";
        }
      }
    },
    methods: {
      reset() {
        //  涉及到了vue的虚拟dom与生命周期
        this.userPhone = "";
        this.$nextTick(() => {
          let username = document.getElementById("userName");
          username.value = "";
          document.getElementsByClassName("icon-deletemobile")[0].style.display = "none";
        })
      },

      showPwd() {
        let userPwd = document.getElementById("userPwd");
        if (userPwd.getAttribute("type") == "text") {
          userPwd.setAttribute("type", "password");
        } else {
          userPwd.setAttribute("type", "text");
        }
      },

      sendMes() {
        console.log("发送验证码");

        let userPhone = this.userPhone.trim();
        let regUser = /^1[3|4|5|7|8][0-9]{9}$/;
        regUser = new RegExp(regUser);
        if (userPhone.length == 0 && userPhone == "") {
          console.log("手机号不能为空");
          this.showNativeToast("手机号不能为空");
          return;
        } else if (!regUser.test(userPhone)) {
          console.log("手机验证不通过");
          this.showNativeToast("手机号码不正确");
          return;
        }

        var Mestime = 60;
        //  1：进行数据交互
        //  2: 显示倒计数
        
        let o = this.sendMes;

        //调用自定义的验证插件
        if (!this.isDebug) {
          Cordova.exec((data) => {
              this.showNativeToast("验证码获取成功");
              var TIP = this.$refs.MesCode;
              TIP.removeEventListener("click", this.sendMes, false);
              TIP.style.backgroundColor = "rgba(27, 28, 29, 0.34)";
              function time(o) {
                if (Mestime == 0) {
                  TIP.innerHTML = "发送验证码";
                  TIP.addEventListener("click", o, false);
                   TIP.style.backgroundColor = "#5bc8ff";
                 } else {
                   TIP.innerHTML = "等待 " + Mestime + " 秒";
                   Mestime--;
                   setTimeout(() => {
                        time(o);
                   }, 1000)
                 }
             }
             time(o);
            }, (data) => {
              this.showNativeToast("获取失败：" + data);
            }, "SmartHomePlugins",
            "getVerify", [userPhone]);
        }

        
      },

      registerUser() {
        let userPhone = this.userPhone.trim();
        let userPwd = this.userPwd.trim();
        let userMes = this.userMes.trim();
        let regUser = /^1[3|4|5|7|8][0-9]{9}$/;
        regUser = new RegExp(regUser);
        let regPwd = /^(?![0-9]*$)(?![a-zA-Z]*$)[0-9A-Za-z]{6,}$/;
        regPwd = new RegExp(regPwd);
        if (userPhone.length == 0 && userPhone == "") {
          console.log("手机号不能为空");
          this.showNativeToast("手机号不能为空");
        } else if (userPwd.length == 0 && userPwd == "") {
          console.log("密码不能为空");
          this.showNativeToast("密码不能为空");
        } else if (userMes == "" && userMes.length == 0) {
          console.log("验证码不能为空");
          this.showNativeToast("验证码不能为空");
        } else if (!regUser.test(userPhone)) {
          this.showNativeToast("手机号码格式不正确，请确认");
        } else if (!regPwd.test(userPwd)) {
          console.log("密码验证不通过");
          this.showNativeToast("密码格式不正确，请确认");
          // } else if ("test" != userMes) {
          //   console.log("验证码不通过");
          //   this.showToast("验证码不正确");
        } else {
          //调用自定义的验证插件
          if(this.isDebug){
            this.$router.push({
              path: '/',
              name: 'login',
              query: {
                account: userPhone,
                password:userPwd,
              }
            })
          }else{
            Cordova.exec((data) => {
                this.showNativeToast("注册成功");
                this.$router.push({
                  path: '/',
                  name: 'login',
                  query: {
                    account: userPhone,
                    password:userPwd,
                  }
                })
              }, (data) => {
                this.showNativeToast("注册失败:" + data);
              }, "SmartHomePlugins",
              "register", [userPhone, userPwd, userMes]);
          }

        }
      },

      changeCol() {
        let checkBox = this.$refs.checkBox_one;
        let register = this.$refs.register_button;
        if (checkBox.checked) {
          register.style.backgroundColor = "rgb(91, 200, 255)";
          register.addEventListener("click", this.registerUser, false);
        } else {
          register.style.backgroundColor = " rgba(0, 0, 0, 0.31)";
          register.removeEventListener("click", this.registerUser, false);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  * {
    margin: 0px;
    padding: 0px;
    #wrapper {
      height: 100%;
      width: 100%;
      #title_top {
        height: 60px;
      }
    }
  }

  #wrapper_paper {
    background-color: #f9f9f9;
    height: 100%;
    /* 解决样式重叠
       父div使用
     */
    overflow: hidden;
  }

  #user_register {
    height: 45%;
    margin-top: 5%;
    overflow: hidden;
    width: 100%;
  }

  .user_text {
    border: 1px solid #dfdfdf;
    height: 16%;
    margin: 8px auto;
    width: 80%;
    background-color: white;
    display: inline-flex;
    align-items: center;
  }

  .item_left {
    width: auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center 
  }

  .items_img {
    width: 40%;
    // margin-top: 20%;
  }

  .items_input {
    width: 100%;
    height: 100%;
    display: inline-flex;
    align-items: center;
    input {
      width: 100%;
      height: 100%;
      border: 0px;
      font-size: 14px;
    }
  }

  .passCheck {
    width: 50%;
    height: 70%;
    color: white;
    border-style: none;
    border-radius: 3px;
    margin: 8px 5px;
  }

  .icon {
    width: 10%;
    height: 100%;
  }

  .icon-concealment {
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/concealment_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 10px;
  }

  .icon-deletemobile {
    background-size: 15px 15px;
    background-repeat: no-repeat;
    background-image: url(../../../../static/image/delete_icon.png);
    width: 18px;
    height: 15px;
    margin: 0 5px 0 10px;
  }

  .register_button {
    width: 80%;
    // height: 15%;
    font-family: PingFangLight;
    margin: 18px auto 10px;
    overflow: hidden;
    line-height: 48px;
  }

  .tipText {
    color: white;
    font-size: medium;
  }

  .otherOp {
    width: 80%;
    margin: 0 auto;
    margin-top: 5px;
    font-family: PingFangLight;
    font-size: 11px;
  }

  @font-face {
    font-family: PingFangLight;
    src: url('../../../../static/font/PingFangLight.ttf');
  }

</style>



