<template xmlns:v-bind="">

  <mu-paper>
    <mu-bottom-nav :value="bottomValue">
      <mu-bottom-nav-item value="首页" title="首页" icon=" "  v-bind:iconClass="{selected:this.bottomIndex=='首页'}"  :titleClass="{text_selected:this.bottomIndex=='首页'}" to="/indexHome_page"/>
      <mu-bottom-nav-item value="场景" title="场景" icon=" "  v-bind:iconClass="{selected:this.bottomIndex=='场景'}"  :titleClass="{text_selected:this.bottomIndex=='场景'}" to="/scene_page"/>
      <mu-bottom-nav-item value="设备" title="设备" icon=" "  v-bind:iconClass="{selected:this.bottomIndex=='设备'}"  :titleClass="{text_selected:this.bottomIndex=='设备'}" to="/device_page"/>
      <mu-bottom-nav-item value="我的" title="我的" icon=" "  v-bind:iconClass="{selected:this.bottomIndex=='我的'}"  :titleClass="{text_selected:this.bottomIndex=='我的'}" to="/mine_page"/>
    </mu-bottom-nav>
  </mu-paper>

</template>

<script>
  export default {
   props:['bottomIndex'],
    data () {
      return {
        //bottomValue 数据来源 初始化是通过页面布局传入
        bottomValue:this.bottomIndex,
      }
    },
    methods: {
      created(){
      },
      mounted(){
      }
    },
  }
</script>
<style>
  a:nth-child(1) .mu-bottom-item-icon {
    background-image: url(../../../static/image/indexHome_normal.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }
  a:nth-child(1) .mu-bottom-item-icon.selected{
    background-image: url(../../../static/image/indexHome_press.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }
  a:nth-child(2) .mu-bottom-item-icon {
    background-image: url(../../../static/image/device_page_normal.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }
  a:nth-child(2) .mu-bottom-item-icon.selected {
    background-image: url(../../../static/image/device_page_press.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }

  a:nth-child(3) .mu-bottom-item-icon{
    background-image: url(../../../static/image/scene_page_normal.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }
  a:nth-child(3) .mu-bottom-item-icon.selected {
    background-image: url(../../../static/image/scene_page_press.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }

  a:nth-child(4) .mu-bottom-item-icon {
    background-image: url(../../../static/image/mine_page_normal.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }

  a:nth-child(4) .mu-bottom-item-icon.selected {
    background-image: url(../../../static/image/mine_page_press.png) ;
    background-repeat:no-repeat;
    width:24px;
    height:24px;
    background-size:auto 24px;
  }

  .mu-bottom-item-active .mu-bottom-item-text.text_selected {
    color: rgba(0,0,0,.54);
    font-size: inherit;
  }
  .mu-bottom-nav-shift {
    width:100%;
    FILTER: progid:DXImageTransform.Microsoft.Gradient(gradientType=0,startColorStr=#808080,
    endColorStr=#808080); /*IE*/
    background:-moz-linear-gradient(left,#808080, #808080);/*火狐*/
    background:-webkit-gradient(linear, 0% 0%, 100% 0%,from(#808080), to(#808080));/*谷歌*/
  }

</style>



