<template>
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar title-name="开锁记录" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :back-src="backSrc"></titleBar>

    <div class="histortyListInPageHistorty">
      <mu-divider/>


      <div class="historyDataDiv" v-for="(data,index) in list">
        <!--图片 名字-->
        <div class="dataInfo">
          <img class="historyIcon" :src="data.src"/>
          <p class="historyNameTxt">{{data.description}}</p>
        </div>

        <p class="historyValidityDiv">{{data.time}}</p>
        <mu-divider/>
      </div>

    </div>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        isTitleBarBackImageVisible: true,
        isSaveVisible: false,
        backSrc: "static/image/close.png",
        //下拉弹窗
        list: [],
        //地址
        devAddress: this.$route.query.devAddress,
      }
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },

    created() {
      // this.computeImageInList(this.list);
      if (this.isDebug) {
        this.list = [{
          "id": 7,
          "userId": 49,
          "createTime": "2018-03-16 14:12:13",
          "hostId": 31,
          "address": "",
          "statusCode": "3",
          "value": "",
          "description": "蓝牙开锁",
          "time": "2018-03-16 15:27:57.0"
        },
          {
            "id": 6,
            "userId": 49,
            "createTime": "2018-03-16 14:12:13",
            "hostId": 31,
            "address": "",
            "statusCode": "3",
            "value": "",
            "description": "蓝牙开锁",
            "time": "2018-03-16 15:27:57.0"
          }];
        this.setImage();
      } else {
        this.toLoadKeyRecord();
      }
    },

    methods: {
      back() {
        this.$router.go(-1)
      },
      addNewHistorty() {
        this.$router.push('/AddHistorty');
      },
      toLoadKeyRecord() {
        Cordova.exec((data) => {
            this.list = eval("(" + data + ")");
            this.setImage();
          },
          (data) => {
            console.log("失败：" + data)
          }, "LockPlugins",
          "listLockLog",
          [this.devAddress]);
      },
      setImage() {
        console.log(JSON.stringify(this.list))
        //根据授权类型加载图片
        this.list.map((obj) => {
          switch (obj.statusCode) {
            case "0":
            case 0:
              return obj.src = "static/image/fingerprint.png"
              break;
            case "1":
            case 1:
              return obj.src = "static/image/psw.png"
              break;
            case "2":
              return obj.src = "static/image/key.png"
              break;
            // case "3":
            //   obj.src = obj.src = "static/image/key.png"
              // break
            case "4":
            case 4:
              return obj.src = "static/image/card.png"
              break;
            case "5":
            case 5:
              return obj.src = "static/image/tempPsw.png"
              break;
            default:
              return obj.src = "static/image/app.png"
              break;
          }
        });
      }
    },
  }
</script>


<style>
  /***********列表 ************/

  .historyDataDiv {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: white;
    -webkit-overflow-scrolling: touch;
  }

  .dataInfo {
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
  }

  .historyIcon {
    margin-left: 20px;
    width: 30px;
    height: 30px;
  }

  .historyNameTxt {
    font-size: small;
    font-weight: bold;
    margin-top: 16px;
    margin-bottom: 16px;
    margin-left: 16px;
    flex: 1;
    text-align: left;
  }

  .historyValidityDiv {
    text-align: left;
    font-size: small;
    margin-left: 60px;
    margin-bottom: 16px;
    opacity: 0.8;
  }

  /***********列表 the end ************/
  .right .mu-flat-button {
    margin: 0px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  .histortyListInPageHistorty {
    background-color: white;
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
    -webkit-overflow-scrolling: touch;
  }

  .dataInfo {
    background-color: #ffffff;
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
  }


</style>



