<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <div class="addDeviceListHeader1">
      <p v-on:click="cancel" class="changeRegion">取消</p>
      <div class='allDeviceDiv' v-on:click="allDevice">
        <img class='cancelImage' src="../../../../static/image/allDeviceRegion.png"/>
        <p style="float: right;margin-left:10px;margin-top:0px;margin-right: 0px;text-align: center">全部设备</p>
      </div>
    </div>
    <div class="listWrapper">
      <mu-divider/>
      <mu-paper :zDepth="1" class="regionMargin" :rounded="true">

        <!--无区域时显示“未设置区域”弹窗-->
        <mu-popup position="top" :overlay="false" popupClass="region-popup-top1" :open="regionList.length == 0">
          <div class="regionPopupBg">
            <p class="NullRegionTextHint2">暂未设置区域</p>
          </div>
        </mu-popup>

        <div class="regionItemDiv" v-for="(parentRegion,parentIndex) in regionList" v-if="parentRegion.parentRegionNo==0">

          <!--------父区域-------->
          <div class="paregionItemDivTest" v-on:click="setRegion(parentIndex)">
            <div class="regionNameFlex">
              <p class="paregionRegionNameTextStyle">{{parentRegion.name}}</p>
            </div>
            <div class="selectedRegionDiv" v-bind:class="{ visible: selectedRegion==parentIndex }">
              <img class='selectedRegionImage' v-bind:class="{ visible: selectedRegion==parentIndex }"
                   src="../../../../static/image/check_icon.png"/>
              <div class="selectedTxt">已选择该区域</div>
            </div>
          </div>

          <!--<div class="regionHint" v-on:click="setRegion(index)">{{parentRegion.name}}</div>-->

          <mu-divider/>

          <!--------子区域-------->
          <div v-for="(childRegion,childIndex) in regionList" v-on:click="setRegion(childIndex)"
               v-if="childRegion.parentRegionNo!=0 && (childRegion.parentRegionNo==parentRegion.ordinal)">
            <div class="itemDivTest">
              <div class="regionNameFlex">
                <p class="regionNameTextStyle">{{childRegion.name}}</p>
              </div>
              <div class="selectedRegionDiv" v-bind:class="{ visible: selectedRegion==childIndex }">
                <img class='selectedRegionImage' v-bind:class="{ visible: selectedRegion==childIndex }"
                     src="../../../../static/image/check_icon.png"/>
                <div class="selectedTxt">已选择该区域</div>
              </div>
            </div>
            <mu-divider/>
          </div>
        </div>
      </mu-paper>
    </div>
  </div>

</template>

<script>
  import {mapMutations, mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        name: '我的区域',
        isTitleBarBackImageVisible: true,
        isRegionDataNull:false
      }
    },

    computed: {
      //组件上需要的变量与store中处理后的数据之间的映射
      ...mapState({
        regionList: state => state.myRegionList.regionLists,
        currentRegionObj: state => state.myRegionList.currentRegion,
        selectedRegion: state => state.myRegionList.selectedRegionIndex,
      })
    },


    created() {
      if (this.isDebug) {
        this.refreshLists([])
        //更新 selectedRegion（在列表中的第几个 0到.length）
        this.computedRegionIdInList();
      } else {
        //进入页面在查询区域列表
        Cordova.exec(this.getAllRegionSuccessFunction, this.getAllRegionFailFunction, "SmartHomePlugins",
          "getAllRegion", []);
      }
    },

    methods: {
      //通过映射，可通过this.xxx映射到this.$store.commit('xxx')`
      ...mapMutations([
        'refreshLists', //更新区域列表
        'refreshDevicesLists',  //更新区域下的设备
        'refreshCurrentRegionObj',  //更新当前区域对象
        'refreshIsFromRegionPage',  //是否加载选中每一个区域后的设备
        'computedRegionIdInList'  //显示当前选中的状态
      ]),

      ...mapActions([
        'refreshCurrentRegionObjByActions', // 将 `this.refreshCurrentRegionObjByActions()` 映射为 `this.$store.dispatch('refreshCurrentRegionObjByActions')`
      ]),
      //区域
      getAllRegionSuccessFunction: function (message) {
        var obj = eval("(" + message + ")");
        //更新区域
        this.refreshLists(obj)
        //更新 selectedRegion（在列表中的第几个 0到.length）
        this.computedRegionIdInList();
      },
      getAllRegionFailFunction: function () {
        console.log("++++++++获取区域数据失败++++++++")
      },


      setRegion: function (index) {
        this.refreshCurrentRegionObjByActions(index);
        this.refreshIsFromRegionPage(true)
        if (this.isDebug) {
          this.refreshDevicesLists([{
            "hostId": 8,
            "region": 2,
            "deviceTypeCode": 32513,
            "id": 297,
            "address": "13_13",
            "devViewCode": 0,
            "status": "{\n\t\"status\":-1\n}",
            "sceneIds": "",
            "name": "区域JDQ_灯1"
          },]);

        }
        this.$router.go(-1)
      },


      allDevice: function () {
        //-1 表示全部设备
        this.refreshCurrentRegionObjByActions(-1);
        //改变标志位
        this.refreshIsFromRegionPage(true)
        if (this.isDebug) {
          this.refreshDevicesLists([
            {
              "hostId": 8,
              "region": 2,
              "deviceTypeCode": 32513,
              "id": 297,
              "address": "13_13",
              "devViewCode": 0,
              "status": "{\n\t\"status\":-1\n}",
              "sceneIds": "",
              "name": "JDQ_灯1"
            },
            {
              "hostId": 8,
              "region": 2,
              "deviceTypeCode": 32513,
              "id": 298,
              "address": "11_11",
              "devViewCode": 0,
              "status": "{\n\t\"status\":1\n}",
              "sceneIds": "",
              "name": "JDQ_灯2"
            },
            {
              "hostId": 8,
              "region": 2,
              "deviceTypeCode": 32513,
              "id": 299,
              "address": "12_12",
              "devViewCode": 0,
              "status": " ",
              "sceneIds": "71,84,",
              "name": "JDQ_灯3"
            },
            {
              "hostId": 8,
              "region": 2,
              "deviceTypeCode": 0x000B,
              "id": 300,
              "address": "10_10",
              "devViewCode": 0,
              "status": "{\n\t\"status\":2\n}",
              "sceneIds": "71,84,",
              "name": "锁"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 9,
              "id": 301,
              "address": "23_-1",
              "devViewCode": 0,
              "status": "{\n\t\"status\":-1\n}",
              "sceneIds": "",
              "name": "单控1"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 9,
              "id": 303,
              "address": "23_-2",
              "devViewCode": 0,
              "status": " ",
              "sceneIds": "",
              "name": "单控2"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 9,
              "id": 301,
              "address": "23_-1",
              "devViewCode": 0,
              "status": "{\n\t\"status\":100\n}",
              "sceneIds": "",
              "name": "单控3"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 9,
              "id": 303,
              "address": "23_-2",
              "devViewCode": 0,
              "status": "{\n\t\"status\":-1\n} ",
              "sceneIds": "",
              "name": "双控1"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 9,
              "id": 303,
              "address": "23_-2",
              "devViewCode": 0,
              "status": " ",
              "sceneIds": " ",
              "name": "双控2"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 19,
              "id": 303,
              "address": "23_-2",
              "devViewCode": 0,
              "status": "{\n\t\"in\":10\n}",
              "sceneIds": "",
              "name": "双控3"
            },
            //空调的测试数据
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 0x0005,
              "id": 304,
              "address": "23_-3",
              "devViewCode": 0,
              "status": "{\n\t\"in\":10\n}",
              "sceneIds": "",
              "name": "空调1"
            },
            {
              "hostId": 8,
              "region": 1,
              "deviceTypeCode": 0x0006,
              "id": 304,
              "address": "23_-3",
              "devViewCode": 0,
              "status": "{\n\t\"in\":10\n}",
              "sceneIds": "",
              "name": "空调2"
            }
          ])
        }
        this.$router.go(-1)
      },

      cancel: function () {
        this.$router.go(-1)
      },

    }
  }
</script>


<style>
  .selectedRegionImage {
    width: 36px;
    height: 36px;
    visibility: hidden;
    margin: 0 auto;
  }

  .selectedRegionDiv {
    visibility: hidden;
    justify-content: center;
    flex-direction: column;
    display: flex;
    margin-right: 20px;
  }

  .visible {
    visibility: visible;
  }

  .regionNameFlex {
    flex: 1;
    text-align: left;
    vertical-align: middle;
    justify-content: center;
    display: flex;
    flex-direction: column;
  }

  .itemDivTest {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
  }
  .paregionItemDivTest {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: center;
    /*background-color: #f9f9f9;*/
  }

  selectedTxt {
    visibility: visible;
    color: black;
    opacity: 0.5;
    font-size: small;
    margin-right: 10px;
  }

  .cancelImage {
    /*margin-top: 30px;*/
    width: 24px;
    height: 24px;
  }

  .allDeviceDiv {
    float: left;
    margin-left: 20px;
    height: 84px;
  }

  .changeRegion {
    float: right;
    margin-right: 20px;
    font-size: medium;
    margin-top: 0px;
  }

  .addDeviceListHeader1 {
    position: fixed;
    z-index: 99999999;
    width: 100%;
    height: 84px;
    line-height: 84px;
    background-color: white;
    padding-top: 20px;
  }

  .textStyle {
    font-size: large;
    text-align: left;
    padding-left: 10px;
    flex: 1;
  }

  .paregionRegionNameTextStyle {
    text-align: left;
    padding-left: 10px;
    height: 40px;
    line-height: 40px;
    /*background-color: #f9f9f9;*/
    /* font-size: 1rem; */
    font-size: large;
    opacity: 0.5;
  }
  .regionNameTextStyle {
    font-size: large;
    text-align: left;
    padding-left: 10px;
    opacity: 0.5;
  }

  .listWrapper {
    margin-top: 84px;
    height: calc(100% - 84px);
    overflow: scroll;
    background-color: #f9f9f9;
    -webkit-overflow-scrolling: touch;
  }

  .regionMargin {
    margin: 20px;
  }

  .regionHint {
    height: 40px;
    line-height: 40px;
    background-color: #f9f9f9;
    font-size: 1rem;
    color: #999999;
    opacity: 0.5;
    text-align: left;
    padding-left: 10px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  /***************无区域弹窗************/
  .region-popup-top1 {
    margin-top: 100px;
    height: 80px;
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    background-color: lightgrey;
  }

  /*弹窗提示*/
  .regionPopupBg {
    background-color: lightgrey;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }

  .NullRegionTextHint2 {
    margin: 0px;
    font-size: medium;
  }
</style>



