<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar :title-name="name"  :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :is-left-click="true" :back-src="backSrc" @LeftBttonClick='backToList'></titleBar>

    <div class="contentList">
      <mu-divider/>
      <p class="hintContentList">当前主机列表</p>
      <mu-divider/>
      <!--主机列表-->
      <div class="demo-grid">
        <!--主机列表-->
        <mu-row>
          <mu-col class="controllerItem" v-for="(device,index) in devicesList" width="100" tablet="100" desktop="100">
            <div class="controller" @touchstart ="showUnBindDialog(index)" @touchend="clearLoop">
              <img class='controllerImage' src="../../../../static/image/controller_icon.png"/>
              <!--<img class='settingsImage' v-if="device.isCurrentHost"  v-on:click="clickToSettings" src="../../../../static/image/ic_settings.png"/>-->
              <div class="flexLeft">
                <p class="textName">{{device.name}}</p>
                <p class="textstatus" v-if="device.status==1">在线</p>
                <p class="textstatus" v-if="device.status==0">离线</p>
              </div>
              <div class="flexRight">
                <img class='checkImageHidden' v-bind:class="{checkImageVisible: device.isCurrentHost}"
                     src="../../../../static/image/check_icon.png"/>
                <p class="text1" v-if="device.isCurrentHost">已设置为当前主机</p>
                <p class="text1" v-else v-on:click.stop="setToCurrentController(index)">设置为当前主机</p>
              </div>
            </div>
            <mu-divider/>
          </mu-col>
        </mu-row>
      </div>
    </div>
    <!--底部添加主机-->
    <div class="addControllerDiv">
      <mu-divider/>
      <img class="addButton" v-on:click="clickToaddController_page"
           src="../../../../static/image/addControllerIcon.png"/>
      <div v-on:click="clickToaddController_page">添加主机设备</div>
    </div>


    <!--确认切换主机弹出框-->
    <div v-if="devicesList.length!=0" class="confirmDialogInvisible"
         v-bind:class="{confirmDialogVisible:isConfirmDialogVisible}">
      <mu-dialog :open="isConfirmDialogVisible" title="提示" @close="close">
        确认切换 {{this.devicesList[this.clickControllerIndex].name}} 为当前主机？
        <mu-flat-button slot="actions" @click="close" primary label="取消"/>
        <mu-flat-button slot="actions" primary @click="confirm" label="确定"/>
      </mu-dialog>
    </div>

    <!--确认解绑主机弹出框-->
    <div v-if="devicesList.length!=0">
      <mu-dialog :open="unBindDialog" title="提示" @close="close">
        确认解绑 {{this.devicesList[this.unBindDialogIndex].name}} 主机？
        <mu-flat-button slot="actions" @click="closeUnBind" primary label="取消"/>
        <mu-flat-button slot="actions" primary @click="unBind" label="确定"/>
      </mu-dialog>
    </div>
  </div>
</template>

<script>

  import {mapMutations, mapState, mapActions} from 'vuex'

  export default {
    data() {
      return {
        name: '我的主机',
        isTitleBarBackImageVisible: true,
        isSaveVisible: false,
        backSrc: "static/image/close.png",
        //查询到的数据
        devicesList: [],
        //原始数据
        OriginDeviceList: [],
        //当前主机标志位
        currentControllerIndex: 0,
        //确认切换主机弹出框
        isConfirmDialogVisible: false,
        //点击某一条
        clickControllerIndex: 0,
        Loop: "",
        unBindDialog: false,
        unBindDialogIndex: 0,
        fromPage: this.$route.query.fromPageName,
      }
    },
    computed: {},

    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },
    created() {
      if (this.isDebug) {
        //临时数据
        this.OriginDeviceList = [
          {isCurrentHost: false, deviceType: '开关', name: '开关', status: '0', mac: "111"},
          {isCurrentHost: false, deviceType: '蓝牙锁', name: '蓝牙锁', status: '1', mac: "222"},
          {isCurrentHost: false, deviceType: '开关', name: '开关', status: '0', mac: "333"},
          {isCurrentHost: false, deviceType: '蓝牙锁', name: '蓝牙锁', status: '1', mac: "444"},

        ],
        //更新数据展示
        this.devicesList = this.OriginDeviceList;
//        this.devicesList=[];
      } else {
        //获取已添加主机列表接口
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction, "SmartHomePlugins",
          "getHostList", []);
      }
    },

    methods: {

      //通过映射，可通过this.xxx映射到this.$store.commit('xxx')`
      ...mapMutations([
        'refreshIsFromRegionPage',  //切换需要重新获取下全部设备
      ]),

      ...mapActions([
        'refreshCurrentRegionObjByActions', //切换需要重新获取下全部设备
      ]),

      // 长按事件
      showUnBindDialog(index) {
        this.unBindDialogIndex = index;
        clearInterval(this.Loop);//再次清空定时器，防止重复注册定时器
        this.Loop = window.setInterval(this.Interval, 800);
      },
      Interval: function () {
        //弹出解绑主机
        console.log("弹出解绑主机")
        this.unBindDialog = true;
      },

      clearLoop() {
        window.clearInterval(this.Loop);
      },

      //解绑主机
      unBind: function () {
        this.unBindDialog = false;
        var mac1 = this.devicesList[this.unBindDialogIndex].mac
        //确定解绑
        if (this.isDebug) {
          console.log("确定解绑 mac1" + mac1)
        } else {
          Cordova.exec(this.unbindHostSuccessFunction, this.unbindHostFailFunction, "SmartHomePlugins",
            "unbindHost", [mac1]);
        }

      },
      closeUnBind: function () {
        this.unBindDialog = false
      },

      unbindHostSuccessFunction: function () {
        this.showNativeToast("解绑成功")
        //重新 获取已添加主机列表接口
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction,"SmartHomePlugins",
          "getHostList", []);
      },

      unbindHostFailFunction: function () {
        this.showNativeToast("解绑失败")
      },

      getHostListSuccessFunction: function (message) {
        var obj = eval("(" + message + ")");
        console.log("获取主机列表成功" + JSON.stringify(obj))
        //-{
//        "id": 9,
//          "name": "ut000000000000000000000000000000",
//          "mac": "000000000001",
//          "type": "host",
//          "bootVersion": "1000",
//          "configVersion": "3",
//          "dataVersion": "1000",
//          "serverVersion": "1000",
//          "qrcode": "111",
//          "createTime": 1516848110000,
//          "status": "1",
//          "isCurrentHost": 1
//      }
        obj.sort((obj)=>{
          if(obj.isCurrentHost){
            return -1;
          }else{
            return 1;
          }
        });
        this.devicesList = obj;
      },
      getHostListFailFunction: function () {
        console.log("获取主机列表失败")
        this.devicesList = [];
      },

      backToList: function () {
        console.log("fromPage  " + this.fromPage)
        if (this.fromPage == 'mine_page') {
          this.$router.push(
            {
              path: '/mine_page'
            }
          );
        } else {
          this.$router.push(
            {
              path: '/scene_page'
            }
          );
        }

      },
      //点击设置为当前主机
      setToCurrentController: function (index) {
        console.log("点击设置为当前主机")
        //点击某一条，用以获取主机名称，弹窗提示
        this.clickControllerIndex = index;
        //提示是否切换主机
        this.isConfirmDialogVisible = true;
      },

      confirm() {
        if (this.isDebug) {
          //-1 表示全部设备
          this.refreshCurrentRegionObjByActions(-1);
          this.refreshIsFromRegionPage(false)
          //关闭弹窗
          this.isConfirmDialogVisible = false;
          //改回之前的当前主机
          this.devicesList[this.currentControllerIndex].isCurrentHost = false;
          //记录最新的当前主机索引
          this.currentControllerIndex = this.clickControllerIndex;
          //更改最新的当前主机
          this.devicesList[this.currentControllerIndex].isCurrentHost = true;
          this.devicesList.sort((obj)=>{
            if(obj.isCurrentHost){
              return -1;
            }else{
              return 1;
            }
          });
        } else {
          var mac1 = this.devicesList[this.clickControllerIndex].mac;
          console.log("设置当前主机+++++++++mac1+++++++" + mac1)
          Cordova.exec(this.setCurrentHostSuccessFunction, this.setCurrentHostFailFunction, "SmartHomePlugins",
            "setCurrentHost", [mac1]);
        }
      },

      setCurrentHostSuccessFunction: function () {
        console.log("设置当前主机成功")
        //关闭弹窗
        this.isConfirmDialogVisible = false;
        //改变标志位  切换主机后需要重新获取下全部设备
        //-1 表示全部设备
        this.refreshCurrentRegionObjByActions(-1);
        this.refreshIsFromRegionPage(false)

//        //改回之前的当前主机
//        this.devicesList[this.currentControllerIndex].isCurrentHost=false;
//        //记录最新的当前主机索引
//        this.currentControllerIndex = this.clickControllerIndex;
//        //更改最新的当前主机
//        this.devicesList[this.currentControllerIndex].isCurrentHost=true;
//        console.log("改回之前的当前主机" +this.devicesList[this.currentControllerIndex].name +"为false"
//          +"更改最新的当前主机" +this.devicesList[this.currentControllerIndex].name +"为true")
        Cordova.exec(this.getHostListSuccessFunction, this.getHostListFailFunction, "SmartHomePlugins",
          "getHostList", []);
      },
      setCurrentHostFailFunction: function () {
        console.log("设置当前主机失败")
      },
      close() {
        //关闭弹窗
        this.isConfirmDialogVisible = false
      },
      //跳转到主机设置页面
      clickToSettings: function () {
        this.$router.push(
          {
            path: '/settings'
          }
        );
      },
      //跳转局域网搜索页面
      clickToaddController_page: function () {
        this.$router.replace(
          {
            path: '/udpConfig',
            query: {
              fromPageName: this.fromPage,
            }
          }
        );
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>

  .hintContentList {
    font-size: 16px;
    color: black;
    opacity: 0.5;
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .contentList {
    margin-top: 64px;
    height: calc(100% - 64px);
    overflow: scroll;
    background-color: #f9f9f9;
  }

  .addControllerDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    bottom: 0;
    position: fixed;
    background-color: white;
  }

  .addButton {
    width: 40px;
    height: 40px;
  }

  .demo-paper {
    display: inline-block;
    text-align: center;
    position: fixed;
    left: 0;
    bottom: 0;
    font-size: medium;
    flex-direction: column;
    background-color: white;
    align-items: center;
    width: 100%;
    vertical-align: middle;
  }

  .demo-grid div[class*="col-"] {
    margin-bottom: 0px;
    padding: 0px;
    -webkit-overflow-scrolling: touch;
  }

  .text1 {
    margin: 0px;
    font-size: small;
  }

  .textstatus {
    margin: 0px;
    font-size: small;
    opacity: 0.5;

  }

  .textName {
    font-size: medium;
    margin-bottom: 5px;
  }

  .settingsImage {
    position: absolute;
    top: 15%;
    left: 13%;
    width: 22px;
    height: 22px;
  }

  .flexLeft {
    flex: 1;
    text-align: left;
    /*text-align:center;*/
    vertical-align: middle;
    justify-content: center;
    flex-direction: column;
    display: flex;
  }

  .flexRight {
    flex: 1;
    justify-content: center;
    flex-direction: column;
    display: flex;
  }

  .controller {
    position: relative;
    display: flex;
    flex-direction: row;
    background-color: white;
  }

  .controllerImage {
    width: 20%;
    height: 20%;
  }

  .checkImageHidden {
    /*margin-top: 13px;*/
    width: 20%;
    height: 40%;
    visibility: hidden;
    margin: 0 auto;
  }

  .checkImageVisible {
    width: 20%;
    height: 40%;
    visibility: visible;
    margin: 0 auto;
  }

  .controllerItem {
    background-color: white;
    width: 100%;
  }

  .no_fix_height_wapper {
    background-color: lightgrey;
  }

  .hint {
    font-size: small;
    opacity: 0.7;
    height: 20px;
    margin-bottom: 0px;
    margin-left: 20px;
    text-align: left;
  }

  html, body, wrapper {
    height: 100%;
    /*overflow:hidden;*/
  }
</style>



