<template>
  <div id="user_title">
    <div v-if="isImg" id="title_img" @click="back">
    </div>
    <div id="titile_text">
      <span>{{ titleName }}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'userTitle',
    data() {
      return {};
    },

    props: ['titleName', 'isImg'],

    methods: {
      back: function () {
        console.log("back");
        this.$router.go(-1);
      }
    }
  }
</script>

<style scoped>
  #user_title {
    position: fixed;
    line-height: 60px;
    width: 100%;
    height: 60px;
    align-items: center;
    background-color: white;
  }

  #titile_text {
    font-size: medium;
    font-weight: bolder;
    color: #333333;
    font-family: PingFangLight;
    width: auto;
  }

  @font-face {
    font-family: PingFangLight;
    src: url('../../../../static/font/PingFangLight.ttf');
  }

  #title_img {
    width: 40px;
    height: 40px;
    position: absolute;
    margin-top: 10px;
    margin-left: 20px;
    background-image: url("../../../../static/image/ic_back.png");
    background-size: 100%;
  }
</style>



