<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div style="width: 100%;height: 100%">
    <!--引入标题栏-->
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              :is-left-click="true"  @LeftBttonClick='back1'  @clickRightButton="clickSave" :src="srcImg" :DesWord="DesWord" :back-src="backSrc"></titleBar>

    <div class="wapperColumn">
      <mu-divider/>
      <div class="timer">

        <div >
          <!--<mu-picker :slots="addressSlots" :visible-item-count="3" @change="addressChange" :values="address"/>-->
          <timerSwiper :timer-data="timerData" @changeTimerData="changeTimerData"></timerSwiper>
        </div>
        <div class="deviderTimer">:</div>
      </div>

      <div class="weeks">
        <div class="testDiv" v-for="(week, index) in weeksText">
          <mu-checkbox name="group" :nativeValue="weeksText[index].name" v-model="list" :label='"周"+ week.name'
                       class="demo-checkbox" labelLeft/>
          <mu-divider/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  const weeksTextArray = [

    {name: "一", value: 0},
    {name: "二", value: 0},
    {name: "三", value: 0},
    {name: "四", value: 0},
    {name: "五", value: 0},
    {name: "六", value: 0},
    {name: "日", value: 0},]

  export default {
    data() {
      return {
        pageType:this.$route.query.pageType,
        //标题栏有关属性
        name: "场景定时",
        isSaveVisible: true,
        isTitleBarBackImageVisible: true,
        isImg: false,
        DesWord: "保存",
        backSrc: "static/image/close.png",
        srcImg: "static/image/add_icon1.png",
        //标题栏有关属性  the end

        sceneData: [],
        fromPage: "",
        //日期组件
        hour: "",
        mins: "",
        timerData: "",
        list: [],
      }
    },

    computed: {
      // 一个计算属性的 getter
      isHaveRepeat: function () {
        var num = 0;
        for (var s = 0; s < 7; s++) {
          if (this.sceneData.week[s].value == 0) {
            num++;
          }
        }

        if (num == 7) {
          return false;
        } else {
          return true;
        }
      },

      weeksText: {
        get() {
          var listForMap = this.list;
          var size = listForMap.length
          var num = 0;
          //console.log(" 数组size  " + size)

          weeksTextArray.map(function (weeksObj) {
            num = 0
            //遍历选中的数据
            for (var t = 0; t < size; t++) {
              //匹配到
              if (weeksObj.name == listForMap[t]) {
                //console.log(" 选中 " + weeksObj.name)
                weeksObj.value = 1;
                return
              }
              num++
            }
            //console.log(" 不在选中的数组中num " + num)
            //不在选中的数组中，则赋值为0
            if (num == size) {
              //console.log(" 不在选中的数组中" + weeksObj.name)
              weeksObj.value = 0
            }
          })
          //console.log("处理后的数组 " + JSON.stringify(weeksTextArray))
          return weeksTextArray
        },
        //进入页面赋值
        set(newArray) {
          var listForAdd = [];
          newArray.map(function(newArrayObj) {
            if (newArrayObj.value == 1) {
              listForAdd.push(newArrayObj.name)
            }
          })
          this.list = listForAdd;
          console.log("改变" + JSON.stringify(this.list))
        },
      },


    },

    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
    },

    created() {
      //获取传进来的场景数据
      var sceneDataTempt = this.$route.query.sceneData;
      var fromPageName = this.$route.query.fromPageName;

      if (typeof(sceneDataTempt) != "undefined" && typeof(fromPageName) != "undefined") {
        this.sceneData = sceneDataTempt;
        this.weeksText = this.sceneData.week;
        this.fromPage = fromPageName;
        //初始化时间控件
        if (this.sceneData.time == " " || this.sceneData.time == "") {
          console.log(" 初始化时间控件 空")
          this.hour = '00',
            this.mins = '00'
        } else {
          this.initTimerScroll();
        }
      } else {
        console.log("++++++++传入的场景数据为空++++++++")
      }
    },

    methods: {
      initTimerScroll: function () {
        this.timerData = this.sceneData.time
        console.log("++++++++传入的场景时间++++++++" + JSON.stringify(this.timerData))
      },
      //从子组件内传回的数据
      changeTimerData: function (timerData) {
        this.hour = timerData[0]
        this.mins = timerData[1]
      },

      clickSave: function () {
        this.sceneData.time = this.hour + ":" + this.mins;
        this.sceneData.week = this.weeksText;
        //获取到时间与重复
        console.log("++++++++修改时间后的场景数据++++++++" + JSON.stringify(this.sceneData))
        if (this.isHaveRepeat) {
          if (this.fromPage == "addScene") {
            console.log("addScene")
            this.$router.push({
              path: '/addScene',
              name: 'addScene',
              query: {
                sceneData: this.sceneData,
                pageType:this.pageType,
              }
            })
          } else if (this.fromPage == "editScene") {
            console.log("editScene")
            this.$router.push({
              path: '/editScene',
              name: 'editScene',
              query: {
                sceneData: this.sceneData,
                pageType:this.pageType,
              }
            })
          }
        } else {
          this.showNativeToast("请选择重复")
        }

      },
      back1: function () {

        if (this.fromPage == "addScene") {
          console.log("addScene")
          this.$router.push({
            path: '/addScene',
            name: 'addScene',
            query: {
              sceneData: this.sceneData,
              pageType: this.pageType,
            }
          })
        // } else if (this.fromPage == "editScene") {
        //   console.log("editScene")
        //   this.$router.push({
        //     path: '/editScene',
        //     name: 'editScene',
        //     query: {
        //       sceneData: this.sceneData
        //     }
        //   })
        }


      },

      //选择周一至周日
      weekSelected: function (index) {

        if (this.weeksText[index].value == 0) {
          this.weeksText[index].value = 1;
        } else {
          this.weeksText[index].value = 0;
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
  /************定义时间控件**********/
  .deviderTimer {
    position: absolute;
    font-size: 36px;
    top: 50%;
    left: 50%;
    margin-left: -5px;
    margin-top: -27px;
  }

  .demo-picker-container-in-scene-timer {
    /* height: 100%; */
  }

  .timer .mu-picker{
    height: 100%;
  }
  /************定义时间控件 the end**********/
  .testDiv {
    line-height: 50px;
    height: 50px;
    background-color: white;
    position: relative;
    margin: 2px;
  }

  .testDiv .mu-checkbox {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }

  .weekSelected {
    background-color: #4cc6ff;
  }

  .weeks {
    margin-top: 10px;
    background-color: white;
  }

  .mu-picker-item.selected {
    font-size: 45px;
  }

  .imageBack {
    visibility: visible;
    position: absolute;
    float: left;
    z-index: 999999;
    width: 40px;
    height: 40px;
    background-size: auto 40px;
    background-image: url(../../../../static/image/back_white_icon.png);
    margin-top: 10px;
  }

  .wapperColumn {
    background-color: #fbfbfb;
    flex-direction: column;
    display: flex;
    margin-top: 64px;
    height: calc(100% - 64px);
  }

  .save1 {
    visibility: visible;
    margin-top: 20px;
    margin-right: 20px;
    font-size: large;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 999999999;
  }

  .timer {
    flex: 1;
    /*width: 100%;*/
    display: flex;
    background-color: white;
    align-items: center;
    justify-content: center;
    /*vertical-align: middle;*/
    flex-direction: column;
    position: relative;
  }

  .setTimeHint {
    margin: 10px;
    font-size: larger;
    height: 20px;
    text-align: left;
    position: relative;
    margin-left: 5px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }
</style>



