<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div style="height: 100%;">
    <!--引入标题栏-->
    <div class="addDeviceListHeader1">
      <p v-on:click="saveList" class="saveList">完成</p>
      <img v-on:click="backEditScene" class='cancelImage' src="../../../../static/image/cancel.png"/>
    </div>

    <div class="wrapperDeviceList3">
      <mu-divider/>
      <div class="deviceItems1" v-for="(item,index) in list">

        <!-----deviceTypeImage 根据类型显示 ------>
        <img v-if="item.deviceTypeCode==0x7F01" class="deviceImage1" src="../../../../static/image/light_on_image.png"/>
        <img v-else-if="item.deviceTypeCode==0x7F08" class="deviceImage1"
             src="../../../../static/image/exhaust_Fan_open_icon.png"/>
        <img v-else-if="item.deviceTypeCode==0x000B" class="deviceImage1"
             src="../../../../static/image/lock_image.png"/>
        <img v-else-if="item.deviceTypeCode==0x0009 || item.deviceTypeCode==0x0013" class="deviceImage1"
             src="../../../../static/image/curtain_image.png"/>
        <img v-else-if="item.deviceTypeCode == 0x0005 || item.deviceTypeCode == 0x0006 || item.deviceTypeCode == 0x0011"
             class="deviceImage1" src="../../../../static/image/airConditioner.png">

        <div class="addMoreDeviceForSceneDeviceState2">
          <div class="deviceItemClass1">{{item.name}}</div>
          <mu-checkbox :value="item.isAdded" class="demo-checkbox" v-model="item.isAdded" labelLeft/>          
        </div>        
      </div>
      <!-- <mu-divider /> -->
    </div>

  </div>
</template>

<script>

  export default {
    data() {
      return {
        pageType: this.$route.query.pageType,
        list: [],
        sceneData: [],
        fromPage: "",
      }
    },

    computed: {
      // 一个计算属性的 getter
      addressString: function () {
        console.log("一个计算属性的 getter")
        // `this` 指向 vm 实例 this.sceneData
         var addressArray = [];
        for (var s = 0; s < this.sceneData.deviceData.length; s++) {
          addressArray.push(this.sceneData.deviceData[s].address);
        }
        return addressArray.toString()
      },
    },


    created() {
      //获取传进来的场景数据
      var sceneDataTempt = this.$route.query.sceneData;
      var fromPageName = this.$route.query.fromPageName;

      if (typeof(sceneDataTempt) != "undefined" && typeof(fromPageName) != "undefined") {
        this.sceneData = sceneDataTempt;
        this.fromPage = fromPageName;
        console.log("++++++++获取传进来的场景数据++++++++" + JSON.stringify(this.sceneData.deviceData))
      } else {
        console.log("++++++++传入的场景数据为空++++++++")
      }

      if (this.isDebug) {
        this.list = [
          //未添加的数据
          {
            "id": 75,
            "name": "K1_灯1",
            "hostId": 9,
            "region": 0,
            "deviceTypeCode": 32513,
            "address": "5_5",
            "devViewCode": 0,
//             "status": "{\n\t\"status\":1\n}",
            "status": "",
            "sceneIds": null,
            "isAdded": false,
          },
          {
            "id": 76,
            "name": "电动窗帘test",
            "hostId": 9,
            "region": 0,
            "deviceTypeCode": 9,
            "address": "6_6",
            "devViewCode": 0,
//             "status": "{\n\t\"status\":10\n}",
            "status": "",
            "sceneIds": null,
            "isAdded": false,
          },
          {
            "id": 77,
            "name": "双控电动窗帘test",
            "hostId": 9,
            "region": 0,
            "deviceTypeCode": 19,
            "address": "7_7",
            "devViewCode": 0,
//             "status": "{\n\t\"in\":50\n,\"out\":50}",
            "status": "",
            "sceneIds": null,
            "isAdded": false,
          },
          {
            "id": 78,
            "name": "空调test",
            "hostId": 9,
            "region": 0,
            "deviceTypeCode": 0x0011,
            "address": "7_7",
            "devViewCode": 0,
            //             "status": "{\n\t\"in\":50\n,\"out\":50}",
            "status": "{\n\t\"indoorTem\":25,\n\t\"mode\":3,\n\t\"sleep\":0,\n\t\"status\":0,\n\t\"swingLR\":0,\n\t\"swingUD\":1,\n\t\"tem\":27,\n\t\"wind\":1\n}",
            "sceneIds": null,
            "isAdded": false,
          },
        ],
          console.log("++++++++查询设备++++++++" + " addressString " + this.addressString)
      } else {
        console.log("++++++++查询设备++++++++" + " addressString " + this.addressString)
        Cordova.exec((data) => {
            var obj = eval("(" + data + ")");
            obj.sort((m,n)=>{
              if(m.deviceTypeCode > n.deviceTypeCode){
                return -1;
              }else if(m.deviceTypeCode == n.deviceTypeCode){
                if(m.id < n.id){
                  return -1;
                }else{
                  return 1;
                }
              }else{
                 return 1;
              }            
            });
            this.list = obj;
            console.log("++++++++获取所有设备成功++++++++" + JSON.stringify(this.list))
          }, (data) => {
            console.log("++++++++获取所有设备失败++++++++");
          }, "SmartHomePlugins",
          "getUserDeviceListFromCache", [this.addressString]);
      }

    },
    methods: {
      backEditScene: function () {
        if (this.fromPage == "addScene") {
          console.log("addScene")
          this.$router.push({
            path: '/addScene',
            name: 'addScene',
            query: {
              sceneDataFromDeviceList: this.sceneData,
              pageType: this.pageType,
            }
          })
        }

      },

      saveList: function () {
        let tempDeviceData = this.sceneData.deviceData;
        this.sceneData.deviceData = [];
        var hasAdd = false;
        for (var k = 0; k < this.list.length; k++) {
          console.log(" isAdded " + this.list[k].isAdded);
          
          if (this.list[k].isAdded == true) {
            hasAdd = false;
            for(var j = 0;j<tempDeviceData.length;j++){
            if(tempDeviceData[j].address == this.list[k].address){
              this.sceneData.deviceData.push(tempDeviceData[j]);
              hasAdd = true;
              break;
            }
            }
            if(hasAdd){
             continue;
            }

            console.log("设备是" + this.list[k].deviceTypeCode);
            var obj = {};
            var statusArray = [];
            var statusString = {};
            var innnerStatusString = {};
            var outterStatusString = {};

            //灯
            if (this.list[k].deviceTypeCode == 32513 || this.list[k].deviceTypeCode == 32520) {
              //拼接设备状态
              statusString.code = "status";
              // if (this.list[k].status == "") {
                console.log(this.list[k].name + "  ++++++++状态空+++++++++")
                statusString.value = "1"
                statusString.description = "打开"
              // } else {
              //   var temp = eval("(" + this.list[k].status + ")");
              //   if (temp.status == 0) {
              //     statusString.value = 0;
              //     statusString.description = "关闭"
              //   } else {
              //     statusString.value = 1;
              //     statusString.description = "打开"
              //   }
              // }
              statusArray[0] = statusString
              //单控
            } else if (this.list[k].deviceTypeCode == 9) {

              //拼接设备状态
              statusString.code = "status";
              // if (this.list[k].status == "") {
                statusString.value = "0";
                statusString.description = "请添加窗帘状态";
              // } else {
              //   var temp = eval("(" + this.list[k].status + ")");
              //   statusString.value = temp.status;
              //   statusString.description = "打开" + statusString.value + "%"
              // }
              statusArray[0] = statusString

              //双控
            } else if (this.list[k].deviceTypeCode == 19) {
              // if (this.list[k].status == "") {
                //内帘
                innnerStatusString.code = "in";
                innnerStatusString.value = "0";
                innnerStatusString.description = "请添加内帘状态";
                statusArray[0] = innnerStatusString;
                //外帘
                outterStatusString.code = "out";
                outterStatusString.value = "0";
                outterStatusString.description = "请添加外帘状态";
                statusArray[1] = outterStatusString;
              // } else {
              //   //拼接设备状态
              //   let temp = eval("(" + this.list[k].status + ")");
              //   //内帘
              //   innnerStatusString.code = "in";
              //   innnerStatusString.value = temp.in;
              //   innnerStatusString.description = "内帘" + temp.in + "%";
              //   statusArray[0] = innnerStatusString;
              //   //外帘
              //   outterStatusString.code = "out";
              //   outterStatusString.value = temp.out;
              //   outterStatusString.description = "外帘" + temp.out + "%";
              //   statusArray[1] = outterStatusString;
              // }
              //  空调
            } else if (this.list[k].deviceTypeCode == 0x0005 || this.list[k].deviceTypeCode == 0x0006 || this.list[k].deviceTypeCode == 0x0011) {
              // if (this.list[k].status == "") {
                var status0 = {};
                status0.code = "status";
                status0.value = "0";
                status0.description = "请修改空调状态";


                // var status1 = {};
                // status1.code = "tem";
                // status1.value = "16";
                // status1.description = "请设置空调温度";

                // var status2 = {};
                // status2.code = "mode";
                // status2.value = "0";
                // status2.description = "请设置空调模式";

                statusArray[0] = status0;
                // statusArray[1] = status1;
                // statusArray[2] = status2;
              // } else {
              //   let temp = eval("(" + this.list[k].status + ")");
              //
              //   var status0 = {};
              //   status0.code = "status";
              //   status0.value = temp.status;
              //   status0.description = "状态：" + (temp.status == 1 ? "开" : "关");
              //   statusArray[0] = status0;
              //
              //   var status1 = {};
              //   status1.code = "tem";
              //   status1.value = temp.tem;
              //   status1.description = ",温度：" + temp.tem;
              //
              //   var status2 = {};
              //   status2.code = "mode";
              //   status2.value = temp.mode;
              //   status2.description = ",模式：" + this.getModeName(temp.mode);
              //
              //   statusArray[0] = status0;
              //   statusArray[1] = status1;
              //   statusArray[2] = status2;
              // }
              //  statusArray
              //  添加新场景操作接口
              //  空调状态
              // statusArray[0] = statusString;
              // console.log("回传信息去" + statusString);
            }

            console.log("======befor obj:" + JSON.stringify(obj));
            obj.status = statusArray;
            obj.hostId = this.list[k].hostId
            obj.address = this.list[k].address
            obj.deviceTypeCode = this.list[k].deviceTypeCode
            obj.deviceName = this.list[k].name
            this.sceneData.deviceData.push(obj)
            console.log("======after obj:" + JSON.stringify(obj));
          }
        }

        // console.log("======"+JSON.stringify(this.sceneData));
        console.log("======");
        if (this.fromPage == "addScene") {
          console.log("addScene" + JSON.stringify(this.sceneData))
          this.$router.push({
            path: '/addScene',
            name: 'addScene',
            query: {
              sceneDataFromDeviceList: this.sceneData,
              pageType: this.pageType,
            }
          })
          // } else if (this.fromPage == "editScene") {
          //   console.log("editScene" + JSON.stringify(this.sceneData))
          //   this.$router.push({
          //     path: '/editScene',
          //     name: 'editScene',
          //     query: {
          //       sceneDataFromDeviceList: this.sceneData
          //     }
          //   })
        } else {
          console.log("其他")
        }
      },


      changeCheckbox: function (item, value) {
        console.log(JSON.stringify(this.sceneList) + "值" + value)
      },
      getModeName: function (mode) {
        var name = "制冷";
        switch (mode) {
          case 1:
            name = "制热";
            break;
          case 2:
            name = "除湿";
            break;
          case 3:
            name = "送风";
            break;
          case 4:
            name = "自动";
            break;
        }
        return name;
      }
    }
  }


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>

  .deviceItems1 {
    display: flex;
    flex-direction: row;
    height: 100px;
    align-items: center;
    vertical-align: middle;
    width: 100%;
  }

  .mu-checkbox {
    margin-right: 20px;
  }

  .addMoreDeviceForSceneDeviceState2 .mu-divider {
    position: absolute;
    bottom: 0px;
  }

  .deviceItemClass1 {
    font-size: 14px;
    color: #333333;
    width: 100%;
  }

  .deviceImage1 {
    float: left;
    width: 40px;
    height: 40px;
    margin-left: 20px;
    margin-right: 20px;
  }

  .addMoreDeviceForSceneDeviceState2 {
    flex: 1;
    text-align: left;
    height: 100%;
    position: relative;
    display: flex;
    vertical-align: middle;
    align-items: center;
    flex-direction: row;

  }

  .cancelImage {
    float: left;
    margin-left: 20px;
    width: 24px;
    height: 24px;
    margin-top: 20px;
  }

  .saveList {
    float: right;
    margin-right: 20px;
    font-size: large;
    margin-top: 0px;
  }

  .addDeviceListHeader1 {
    position: fixed;
    z-index: 99999999;
    width: 100%;
    height: 64px;
    line-height: 64px;
    background-color: white;
  }

  .wrapperDeviceList3 {
    margin-top: 64px;
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  @font-face {
    /*font-family: PingFangLight;*/
    /*src: url('../../../static/font/PingFangLight.ttf');*/
    font-family: PingFangMedium;
    src: url('../../../../static/font/PingFangMedium.ttf');
  }

</style>



