const state = {
  // initial state
  needCheckBleVerfyState: true,
  lockStatus: "0",
}


// getters
const getters = {}

// actions
const actions = {
  setNeedCheckBleVerfyState(state, msg) {
    state.needCheckBleVerfyState = msg;
    console.log("state.needCheckBleVerfyState1=" + state.needCheckBleVerfyState);
  }

}

// mutations
const mutations = {
  setNeedCheckBleVerfyState(state, msg) {
    state.needCheckBleVerfyState = msg;
    console.log("state.needCheckBleVerfyState=2" + state.needCheckBleVerfyState);
  },
  setLockStatus(state, msg) {
    state.lockStatus = msg;
    console.log("  mutations state "+ state.lockStatus)
  }
}


export default {
  state,
  getters,
  actions,
  mutations
}



