import Vue from 'vue'
import Vuex from 'vuex'
import homePageDeviceList from './modules/homePageDeviceList'
import myControllerList from './modules/myControllerList'
import myRegionList from './modules/myRegionList'
import calendar from './modules/calendar'
import blueTooth from './modules/blueTooth'

Vue.use(Vuex)

//应用层级别的状态从组件中抽离
export default new Vuex.Store({
  modules: {
    homePageDeviceList,  //主页面设备列表状态模块
    myControllerList,   //主机列表状态模块
    myRegionList,       //区域列表状态模块
    calendar,            //年月日日期组件
    blueTooth,        //蓝牙页面使用到的参数
  },
  //strict: debug,
})



