<template xmlns:v-bind="http://www.w3.org/1999/xhtml" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <mu-paper :zDepth="1" class="deviceMargin" :rounded="true">

    <!--设备未知状态-->
    <div v-if="obj.status==999" class="swith_on" v-on:click="unknowStatus">
      <div class="deviceStateImageDiv">
        <!-- 蓝牙锁 -->
        <img v-if="deviceData.deviceTypeCode== 0x000B" class="devicestatus" src="../../../static/image/lock_error.png"/>
        <!-- 空调 -->
        <img
          v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011"
          class="devicestatus" src="../../../static/image/airConditionerUnLive.png">
        <!-- 窗帘 -->
        <img v-else-if="deviceData.deviceTypeCode== 0x0013 ||deviceData.deviceTypeCode== 0x0009 " class="devicestatus"
             src="../../../static/image/curtain_error.png"/>
        <img class="rightErrorImage" src="../../../static/image/errorImage.png"/>
      </div>
      <p class="device_Name">{{deviceData.name}}</p>
      <!--<p class="device_status_error_txt">离线</p>-->
      <!-- 空调状态显示 -->
      <!--<div class="bootRegion1"-->
           <!--v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011 ">-->
        <!--<div class="iconAc">-->
          <!--<img class="iconAc" src="../../../static/image/air_del_unknow.png"/>-->
        <!--</div>-->
        <!--<img class="acBootImage" src="../../../static/image/power_offline.png"/>-->
        <!--<div class="iconAc">-->
          <!--<img class="iconAc" src="../../../static/image/air_add_unknow.png"/>-->
        <!--</div>-->
      <!--</div>-->
    </div>

    <!--设备离线-->
    <div v-else-if="obj.status== -1" class="swith_on" v-on:click="offLine">
      <div class="deviceStateImageDiv">
        <img v-if="deviceData.deviceTypeCode== 0x000B" class="devicestatus" src="../../../static/image/lock_error.png"/>
        <!-- 空调 -->
        <img
          v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011 "
          class="devicestatus" src="../../../static/image/airConditionerOff.png">
        <img v-else-if="deviceData.deviceTypeCode== 0x0013 ||deviceData.deviceTypeCode== 0x0009 " class="devicestatus"
             src="../../../static/image/curtain_error.png"/>
        <img class="rightErrorImage" src="../../../static/image/errorImage.png"/>
      </div>
      <p class="device_Name">{{deviceData.name}}</p>
      <!--<p class="device_status_error_txt">离线</p>-->

      <!-- 空调状态显示 -->
      <!--<div class="bootRegion1"-->
           <!--v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011 ">-->
        <!--<div class="iconAc">-->
          <!--<img class="iconAc" src="../../../static/image/air_del_unknow.png"/>-->
        <!--</div>-->
        <!--<img class="acBootImage" src="../../../static/image/power_offline.png"/>-->
        <!--<div class="iconAc">-->
          <!--<img class="iconAc" src="../../../static/image/air_add_unknow.png"/>-->
        <!--</div>-->
      <!--</div>-->
    </div>

    <!--设备显示-->
    <div v-else v-on:click="clickToDevicePage" class="swith_on">
      <div class="deviceStateImageDiv">
        <img v-if="deviceData.deviceTypeCode== 0x000B" class="devicestatus" src="../../../static/image/lock_image.png"/>
        <!-- 空调 -->
        <img
          v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011"
          class="devicestatus" src="../../../static/image/airConditionerOn.png">
        <img v-else-if="deviceData.deviceTypeCode== 0x0013 ||deviceData.deviceTypeCode== 0x0009 " class="devicestatus"
             src="../../../static/image/curtain_image.png"/>
      </div>

      <p class="device_Name">{{deviceData.name}}</p>
      <p v-if="deviceData.deviceTypeCode== 0x0013" class="device_status">内帘{{obj.in}}%
        <span class="fenge2"></span>
        外帘{{obj.out}}%
      </p>

      <!--<p v-else-if="deviceData.deviceTypeCode== 0x0009 && (obj.status!=100 )&& (deviceData.status.status!=0 )"-->
         <!--class="device_status">{{obj.status}}%</p>-->
      <!--<p v-else-if="deviceData.deviceTypeCode== 0x0009 && (obj.status==100 ) " class="device_status">窗帘全开</p>-->
      <!--<p v-else-if="deviceData.deviceTypeCode== 0x0009 && (obj.status==0 )" class="device_status">窗帘全关</p>-->
      <!--<p v-else-if="deviceData.deviceTypeCode== 0x000B && (obj.status==0 )" class="device_status">关锁</p>-->
      <!--<p v-else-if="deviceData.deviceTypeCode== 0x000B && (obj.status==1 )" class="device_status">开锁</p>-->
      <!--<p v-else-if="deviceData.deviceTypeCode== 0x000B && (obj.status==2 )" class="device_status">反锁</p>-->
      <!--&lt;!&ndash; 空调状态显示 &ndash;&gt;-->
      <!--<p-->
        <!--v-else-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011 "-->
        <!--class="device_status">{{airStatusTip}}</p>-->

      <!--<div class="bootRegion1"-->
           <!--v-if="deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011 ">-->
        <!--<div v-on:click.stop="subtractTem" class="iconAc">-->
          <!--<img class="iconAc" :src="airDelImg"/>-->
        <!--</div>-->
        <!--<img v-on:click.stop="clickToChangestatus" class="acBootImage" :src="airStatusImg"/>-->
        <!--<div v-on:click.stop="addTem" class="iconAc">-->
          <!--<img class="iconAc" :src="airAddImg"/>-->
        <!--</div>-->
      <!--</div>-->
    </div>

  </mu-paper>
</template>

<script>

  export default {
    data() {
      return {}
    },
    computed: {
      airStatusTip: function () {
        if (this.obj.status == 0) {
          return "关机"
        }
        var string = "自动";
        switch (this.obj.mode) {
          case 0:
            if (this.obj.tem > 32) {
              string = "未知";
            } else {
              string = "制冷 " + (this.obj.tem + "°C");
            }
            break;
          case 1:
            if (this.obj.tem > 32) {
              string = "未知";
            } else {
              string = "制热 " + (this.obj.tem + "°C");
            }
            break;
          case 2:
            string = "除湿";
            break;
          case 3:
            string = "送风";
            break;
          case 4:
            string = "自动";
            break;
        }
        return string;
      },
      airStatusImg: function () {
        if (this.obj.status == 1) {
          return require("../../../static/image/power.png");
        } else {
          return require("../../../static/image/power_off.png");
        }
      },
      airDelImg: function () {
        if (this.obj.status == 1 && (this.obj.mode == 0 || this.obj.mode == 1)) {
          return require("../../../static/image/air_del_normal.png");
        } else {
          return require("../../../static/image/air_del_unknow.png");
        }
      },
      airAddImg: function () {
        if (this.obj.status == 1 && (this.obj.mode == 0 || this.obj.mode == 1)) {
          return require("../../../static/image/air_add_normal.png")
        } else {
          return require("../../../static/image/air_add_unknow.png")
        }
      },

      // 计算属性的 getter
      obj: function () {
        var temp = {};
        if (this.deviceData.status == "" || this.deviceData.status == " ") {
          this.deviceData.status = "{\n\t\"status\":999\n}";

        }

        else if (this.deviceData.deviceTypeCode == 0x0005 || this.deviceData.deviceTypeCode == 0x0006 || this.deviceData.deviceTypeCode == 0x0011) {
          let status = eval("(" + this.deviceData.status + ")");
          if (status.status == 56 || status.mode == 56 || status.status == -1 || status.mode == -1) {//空调状态为56时，为未知状态
            this.deviceData.status = "{\n\t\"status\":999\n}";
          }
        }

        else if (this.deviceData.deviceTypeCode == 0x0013) {
          let status = eval("(" + this.deviceData.status + ")");
          // console.log(this.deviceData.status)
          if (status.in == -1) {//窗帘内帘状态为-1时，改为离线状态
            this.deviceData.status = "{\n\t\"status\":-1\n}";
          }
        }
        temp = eval("(" + this.deviceData.status + ")");
        //this.deviceData.status = temp;
        return temp;
      }
    },
    //需传入的 设备名称，开关机状态
    props: ["deviceData"],

    methods: {
      subtractTem: function () {
        if (this.obj.status != 1 || (this.obj.mode != 0 && this.obj.mode != 1)) {
          return;
        }
        var temp = this.obj.tem - 1;
        if (temp <= 16) {
          temp = 16;
        }
        this.operate((data) => {
          this.showNativeToast("操作成功");
        }, (data) => {
          this.showNativeToast(data);
        }, temp, -1, -1, -1, -1, -1, -1);
      },
      addTem: function () {
        if (this.obj.status != 1 || (this.obj.mode != 0 && this.obj.mode != 1)) {
          return;
        }
        var temp = this.obj.tem + 1;
        if (temp >= 32) {
          temp = 32;
        }
        this.operate((data) => {
          this.showNativeToast("操作成功");
        }, (data) => {
          this.showNativeToast(data);
        }, temp, -1, -1, -1, -1, -1, -1);
      },
      //开关机点击事件
      clickToChangestatus: function () {
        this.operate((data) => {
          this.showNativeToast("操作成功");
        }, (data) => {
          this.showNativeToast(data);
        }, -1, this.obj.status == 1 ? 0 : 1, -1, -1, -1, -1, -1);
      },
      operate(funcSuccess, funcFailed, tem, status, mode, wind, swingLR, swingUDB, sleep) {
        console.log("tem:" + tem + " status:" + status + " mode:" + mode + " wind:" + wind + " swingLR:" + swingLR + " swingUDB:" + swingUDB + " sleep:" + sleep);
        if (this.isDebug) {
          // funcSuccess("dn");
        } else {
          Cordova.exec(funcSuccess, funcFailed, "SmartHomePlugins",
            "operateAirConditioner", [this.deviceData.address, tem, status, mode, wind, swingLR, swingUDB, sleep]);
        }

      },

      //跳转到设备点击事件
      clickToDevicePage: function (device) {
        if (this.deviceData.deviceTypeCode == 0x000B) {
          this.$router.push(
            {
              path: '/blueToothHome',
              name: "blueToothHome",
              query: {titleName: this.deviceData.name, devAddress: this.deviceData.address, status: this.obj}
            }
          );
        } else if (this.deviceData.deviceTypeCode == 0x0005 || this.deviceData.deviceTypeCode == 0x0006 || this.deviceData.deviceTypeCode == 0x0011) {
          this.$router.push({
            path: '/airConditionerPage',
            name: "airConditionerPage",
            query: {titleName: this.deviceData.name, devAddress: this.deviceData.address, status: this.obj, deviceTypeCode:this.deviceData.deviceTypeCode},
          })
        } else {
          this.$emit('changeCurtainValue', this.deviceData);
        }

      },
      offLine: function () {
        this.showNativeToast("设备离线")
      },
      unknowStatus: function () {
        this.showNativeToast("设备未知状态")
      },

    },
//    created(){
//      //console.log("+++名称++++"+ this.deviceData.name)
//
//      if(this.deviceData.status==null ||this.deviceData.status==" "){
//        //显示未知状态
//        console.log("状态空");
//      }else {
//        console.log(" 状态"+ JSON.stringify(eval("("+this.deviceData.status+")")))
//        this.obj=eval("("+this.deviceData.status+")");
//      }
//
//    },


  }
</script>

<style>
  .iconAc {
    width: 24px;
    height: 24px;
    margin: auto;
    flex: 1;
  }

  .rightErrorImage {
    position: absolute;
    top: 0;
    right: 0;
    width: 20px;
    height: 20px;
    margin-top: 10px;
    margin-right: -5px;
  }

  .deviceStateImageDiv {
    position: relative
  }

  .fenge2 {
    margin-left: 6px;
    padding: 12px 6px 0px 6px;
    border-left: 1px solid lightgrey;
    font-size: 0;
  }

  .devicestatus {
    margin-top: 20px;
    width: 40px;
    height: 40px;
    background-size: auto 40px;
  }

  .bootImage {
    width: 24px;
    height: 24px;
  }

  .acBootImage {
    width: 24px;
    height: 24px;
    margin: auto;
  }

  .bootRegion1 {
    visibility: visible;
    flex: 1;
    display: flex;
    flex-direction: row;
    vertical-align: middle;
    width: 100%;
  }

  .device_Name {
    margin: 0%;
    padding-top: 2%;
    font-size: large;
    font-family: PingFangBold;
  }

  .device_status {
    font-size: small;
    margin: 0%;
    padding-bottom: 2%;
    opacity: 0.5;
  }

  .device_status_error_txt {
    font-size: small;
    margin: 0%;
    padding-bottom: 2%;
    opacity: 0.5;
    color: red;
  }

  .swith_on {
    flex-direction: column;
    display: flex;
    position: relative;
    height: 170px;
    background-color: white;
    vertical-align: middle;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
  }

  .mu-paper-round {
    border-radius: 5px;
  }

  @font-face {
    font-family: PingFangBold;
    src: url('../../../static/font/PingFangBold.ttf');
  }

  .deviceMargin {
    margin: 5px;
  }
</style>



