<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div style="height: 100%">
    <!--蓝色背景区域-->
    <div class="scenePageHeader" :style="scenePageBg">
      <!--区域名称-->
      <div class="sceneDiv">
        <p class="sceneText">场景</p>
        <div class='addDiv' v-on:click="addScene">
          <img v-bind:class="{msgHidden: isSceneDataNull}" class="addNewSceneImage"
               src="../../../static/image/addNewScene.png"/>
        </div>
      </div>
    </div>
    <mu-divider/>
    <!--场景列表-->

    <div class="sceneListWrapper" ref="viewBox" id="sceneListWrapper">
      <div id="mescroll" class="mescroll">
      <!-- <mu-refresh-control :refreshing="refreshing" :trigger="trigger" @refresh="refresh"/> -->
      <!-- <mt-loadmore :top-method="refresh" :bottom-all-loaded="true" ref="loadmore"
                   @top-status-change="handleTopChange"> -->
        <swiper-layout-box class="my-layout">
          <swiper-layout
            :key="index" type="right" menuHeight="120px" :menuWidth="'40%' || '100%'"
            v-model="target.open" v-for="(target, index) in sceneList">

            <template slot="content">
              <div @touchstart="changeClose(target, index)" v-on:click="clickItem(target, index)">
                <sceneCardView class="content" :scene-data="target"></sceneCardView>
              </div>
            </template>
            <template slot="menu">
              <div class="menu-item" @click="editItem(target, index)">编辑</div>
              <div class="menu-item" @click="deleteItem(target, index)">删除</div>
            </template>
          </swiper-layout>
        </swiper-layout-box>

       

      <!-- <p class="bottomLine" v-bind:class="{bottomLineVisible:isScroll}"> -->
        <!-- ---------------------我是有底线的---------------------</p> -->
        <!-- <div slot="top" class="mint-loadmore-top">
          <span v-show="refreshing"><fading-circle/></span>
        </div> -->

        <div class="scenePopupBg" v-show="isSceneDataNull">
         <p class="TextHint2">通过在场景中配置设备与命令,可实现一键控制多个设备处于其配置状态。或者在场景中配置定时，实现在某个时间执行某一场景。</p>
         <p class="addSceneText" v-on:click.stop="addScene">添加场景</p>
        </div>
      </div>
      <!-- </mt-loadmore> -->
    </div>

    <!--无场景显示“添加场景”弹窗-->
    <!-- <mu-popup position="top" :overlay="false" popupClass="scene-popup-top1" :open="isSceneDataNull">
      <div class="scenePopupBg">
        <p class="TextHint2">通过在场景中配置设备与命令,可实现一键控制多个设备处于其配置状态。或者在场景中配置定时，实现在某个时间执行某一场景。</p>
        <p class="addSceneText" v-on:click.stop="addScene">添加场景</p>
      </div>
    </mu-popup> -->

    <!--Tab键-->
    <div class="bottomNav">
      <mu-divider/>
      <bottomNav v-bind:bottom-index="bottomNavIndex"></bottomNav>
    </div>

  </div>

</template>


<script>
  import MeScroll from '../../mescroll/mescroll.m'
  import '../../mescroll/mescroll.min.css'

  export default {
    data() {
      return {
        name: '',
        bottomNavIndex: '场景',
        //主机
        isNewUser: false,
        //场景
        isSceneDataNull: false,
        //用户绑定主机数
        host: "",
        //场景数据
        sceneList: [],
        isShowDelete: false,
        //滑动配置
        sliderConf: {
          distance: 3.4,
        },
        //下拉刷新组件
        refreshing: false,
        trigger: null,
        //toast: false
        //是否在滚动
        isScroll: false,
        scenePageBg: {
          backgroundImage: 'url(' + require('../../../static/image/scenePageBg.png') + ')',
        },
        mescroll: {},
        isFirst:true,
      }
    },

    computed: {
//      sceneListAfter:function () {
//
//        return this.sceneList.map((obj)=>{
//          //滑块需要的数据
//          var temp = obj;
//          temp.open = false;
//          return temp;
//        });
//      }
    },
    created() {
      if (this.isDebug) {
        this.sceneList = [
          {
            "name": "测试1",
            "hostSceneId": "15",
            "id": 66,
            "weeks": "1,2,5",
            "strideHost": 0,
            "week": "[" +
            "{\"name\":\"一\",\"value\":\"0\"}," +
            "{\"name\":\"二\",\"value\":\"0\"}," +
            "{\"name\":\"三\",\"value\":\"0\"}," +
            "{\"name\":\"四\",\"value\":\"1\"}," +
            "{\"name\":\"五\",\"value\":\"1\"}," +
            "{\"name\":\"六\",\"value\":\"0\"}," +
            "{\"name\":\"日\",\"value\":\"0\"}" +
            "]",
            "imageUrl": "www.cwerfwef23.com",
            "time": "07:55",
            "timer": 1,
            "hostIds": "9",
            "open": false,
          },
          {
            "name": "测试2",
            "hostSceneId": "15",
            "id": 66,
            "weeks": "1,2,5",
            "strideHost": 0,
            "week": "[" +
            "{\"name\":\"一\",\"value\":\"1\"}," +
            "{\"name\":\"二\",\"value\":\"1\"}," +
            "{\"name\":\"三\",\"value\":\"1\"}," +
            "{\"name\":\"四\",\"value\":\"1\"}," +
            "{\"name\":\"五\",\"value\":\"1\"}," +
            "{\"name\":\"六\",\"value\":\"1\"}," +
            "{\"name\":\"日\",\"value\":\"1\"}" +
            "]",
            "imageUrl": "www.cwerfwef23.com",
            "time": "07:55",
            "timer": 1,
            "hostIds": "9",
            "open": false,
          },
          {
            "name": "测试3",
            "hostSceneId": "15",
            "id": 66,
            "weeks": "1,2,5",
            "strideHost": 0,
            "week": "[" +
            "{\"name\":\"一\",\"value\":\"0\"}," +
            "{\"name\":\"二\",\"value\":\"0\"}," +
            "{\"name\":\"三\",\"value\":\"0\"}," +
            "{\"name\":\"四\",\"value\":\"0\"}," +
            "{\"name\":\"五\",\"value\":\"0\"}," +
            "{\"name\":\"六\",\"value\":\"0\"}," +
            "{\"name\":\"日\",\"value\":\"0\"}" +
            "]",
            "imageUrl": "www.cwerfwef23.com",
            "time": "07:55",
            "timer": 1,
            "hostIds": "9",
            "open": false,
          },
        ]
        //this.sceneList = [];
        if (this.sceneList == "" || this.sceneList == null) {

          setTimeout(() => {
            //显示“添加当前主机”
            this.isSceneDataNull = true;
          }, 2000)
        } else {
          this.isSceneDataNull = false;
        }
      }
      else {
        Cordova.exec(this.getSceneSuccess, this.getSceneFail, "SmartHomePlugins",
          "getSceneList", ["getSceneSuccessFromDb"]);

      }
    },
    mounted() {
      window.getSceneSuccessFromDb = this.getSceneSuccessFromDb;
      this.trigger = this.$el

      this.$nextTick(() => {
        var vm = this;
        this.mescroll = new MeScroll("mescroll", {//第一个参数"mescroll"对应上面布局结构div的id
          down: {
            callback: vm.refresh, //下拉刷新的回调,别写成downCallback(),多了括号就自动执行方法了
          },
          // up: {
          // callback: this.refresh() //上拉加载回调,简写callback:function(page){upCallback(page);}
          // }
        });
      })

      // 首先通过$refs获取dom元素
      this.box = this.$refs.viewBox
      // 监听这个dom的scroll事件
      this.box.addEventListener('scroll', () => {
        console.log(" scroll " + this.$refs.viewBox.scrollTop)
        this.isScroll = this.$refs.viewBox.scrollTop > 0
      }, false)
    },


    methods: {
      loadmore() {
        console.log("loadmore");
      },
      handleTopChange(status) {
        this.topStatus = status;
      },
      //滑块
      changeClose(target, index) {
        for (var i = 0; i < this.sceneList.length; i++) {
          if (index != i) {
            this.sceneList[i].open = false;

          }
        }
      },
      //下拉刷新
      refresh() {
        if(this.isFirst){
          this.isFirst = false;
          return;
        }
        console.log("刷新 +++++++")
        this.refreshing = true
        if (this.isDebug) {
          this.sceneList = [
            {
              "name": "刷新",
              "hostSceneId": "15",
              "id": 66,
              "weeks": "1,2,5",
              "strideHost": 0,
              "week": "[" +
              "{\"name\":\"一\",\"value\":\"0\"}," +
              "{\"name\":\"二\",\"value\":\"0\"}," +
              "{\"name\":\"三\",\"value\":\"0\"}," +
              "{\"name\":\"四\",\"value\":\"0\"}," +
              "{\"name\":\"五\",\"value\":\"1\"}," +
              "{\"name\":\"六\",\"value\":\"0\"}," +
              "{\"name\":\"日\",\"value\":\"0\"}" +
              "]",
              "imageUrl": "www.cwerfwef23.com",
              "time": "07:55",
              "timer": 1,
              "hostIds": "9"
            }]
          setTimeout(() => {
            // this.$refs.loadmore.onTopLoaded();
            this.refreshing = false
            this.mescroll.endSuccess();//无参
          }, 2000)
        } else {
          //调用自定义的验证插件
          Cordova.exec(this.getSceneSuccess, this.getSceneFail, "SmartHomePlugins",
            "getSceneList", []);
        }
      },

      getCurrentHostSuccess: function (message) {
        var obj = eval("(" + message + ")");
        console.log("++++++++获取当前主机成功++++++++" + JSON.stringify(obj))
      },

      getCurrentHostFail: function () {
        console.log("++++++++获取当前主机失败++++++++")
      },

      getSceneSuccess: function (message) {
        if(this.refreshing){
          this.showNativeToast("刷新成功");
          this.refreshing = false;
          this.mescroll.endSuccess();//无参
          // this.$refs.loadmore.onTopLoaded();
        }
        
        console.log("++++++++获取场景数据++++++++1" + message)
        var obj = eval("(" + message + ")");
        console.log("++++++++获取场景数据++++++++2" + JSON.stringify(obj))
        if (obj == null || obj == "") {
          //显示添加场景弹窗
          console.log("++++++++场景数据为空++++++++")
          // setTimeout(() => {
            //显示“添加当前主机”
            this.sceneList = [];
            this.isSceneDataNull = true;
          // }, 2000)
        } else {
          console.log("++++++++场景数据不为空++++++++")
          obj.sort((m,n)=>{
            if(m.id > n.id){
              return 1;
            }else{
              return -1;
            }
          });
          this.sceneList = obj;
          this.isSceneDataNull = false;
        }
      },
      getSceneSuccessFromDb: function (message) {        
        var obj = message;
        console.log("++++++++getSceneSuccessFromDb++++++++" + JSON.stringify(obj))
        if (obj == null || obj == "") {
          
        } else {
          console.log("++++++++场景数据不为空++++++++");
          obj.sort((m,n)=>{
            if(m.id > n.id){
              return 1;
            }else{
              return -1;
            }
          });
          this.sceneList = obj;
          this.isSceneDataNull = false;
        }
      },
      getSceneFail: function (msg) {
        if(this.refreshing){          
          this.refreshing = false;
          this.mescroll.endSuccess();//无参
          // this.$refs.loadmore.onTopLoaded();

        }        
        this.showNativeToast(msg);
        // this.sceneList = [];
      },

      addController: function () {
        this.isNewUser = false;
        this.$router.push({
          path: '/udpConfig',
          name: 'udpConfig',
        })
      },

      addScene: function () {
        this.isSceneDataNull = false;
        this.$router.push({
          path: '/addScene',
          name: 'addScene',
          query: {
            pageType: 0,//添加场景
            number:this.sceneList.length,
          }
        })
      },

      clickItem: function (target, index) {
        console.log("++++++++执行场景++++++++index" + target.id)
        Cordova.exec(this.executeSceneSuccess, this.executeSceneFail, "SmartHomePlugins", "executeScene", [target.id]);
      },

      deleteItem: function (target, index) {
        if (this.isDebug) {
          console.log("删除第几个" + index)
          this.sceneList.splice(index, 1);
          console.log(JSON.stringify(this.sceneList))
        } else {
          console.log("++++++++删除场景++++++++ index" + target.id)
//          this.sceneList.splice(index[1], 1);
          Cordova.exec(this.deleteSuccess, this.deleteFail, "SmartHomePlugins", "deleteScene", [target.id]);
        }
      },
      editItem: function (target, index) {
        console.log("++++++++编辑场景+++++index+++" + index)
        //测试 传第一条
        if (this.isDebug) {
          this.sceneList[0].deviceData = [
            //电动窗帘（奥科）
            {
              hostId: 111,
              address: "11101",
              deviceTypeCode: 9,
              deviceName: "电动窗帘",
              status: [
                {"code": "status", "value": "90", "description": "打开90%"}
              ]
            },
            //双控电动窗帘（奥科）
            {
              hostId: 111,
              address: "22201",
              deviceTypeCode: 19,
              deviceName: "双控电动窗帘",
              status: [
                //内帘
                {"code": "in", "value": "70", "description": "内帘打开70%"},
                //外帘
                {"code": "out", "value": "50", "description": "外帘打开50%"}
              ]
            },
            //灯1
            {
              hostId: 111,
              address: "11105",
              deviceTypeCode: 32513,
              deviceName: "灯1",
              status: [
                {"code": "status", "value": "1", "description": "打开"}
              ]
            },
            //灯2
            {
              hostId: 111,
              address: "11105",
              deviceTypeCode: 32513,
              deviceName: "灯2",
              status: [
                {"code": "status", "value": "0", "description": "关闭"}
              ]
            },
            //电动窗帘（奥科）
            {
              hostId: 111,
              address: "11101",
              deviceTypeCode: 9,
              deviceName: "电动窗帘",
              status: [
                {"code": "status", "value": "50", "description": "打开50%"}
              ]
            },
            //双控电动窗帘（奥科）
            {
              hostId: 111,
              address: "22201",
              deviceTypeCode: 19,
              deviceName: "双控电动窗帘",
              status: [
                //内帘
                {"code": "in", "value": "50", "description": "内帘打开50%"},
                //外帘
                {"code": "out", "value": "50", "description": "外帘打开50%"}
              ]
            },
            //灯1
            {
              hostId: 111,
              address: "11105",
              deviceTypeCode: 32513,
              deviceName: "灯1",
              status: [
                {"code": "status", "value": "1", "description": "打开"}
              ]
            },
            //灯2
            {
              hostId: 111,
              address: "11105",
              deviceTypeCode: 32513,
              deviceName: "灯2",
              status: [
                {"code": "status", "value": "0", "description": "关闭"}
              ]
            },
          ]
          this.sceneList[0].week = eval("("+this.sceneList[0].week+")");

          console.log("++++++++编辑场景数据+++" + JSON.stringify(this.sceneList[0]))
          this.$router.push(
            {
              path: '/addScene',
              name: 'addScene',
              query: {
                dataObj: this.sceneList[0],
                pageType: 1,//编辑场景

              }
            }
          );
          // console.log("编辑场景数据"+JSON.stringify(this.sceneList[index]) );
        } else {
          console.log("++++++++编辑场景++++++++第几条" + target.id)
          Cordova.exec(this.getSuccess, this.getFail, "SmartHomePlugins", "getSceneById", [target.id]);
        }
      },
      //查询index场景数据成功

      getSuccess: function (data) {
        var obj = eval("(" + data + ")")
        console.log("++++++++获取场景数据 editScene ++++++++" + JSON.stringify(data))
        this.$router.push(
          {
            path: '/addScene',
            name: 'addScene',
            query: {
              dataObj: obj,
              pageType: 1,//编辑场景
            }
          }
        );
      },
      getFail: function (msg) {
          this.showNativeToast(msg);
      },
      deleteSuccess: function (data) {
        console.log("++++++++删除场景成功+++重新查询场景数据+++++")
        this.showNativeToast("删除场景成功")
        Cordova.exec(this.getSceneSuccess, this.getSceneFail, "SmartHomePlugins",
          "getSceneList", [])
      },
      deleteFail: function (msg) {
        console.log("++++++++删除场景失败++++++++")
        this.showNativeToast("删除场景失败")
      },
      executeSceneSuccess: function (msg) {
        console.log("++++++++执行场景成功++++++++")

        this.showNativeToast("执行场景成功")
      },
      executeSceneFail: function (msg) {
        console.log("++++++++执行场景失败++++++++")
        this.showNativeToast("执行场景失败")
      }

    }
  }
</script>


<style lang="css">
  .addNewSceneImage {
    width: 24px;
    height: 24px;
  }

  /************滑块样式**********/
  .my-layout .swiper-layout-parent .swiper-slide {
    border: none;
    line-height: 120px;
  }

  .mu-slider{
    width: 90% !important
  }

  .my-layout .swiper-layout-parent .swiper-slide.content {
    /*padding-left: 20px;*/
  }

  .my-layout .swiper-layout-item {
    border: none;
    border-bottom: 1px solid #EAEAEB;
  }

  .gury {
    color: #ccc;
  }

  .my-layout .before-menu .menu-item {
    width: 50%;
    float: left;
    text-align: center;
    color: #fff;
  }

  .my-layout .before-menu .menu-item:nth-of-type(1) {
    background-color: #FF9C00;
  }

  .my-layout .before-menu .menu-item:nth-of-type(2) {
    background-color: #FF3B30;
  }

  /************滑块样式 the end**********/
  .bottomLine {
    color: grey;
    visibility: hidden;
  }

  .bottomLineVisible {
    visibility: visible;
  }

  .msgHidden {
    visibility: hidden;
  }

  /*弹窗提示*/
  .scenePopupBg {
    background-color: lightgrey;
    border-radius: 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    margin: 10px;
    
  }

  .TextHint2 {
    margin: 0px;
    font-size: medium;
  }

  .addControllerText {
    margin-bottom: 20px;
    bottom: 0px;
    color: #6699FF;
    height: 20px;
    position: fixed;
    font-size: medium;
  }

  .addSceneText {
    padding-top: 10px;
    color: #6699FF;
    font-size: medium;
    margin: 0 auto
  }

  .TextHint {
    font-size: large;
  }

  .warnImage {
    padding: 8px;
  }

  .demo-popup-top {
    margin-top: 100px;
    width: 95%;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    background-color: lightgrey;
  }

  .scene-popup-top1 {
    margin-top: 100px;
    height: 150px;
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    background-color: lightgrey;
  }

  /*弹窗提示 the end*/
  .sceneListWrapper {
    height: calc(100% - 56px - 96px);
    /* overflow: scroll; */
    overflow:auto;
    position: relative;
    user-select: none;
    -webkit-overflow-scrolling: touch;
  }

  .addDiv {
    vertical-align: bottom;
    margin-right: 20px;
    position: absolute;
    right: 0;
    bottom: 0;
    margin-bottom: 2px;
  }

  .sceneDiv {
    display: flex;
    flex-direction: row;
    position: relative;
    height: 56px;
    margin: 0;
    align-items: center;
    justify-content: flex-start; /*水平居中*/
    padding-top: 20px;
  }

  .sceneText {
    line-height: 56px;
    text-align: left;
    font-size: 24px;
    font-family: PingFangLight;
    margin-left: 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    color: white;

  }

  .scenePageHeader {
    z-index: 99999;
    width: 100%;
    height: 96px;
    background-color: #5bc8ff;
    background-size: 100% 96px;
  }

  .mu-circle-clipper, .mu-circle-gap-patch{
    border-color: #5bc8ff !important;
  }

  .mu-refresh-control{
    color: #5bc8ff !important;
  }

  @font-face {
    /*font-family: PingFangLight;*/
    /*src: url('../../../static/font/PingFangLight.ttf');*/
    font-family: PingFangMedium;
    src: url('../../../static/font/PingFangMedium.ttf');
  }

</style>



