<template xmlns:v-on="http://www.w3.org/1999/xhtml" xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div class="sceneWapper">
    <!--引入标题栏-->
    <div style="position: fixed;top: 0;right: 0;left: 0">
      <div class="title2">
        <div v-on:click="backScene" class="imageBackScene"></div>
        <p v-on:click="saveScene" class="saveScene">完成</p>
      </div>
      <div class="title3">
        <div class="scenceDesc">
          <div class="scenceDescChance" @click="ChangeScence">
            <img src="static/image/other-sence-2x.png" v-if="senceImg == '' || senceImg == null"/>
            <img :src="senceImg" v-else/>
            <img class="scenceImg2" src="static/image/editScene.png"/>
          </div>
          <div class="scenceText">
            <input v-model="sceneData.name"/>
          </div>
        </div>
        <div class="timeText" v-on:click="clickToEditTimer">
          <p class="timerTxt" v-show="hasRepeat">时间：{{sceneData.time}}</p>
          <p class="line1"></p>
          <div class="repeat1">
            <div style="flex: 1" v-if="hasRepeat">重复：{{repeatText}}</div>
            <div style="flex: 1" v-else>定时</div>
            <img class='toEditTimer' src="../../../../static/image/right.png"/>
          </div>

        </div>
      </div>
    </div>
    <div class="addScene_deviceStateWrapper">


      <div class="deviceItems" v-for="(device,index) in sceneData.deviceData">
        <!-----deviceTypeImage 根据类型显示 ------>
        <img v-if="device.deviceTypeCode==0x7F01" class="deviceImage"
             src="../../../../static/image/light_on_image.png"/>
        <img v-else-if="device.deviceTypeCode==0x7F08" class="deviceImage1"
             src="../../../../static/image/exhaust_Fan_open_icon.png"/>
        <img v-else-if="device.deviceTypeCode==0x000B" class="deviceImage"
             src="../../../../static/image/lock_image.png"/>
        <img v-else-if="device.deviceTypeCode==0x0009 || device.deviceTypeCode==0x0013" class="deviceImage"
             src="../../../../static/image/curtain_image.png"/>
        <img
          v-else-if="device.deviceTypeCode == 0x0005 || device.deviceTypeCode == 0x0006 || device.deviceTypeCode == 0x0011"
          class="deviceImage" src="../../../../static/image/airConditioner.png"/>

        <div class="deviceState1" v-on:click="changeDeviceState(device,index)">
          <div class="deviceItemClass">{{device.deviceName}}
            <br/>
            <div class="displayRow">
              <!-- <p v-if="checkAirStatus(device)" v-bind:class="{redBg:(device.status[0].description=='请修改空调状态')}" class="deviceStateTxt">
                {{device.status[0].description}}</p> -->

              <p v-for="(deviceState,index) in device.status" v-bind:class="{redBg:(deviceState.description=='请添加窗帘状态'
        || deviceState.description =='请添加内帘状态'  || deviceState.description=='请添加外帘状态'
         ||  deviceState.description=='请修改空调状态' ||  deviceState.description=='请设置空调温度' ||  deviceState.description=='请设置空调模式')}"
                 class="deviceStateTxt">
                <span v-if="index!=0" class="fenge"></span>
                {{deviceState.description}}
              </p>
            </div>
          </div>
          <mu-switch slot="right" v-if="device.deviceTypeCode==0x7F01 || device.deviceTypeCode==0x7F08 "
                     :value="device.status[0].value==1 ? true:false"
                     v-on:change="changeLightState(index,device.status[0].value)"></mu-switch>
          <!--<mu-divider />-->
        </div>
      </div>


    </div>
    <div class="addBt">
      <mu-float-button icon="+" backgroundColor="#5bc8ff" v-on:click="addMoreDeviceForScene"/>
    </div>
    <!-----弹窗修改设备的参数 ------>
    <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="isToPop">

      <!--<mu-appbar title="窗帘">-->
      <!--<mu-flat-button slot="right" label="确定" color="white" @click="confirm"/>-->
      <!--<mu-flat-button slot="left" label="取消" color="white" @click="closePop"/>-->
      <!--</mu-appbar>-->
      <div class="selectcurtainPopupTitle">
        <p class="cancelCurtainValueTxt" @click='closePop'>取消</p>
        <p class="curtainPopupTitleName">窗帘</p>
        <p class="confirmCurtainValueTxt" @click='confirm'>确定</p>
      </div>
      <mu-divider/>


      <!-----双控 ------>
      <mu-content-block v-bind:class="{blockInVisible: !isDoubleCurtain}">
        <p class="margionLeftStyle">
          内帘
        </p>
        <div class="columnCenterDiv">
          <sliderTxtComponent></sliderTxtComponent>
          <mu-slider v-model="innerState" :step="5" class="demo-slider2"/>
        </div>
        <p class="margionLeftStyle">
          外帘
        </p>
        <div class="columnCenterDiv">
          <sliderTxtComponent></sliderTxtComponent>
          <mu-slider v-model="outterState" :step="5" class="demo-slider2"/>
        </div>
      </mu-content-block>

      <!-----单控 ------>
      <mu-content-block v-bind:class="{blockInVisible: isDoubleCurtain }">
        <p class="margionLeftStyle">
          窗帘
        </p>
        <div class="columnCenterDiv">
          <sliderTxtComponent></sliderTxtComponent>
          <mu-slider v-model="state" :step="5" class="demo-slider2"/>
        </div>
      </mu-content-block>
    </mu-popup>

    <!-- 中部弹出 -->
    <mt-popup
      v-model="isPopupValue"
      popup-transition="popup-fade">
      <div id="triangle"></div>
      <div id="popup-list">
        <ul class="popup-list-ul">
          <li class="popup-list-li"  v-for="(item,index) in scenceList" :key="item.id" @click="changesSenAct(index)">
            <div class="li-self">
              <img :src="item.img" style="width: 24px;"/>
              <span>{{ item.senceName }}</span>
            </div>
          </li>
        </ul>
      </div>
    </mt-popup>

    <mu-popup position="bottom" popupClass="demo-popup-bottom" :open="isAirConditioner">
      <div class="selectcurtainPopupTitle">
        <p class="cancelCurtainValueTxt" @click='closePopAir'>取消</p>
        <p class="curtainPopupTitleName">{{ curtainName }}</p>
        <p class="confirmCurtainValueTxt" @click='confirmAir'>确定</p>
      </div>
      <!-- 空调 -->
      <mu-content-block id="airBlock">
        <!-- 运行状态 -->
        <div style="width: 100%;line-height:45px;overflow: hidden;margin: 5px auto;background-color: white">
          <div style="width: 50%;float: left;margin-left: 2%;">
            运行状态
          </div>
          <mu-switch slot="right" id="switch-air" v-model="airStatus.airTurn" @change="ChangeOnTurn"></mu-switch>
        </div>
        <!-- 选择状态执行 -->
        <div style="width: 100%;line-height:45px;overflow: hidden;margin: 5px auto;background-color: white"
             @click="toggle">
          <div style="margin-left: 2%;float: left">
            模式
          </div>
          <div style="float: right;margin-right: 4%">
            {{ airStatus.nowModel }}
          </div>
        </div>
        <!-- 滑动组件选择 -->
        <div>
          <div style="width: 100%;line-height: 45px;margin: 5px auto 3px;background-color: white">
            <img src="">
            <span>温度</span>
          </div>
          <div style="height: 80px;width: 100%;margin: 5px auto;background-color: white">
            <SliderDescCompent :ArrayNumber.once="ArrayNumber"></SliderDescCompent>
            <div style="height: 30px;width: 80%;background-color: white;margin: 0 auto">
              <el-slider v-model="airStatus.airTemp" :step="4" :min="16" :max="32" show-stops @change="ChangeSlider"
                         :disabled="!airStatus.airTurn"></el-slider>
            </div>
          </div>
        </div>
      </mu-content-block>

    </mu-popup>

    <mu-drawer right :open="open" @close="toggle()" id="show_Model" style="height: 50%;margin-top: auto">
      <mu-appbar title="选择模式" style="height: 15%"/>
      <mu-list style="height: 85%">
        <div style="overflow: hidden;height: 20%;" @click.stop="closeDrawer(1)">
          <div class="model_text_desc">
            <mu-list-item title="制冷模式"/>
          </div>
          <div class="drawerText" v-if="airStatus.nowModel == '制冷模式'">
            当前模式
          </div>
        </div>
        <div style="overflow: hidden;height: 20%" @click.stop="closeDrawer(2)">
          <div class="model_text_desc">
            <mu-list-item title="制热模式"/>
          </div>
          <div class="drawerText" v-if="airStatus.nowModel == '制热模式'">
            当前模式
          </div>
        </div>
        <div style="overflow: hidden;height: 20%" @click.stop="closeDrawer(3)">
          <div class="model_text_desc">
            <mu-list-item title="除湿模式"/>
          </div>
          <div class="drawerText" v-if="airStatus.nowModel == '除湿模式'">
            当前模式
          </div>
        </div>
        <div style="overflow: hidden;height: 20%" @click.stop="closeDrawer(4)">
          <div class="model_text_desc">
            <mu-list-item title="送风模式"/>
          </div>
          <div class="drawerText" v-if="airStatus.nowModel == '送风模式'">
            当前模式
          </div>
        </div>
        <div style="overflow: hidden;height: 20%" @click.stop="closeDrawer(5)">
          <div class="model_text_desc">
            <mu-list-item title="自动"/>
          </div>
          <div class="drawerText" v-if="airStatus.nowModel == '自动'">
            当前模式
          </div>
        </div>
      </mu-list>
    </mu-drawer>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        sceneNo: this.$route.query.number,
        pageType: this.$route.query.pageType,
        name: ' ',
        isTitleBarBackImageVisible: true,
        deviceTypeImage: require("../../../../static/image/light_on_image.png"),
        //lightState:true,
        deviceName: '一楼/客厅：水晶灯',
        sceneData: "",
        // 初始场景
        senceImg:"",
        repeatText: "",
        isToPop: false,
        //双控状态
        innerState: 0,
        outterState: 0,
        //单控状态
        state: 0,
        isDoubleCurtain: false,
        currentCurtainIndex: "",
        sceneWeek: [],
        sceneWeekString: "",
        ArrayNumber: ['16', '20', '24', '28', '32'],
        curtainName: "空调",
        //  控制空调弹窗
        isAirConditioner: false,
        //  控制模式选择弹窗
        open: false,
        //  空调模式(空调滑块初始值)
        airStatus: {airTurn: false, nowModel: "制冷模式", airTemp: 20, mode: 0},
        //  空调检测量
        SwitchDetection: '',
        //  场景选择
        isPopupValue:false,
        // 场景列表
        scenceList:[
          {img:"static/image/go-home-2x.png",senceName:"回家"},
          {img:"static/image/leave-home-2x.png",senceName:"离家"},
          {img:"static/image/movie-sence-2x.png",senceName:"影院"},
          {img:"static/image/rest-sence-2x.png",senceName:"休息"},
          {img:"static/image/read-sence-2x.png",senceName:"阅读"},
          {img:"static/image/Cozy-sence-2x.png",senceName:"温馨"},
          {img:"static/image/eat-sence-2x.png",senceName:"就餐"},
          {img:"static/image/romantic-sence-2x.png",senceName:"浪漫"},
          {img:"static/image/wash-and-rinse-sence-2x.png",senceName:"洗漱"},
          {img:"static/image/other-sence-2x.png",senceName:"其他"}
        ]
      }
    },
    created() {
      console.log("pageType:" + this.pageType);
      if (this.pageType == 1) {//编辑场景
        var sceneDataForModify = this.$route.query.dataObj;
        if (typeof(sceneDataForModify) != "undefined") {
          this.sceneData = sceneDataForModify;
          console.log("传入编辑场景" + JSON.stringify(sceneDataForModify))
          this.computeWeeks();
          //添加
        } else {
          console.log("传入编辑场景空")
        }
      } else {//添加场景
        this.sceneData = {
          name: "场景1",
          imageUrl: '',
          time: " ",
          week: [
            {"name": "一", "value": 0},
            {"name": "二", "value": 0},
            {"name": "三", "value": 0},
            {"name": "四", "value": 0},
            {"name": "五", "value": 0},
            {"name": "六", "value": 0},
            {"name": "日", "value": 0}
          ],
          //默认添加场景的设备数量为空
          deviceData: []
        }
        this.sceneData.name = "场景" + (this.sceneNo + 1);
      }


      //兼容从编辑定时页面传回的时间
      var sceneDataFromTimer = this.$route.query.sceneData;
      console.log("获取修改到的场景数据" + JSON.stringify(this.sceneData))
      if (typeof(sceneDataFromTimer) != "undefined") {
        //获取修改到的时间
        this.sceneData = sceneDataFromTimer;
        this.computeWeeks();
        console.log("获取修改到的场景数据不为空" + JSON.stringify(this.sceneData))
      } else {
        console.log("没有获取修改到的时间")
      }


      //兼容从设备列表传回的数据
      var sceneDataFromDeviceList = this.$route.query.sceneDataFromDeviceList;
      console.log("获取修改到的场景数据" + JSON.stringify(this.sceneData))
      if (typeof(sceneDataFromDeviceList) != "undefined") {
        //获取修改到的时间
        this.sceneData = sceneDataFromDeviceList;
        this.computeWeeks();
        console.log("获取修改到的场景数据不为空" + JSON.stringify(this.sceneData))
      } else {
        console.log("没有获取修改到的时间")
      }
    },


    mounted() {
      //  对观测量初始化
      this.SwitchDetection = {...this.airStatus};
      console.log(this.SwitchDetection.airTurn);
    },

    computed: {
      hasRepeat: function () {
        return this.sceneData.time != "" && this.repeatText != "";
      }
    },

    methods: {
      // checkAirStatus:function(device){//检查设备，如果设备是空调且是关闭状态时不显示空调的另两个状态
      //   if(device.deviceTypeCode == 0x0005 || device.deviceTypeCode == 0x0006 || device.deviceTypeCode == 0x0011){
      //     if(device.status[0]!= null && device.status[0].value == "0"){
      //         return true;
      //     }
      //   }
      //   return false;
      // },
      addMoreDeviceForScene: function () {
        //获取未添加的场景
        this.$router.push({
          path: '/addMoreDeviceForScene',
          name: 'addMoreDeviceForScene',
          query: {
            sceneData: this.sceneData,
            fromPageName: "addScene",
            pageType: this.pageType,
          }
        })
      },

      saveScene: function () {
        this.computeWeekString();


//        this.sceneData.deviceData.forEach(function (currentValue, index, arr) {
//          var data= currentValue.status
//          if(data[0].description=='请添加窗帘状态'||data[0].description=='请添加内帘状态' ||data[0].description=='请添加外帘状态' ){
//            console.log("请添加" +currentValue.deviceName + data[0].description )
//          }else {
//            //保存场景数据
//            Cordova.exec(this.saveSceneDataSuccess, this.saveSceneDataSuccessFail, "SmartHomePlugins", "addScene", []);
//          }
//        })
//
        if (this.isDebug) {
          //获取到时间与重复
          if (this.pageType == 0) {
            console.log("添加新的一条场景" + JSON.stringify(this.sceneData))
          } else {
            console.log("修改一条场景" + this.sceneWeekString);
          }

          this.$router.push({
            path: '/scene_page',
            name: 'scene_page',
          })
        } else {
          var name = this.sceneData.name;
          var time = this.sceneData.time;
          var weeks = this.sceneWeekString;
          var deviceData = JSON.stringify(this.sceneData.deviceData);
          //调用自定义的验证插件
          if (name == "") {
            this.showNativeToast("请输入名字");
          } else if (this.getBlen(name) > 24) {
            this.showNativeToast("场景名字长度超出范围，请修改");
          } else if (deviceData == "[]") {
            this.showNativeToast("请添加设备");
          } else {
            if (this.pageType == 0) {//添加场景
              console.log("添加新的一条场景" + JSON.stringify(this.sceneData))
              Cordova.exec((data) => {
                console.log("++++++++添加场景成功++++++++")
                this.showNativeToast("添加场景成功")
                this.$router.push({
                  path: '/scene_page',
                  name: 'scene_page',
                })
              }, (data) => {
                console.log("++++++++添加场景失败++++++++")
                this.showNativeToast("添加场景失败:" + data);
              }, "SmartHomePlugins", "addScene", [name, time, weeks, deviceData]);
            } else {
              var sceneId = this.sceneData.id;
              //调用自定义的验证插件
              console.log("保存编辑场景" + " sceneId " + sceneId + " weeks " + weeks + "" + " deviceData " + deviceData)
              Cordova.exec((data) => {
                console.log("++++++++保存编辑场景++++++++")
                this.showNativeToast("场景修改成功")
                this.$router.push({
                  path: '/scene_page',
                  name: 'scene_page',
                });
              }, (data) => {
                console.log("++++++++场景修改失败++++++++")
                this.showNativeToast("场景修改失败:" + data)
              }, "SmartHomePlugins", "updateScene", [sceneId, name, time, weeks, deviceData]);
            }
          }
        }
      },
      getBlen: function (str) {
        if (str == null) return 0;
        if (typeof(str) != "string") {
          str = "";
        }
        return str.replace(/[^\x00-\xff]/g, "01").length;
      },
      backScene: function () {
        this.$router.push({
          path: '/scene_page',
          name: 'scene_page',
        })
      },
      //编辑场景时间
      clickToEditTimer: function () {
        //console.log("编辑场景时间传入"+JSON.stringify(this.sceneData))
        this.$router.push({
          path: '/scene_timer',
          name: 'scene_timer',
          query: {
            sceneData: this.sceneData,
            fromPageName: "addScene",
            pageType: this.pageType,
          }
        })
      },

      //时间对象转化成string
      computeWeekString: function () {
        var arry = [];
        for (var s = 0; s < 7; s++) {
          if (this.sceneData.week[s].value == 1) {
            arry.push(s + 1);
          }
        }
        this.sceneWeekString = arry.toString();
      },
      computeWeeks: function () {
        //拼接重复
        var num = 0;
        for (var k = 0; k < 7; k++) {
          if (this.sceneData.week[k].value == 1) {
            if (num == 0) {
              this.repeatText = "周" + this.sceneData.week[k].name;
            } else {
              this.repeatText += ("," + "周" + this.sceneData.week[k].name);
            }
            num++;
          }
        }
      },
      changeDeviceState: function (deviceData, index) {
        this.currentCurtainIndex = index;
        //双控
        if (deviceData.deviceTypeCode == 0x0013) {
          this.isToPop = true;
          this.isDoubleCurtain = true;
          //console.log("修改窗帘"+ deviceData.status );
          for (var m = 0; m < deviceData.status.length; m++) {
            if (deviceData.status[m].code == "in") {
              //赋初始值
              this.innerState = deviceData.status[m].value;
            } else if (deviceData.status[m].code == "out") {
              this.outterState = deviceData.status[m].value;
            }
          }
          // console.log("双控"+ "内帘" + this.innerState + " 外帘 " +this.outterState)
          //单控
        } else if (deviceData.deviceTypeCode == 0x0009) {
          this.isToPop = true;
          this.isDoubleCurtain = false;
          for (var s = 0; s < deviceData.status.length; s++) {
            if (deviceData.status[s].code == "status") {
              this.state = deviceData.status[s].value;
            }
          }
        } else if (deviceData.deviceTypeCode == 0x0005 || deviceData.deviceTypeCode == 0x0006 || deviceData.deviceTypeCode == 0x0011) {
          //  控制是否弹窗空调
          this.isAirConditioner = true;
          for (var k = 0; k < deviceData.status.length; k++) {
            if (deviceData.status[k].code == "status") {
              this.airStatus.airTurn = (parseInt(deviceData.status[k].value) == 1);
            } else if (deviceData.status[k].code == "tem") {
              this.airStatus.airTemp = parseInt(deviceData.status[k].value);
            } else if (deviceData.status[k].code == "mode") {
              this.airStatus.mode = parseInt(deviceData.status[k].value);
              this.changeAirMode(this.airStatus.mode + 1);
            }
          }
          console.log("deviceData.status:" + JSON.stringify(deviceData.status));
          console.log("this.airStatus.airTurn:" + this.airStatus.airTurn + "  this.airStatus.airTemp:" + this.airStatus.airTemp + "  this.airStatus.mode:" + this.airStatus.mode);
        }
      },

      changeLightState: function (index, value) {
        console.log("index" + index + " 值 " + value)
        console.log("====switch deviceData:" + JSON.stringify(this.sceneData.deviceData[index]));
        if (value == 0) {
          //console.log("打开")
          this.sceneData.deviceData[index].status[0].description = "打开";
          this.sceneData.deviceData[index].status[0].value = 1;
        } else {
          // console.log("关闭")
          this.sceneData.deviceData[index].status[0].description = "关闭";
          this.sceneData.deviceData[index].status[0].value = 0;
        }
        console.log("====switch deviceData:" + JSON.stringify(this.sceneData.deviceData[index]));
      },
      closePop: function () {
        this.isToPop = false;
      },
      confirm: function () {
        this.isToPop = false;
        //更改场景数组内数据。
        if (this.isDoubleCurtain) {
          //[0] 内帘
          this.sceneData.deviceData[this.currentCurtainIndex].status[0].value = this.innerState;
          this.sceneData.deviceData[this.currentCurtainIndex].status[0].description = "内帘" + this.innerState + "%";
          //[1] 外帘
          this.sceneData.deviceData[this.currentCurtainIndex].status[1].value = this.outterState;
          this.sceneData.deviceData[this.currentCurtainIndex].status[1].description = "外帘" + this.outterState + "%";
          console.log(" 修改窗帘状态后的场景数据 " + "内帘 " + this.innerState + " 外帘 " + this.outterState + " 设备数据 " + JSON.stringify(this.sceneData.deviceData[this.currentCurtainIndex]))
        } else {
          this.sceneData.deviceData[this.currentCurtainIndex].status[0].value = this.state;
          this.sceneData.deviceData[this.currentCurtainIndex].status[0].description = "打开" + this.state + "%";
          console.log(" 修改窗帘状态后的场景数据 " + JSON.stringify(this.sceneData.deviceData[this.currentCurtainIndex].status[0]))
        }
      },

      //  控制空调数据
      confirmAir: function () {
        this.isAirConditioner = false;
        //更改场景数组内数据。
        // this.sceneData.deviceData[this.currentCurtainIndex].status[0].value = "空调";
        console.log(this.SwitchDetection);
        this.SwitchDetection.nowModel = this.airStatus.nowModel;
        this.SwitchDetection.airTemp = this.airStatus.airTemp;
        // if (this.SwitchDetection.airTurn != this.airStatus.airTurn) {
        this.SwitchDetection.airTurn = this.airStatus.airTurn;
        this.sceneData.deviceData[this.currentCurtainIndex].status[0].code = "status";
        this.sceneData.deviceData[this.currentCurtainIndex].status[0].value = this.airStatus.airTurn ? 1 : 0;
        this.sceneData.deviceData[this.currentCurtainIndex].status[0].description = "状态：" + (this.airStatus.airTurn ? "开启" : "关闭");

        if (!this.airStatus.airTurn) {//空调状态为关时不保存其他状态到服务器
          this.sceneData.deviceData[this.currentCurtainIndex].status = this.sceneData.deviceData[this.currentCurtainIndex].status.slice(0, 1);
          return;
        }

        if (typeof(this.sceneData.deviceData[this.currentCurtainIndex].status[1]) == 'undefined') {
          this.sceneData.deviceData[this.currentCurtainIndex].status[1] = {};
        }
        this.sceneData.deviceData[this.currentCurtainIndex].status[1].code = "tem";
        this.sceneData.deviceData[this.currentCurtainIndex].status[1].value = this.airStatus.airTemp;
        this.sceneData.deviceData[this.currentCurtainIndex].status[1].description = "温度：" + this.airStatus.airTemp;

        if (typeof(this.sceneData.deviceData[this.currentCurtainIndex].status[2]) == 'undefined') {
          this.sceneData.deviceData[this.currentCurtainIndex].status[2] = {};
        }
        this.sceneData.deviceData[this.currentCurtainIndex].status[2].code = "mode";
        this.sceneData.deviceData[this.currentCurtainIndex].status[2].value = this.airStatus.mode;
        this.sceneData.deviceData[this.currentCurtainIndex].status[2].description = "模式：" + this.airStatus.nowModel;

      },
      closePopAir: function () {
        this.isAirConditioner = false;
        if (this.SwitchDetection.airTurn != this.airStatus.airTurn) {
          //  恢复原来状态
          this.airStatus.airTurn = this.SwitchDetection.airTurn;
        }
        //  恢复原来状态
        this.airStatus.nowModel = this.SwitchDetection.nowModel;
        this.airStatus.airTemp = this.SwitchDetection.airTemp;
      },

      //  更改空调模式
      toggle() {
        if (this.airStatus.airTurn) {
          this.open = !this.open;
          this.isAirConditioner = false;
          //  获取设备高度
          let Appheight = document.documentElement.clientHeight;
          //  设置弹出的高度
          let marginTop = Appheight * 0.5;
          document.getElementById("show_Model").style.marginTop = marginTop;
        }
      },

      changeAirMode(val) {
        switch (val) {
          case 1:
            this.airStatus.nowModel = "制冷";
            break;
          case 2:
            this.airStatus.nowModel = "制热";
            break;
          case 3:
            this.airStatus.nowModel = "除湿";
            break;
          case 4:
            this.airStatus.nowModel = "送风";
            break;
          case 5:
            this.airStatus.nowModel = "自动";
            break;
        }
      },

      closeDrawer(val) {
        this.changeAirMode(val);
        this.open = false;
        this.airStatus.mode = val - 1;
        this.isAirConditioner = true;
        console.log("目前模式为" + this.airStatus.nowModel + " 之前是 " + this.SwitchDetection.nowModel);
      },

      //  空调滑块
      ChangeSlider: function (val) {
        //  这里获取数值
        this.airStatus.airTemp = val;
      },

      //  空调开关
      ChangeOnTurn() {
        this.airStatus.airTurn = !this.airStatus.airTurn;
        console.log(this.airStatus.airTurn);
      },

      // 更换场景弹窗
      ChangeScence:function () {
        this.isPopupValue = true;
        console.log("123123");
      },

      // 更改场景按钮
      changesSenAct:function (key) {
        console.log(key);
        this.isPopupValue = false;
        this.senceImg = this.scenceList[key].img;
        this.sceneData.name = this.scenceList[key].senceName;
      }
    }
  }
</script>

