<template xmlns:v-bind="http://www.w3.org/1999/xhtml" xmlns:v-on="http://www.w3.org/1999/xhtml">
  <!--标题栏-->
  <div class="TitleBar">
    <div v-on:click="back" class="imageDiv1" v-bind:class="{isVisibleClass:isVisible}">
      <img class="leftImage" :src="backSrc"/>
    </div>
    <p class="TitleName">{{titleName}}</p>
    <div class="imageDiv2">
      <img  class="rightImage" v-on:click="clickSave" v-bind:class="{isSaveVisibleClass:isSaveVisible}" v-if="isImg" :src="src" id="imgTurn"/>
      <span class="DesWords"  v-on:click="clickSave" v-else>{{ DesWord }}</span>
    </div>
  </div>
</template>

<style>
  .TitleBar{
    position: fixed;
    z-index: 99999;
    line-height: 64px;
    /*position: fixed;*/
    /*position: relative;*/
    padding-top: 10px;
    align-items: center;
    width:100%;
    height:64px;
    FILTER: progid:DXImageTransform.Microsoft.Gradient(gradientType=0,startColorStr=#5bc8ff,
    endColorStr=#12b0ff); /*IE*/
    background:-moz-linear-gradient(left, #5bc8ff, #12b0ff);/*火狐*/
    background:-webkit-gradient(linear, 0% 0%, 100% 0%,from(#5bc8ff), to(#12b0ff));/*谷歌*/
  }



  /* 用于左侧 */

  .leftImage{
    z-index: 9999;
    width:24px;
    height: 24px;
    background-size:auto 24px;
    background-repeat: no-repeat;
    vertical-align: middle;
  }

  .imageDiv1{
    visibility: hidden;
    position: relative;
    z-index: 9999;
    height: 64px;
    width: 24px;
    float: left;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
  }


  /* 用于右侧 */
  .rightImage{
    z-index: 9999;
    width:24px;
    height: 24px;
    background-size:auto 24px;
    background-repeat: no-repeat;
    vertical-align: middle;
  }


  .imageDiv2{
    /*visibility: hidden;*/
    position: relative;
    z-index: 9999;
    height: 64px;
    width: 32px;
    float: right;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
  }

  .isVisibleClass{
    visibility: visible;
  }


  .isSaveVisibleClass{
    visibility: visible;
  }

  .TitleName{
    line-height: 64px;
    position: absolute;
    text-align:center;
    left: 0;
    right: 0;
    margin: 0px;
    font-size: 18px;
    color: white;
    font-family:PingFangLight;
    vertical-align: middle;
  }

  .DesWords{
    visibility: visible
  }

  @font-face {
    font-family: PingFangLight;
    src: url('../../../static/font/PingFangLight.ttf');
  }

</style>


<script>
  export default {
    name: 'titleBar',
    data() {
      return {
        name: '',
      }
    },

    props: ['titleName','isVisible', 'isSaveVisible','isImg','src','DesWord',"backSrc",'isLeftClick'],

    created() {
      // console.log(isImg);
    },
    mounted() {

    },
    methods: {
      back: function () {
        console.log("back")
        if(this.isLeftClick){
          this.$emit('LeftBttonClick');
          return
        }
        this.$router.go(-1)
      },

      clickSave: function () {
        this.$emit('clickRightButton');
      }
    }
  }
</script>



