import * as types from './calendarVariable';

const state = {
  dateYear: '',
  dateMonth: '',
  dateDay: '',
  date: '',     //完整的日期
  status: false,
  ok: false,      //点击ok
  mark: false, //是否显示遮罩,
  //
  endCalendarDate: '',     //结束的完整的日期
}

const actions = {
  //日期组件的开关
  calendarStatus: function ({commit}, status) {
    commit(types.COMM_MARK_STATUS, status);
    commit(types.CALENDAR_STATUS, status)
  },
  //遮罩的开关
  markStatus({commit}, status) {
    commit(types.COMM_MARK_STATUS, status);
  },
  //单个数值
  calendarDateStatus: function ({commit}, date) {

    commit(types.CALENDAR_DATE_STATUS, date);

  },
  //确定日期
  calendarOk: function ({commit}, status) {
    commit(types.CALENDAR_CHOICE, status[0]);
    if (state.dateYear && state.dateMonth && state.dateDay && status[0]) {
      //根据标志位，判断起止与结束
      //console.log("根据标志位" + status[1])
      if (status[1]) {
        commit(types.CALENDAR_GET_DATE);
      } else {
        commit(types.END_CALENDAR_CHOICE);
      }

      commit(types.COMM_MARK_STATUS, false);
      commit(types.CALENDAR_STATUS, false);
      commit(types.CALENDAR_CHOICE, false);
    }
  },

  //更新开始日期组件
  refreshStartCalendar: function ({commit}, status) {
    var temp = status.split("/");
    state.dateYear = temp[0]
    state.dateMonth = temp[1]
    state.dateDay = temp[2]
    commit(types.CALENDAR_GET_DATE);
  },
  //更新结束日期组件
  refreshEndCalendar: function ({commit}, status) {
    var temp = status.split("/");
    state.dateYear = temp[0]
    state.dateMonth = temp[1]
    state.dateDay = temp[2]
    commit(types.END_CALENDAR_CHOICE);
  },


}

const mutations = {

  [types.CALENDAR_STATUS](state, status) {
    state.status = status
  },
  //开始滚轮控件
  [types.CALENDAR_GET_DATE](state) {
    state.date = state.dateYear + '-' + state.dateMonth + '-' + state.dateDay;


  },
  //结束滚轮控件
  [types.END_CALENDAR_CHOICE](state) {
    state.endCalendarDate = state.dateYear + '-' + state.dateMonth + '-' + state.dateDay;
  },

  [types.CALENDAR_DATE_STATUS](state, date) {

    switch (date.type) {
      case 'year':
        state.dateYear = date.d;
        break;
      case 'month':
        state.dateMonth = date.d;
        break;
      case 'day':
        state.dateDay = date.d;
        break;
    }

  },
  [types.CALENDAR_CHOICE](state, status) {
    state.ok = status;
  },
  [types.COMM_MARK_STATUS](state, status) {

    state.mark = status
  }
}

const getters = {
  getCalendarStatus: state => state.status,
  getCalendarDate: state => state.date,
  getCalendarOk: state => state.ok,
  getYear: state => state.dateYear,
  getMonth: state => state.dateMonth,
  getDay: state => state.dateDay,
  markStatus: state => state.mark,
  //结束日期
  getEndCalendarDate: state => state.endCalendarDate,
}


export default {
  state,
  actions,
  mutations,
  getters
}



