<template>
  <div class="wapper">
    <!--引入标题栏-->
    <titleBar v-bind:title-name="name" v-bind:is-visible="isTitleBarBackImageVisible"></titleBar>
    <div class="wifiPswWrapper">
    <img class='configImage' src="../../../../static/image/wifi_config_image.jpg"/>
    <p class="text">通过WiFi名称与密码配置网关</p>
    <selectedField  v-on:listenerToTouchEvent="receiveValueFromChildren" v-bind:list-data="wifiList" v-bind:error-hint="error"></selectedField>
    <mu-text-field v-model="pwdValue" hintText="请输入家庭路由密码" underlineClass="line"/><br/>
    <mu-raised-button v-on:click="startConfig" label="开始配置" class="blueButton" primary value="light"/>
    </div>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        name:'WiFi配置',
        isTitleBarBackImageVisible:true,
        //originalItem:'SmartHomeTest',
        wifiList: ['SmartHome1', 'SmartHome2', 'SmartHome3', 'SmartHome4', 'SmartHome5', 'SmartHome6'],
        error:'请选择WiFi名称',
        wifiName:'',
        pwdValue:'',
      }
    },

    created(){
      //获取当前手机连接网络


    },

    methods: {
      //开始配置
      startConfig:function () {
        console.log("开始配置" +"wifiName："+ this.wifiName+"  密码：" + this.pwdValue)
      },
      //父组件传值
      receiveValueFromChildren:function (data) {
        console.log("得到值"+data);
        this.wifiName=data;
      }

    }

  }
</script>


<style>

  .wifiPswWrapper{
    margin-top: 64px;
    font-size: large;
    height: calc(100% - 64px);
    background-color: lightgrey;
  }

  .mu-text-field-focus-line{
    background-color: rgba(0,0,0,.12)
  }
  .mu-text-field-hint.show {
    opacity: 0.8;
    /*color: black;*/
    text-align: -webkit-left;
  }
  .mu-text-field-focus-line.error{
    margin: 0;
    height: 1px;
    border: none;
    background-color: rgba(0,0,0,.12);
    left: 0;
    right: 0;
    position: absolute;
  }
  .mu-text-field-line line{
    background-color: rgba(0,0,0,.12);
  }
  .mu-text-field-focus-line{
    margin: 0;
    height: 1px;
    border: none;
    background-color: #1eaef9;
  }

  .line {
    background-color: rgba(0,0,0,.12);
    height: 1px;
  }

  .text{
    font-size: medium;
  }

  .wapper{
    position: relative;
  }

  .blueButton{
    margin-top: 20%;
  }

  .mu-text-field.has-label .mu-text-field-label {
    visibility: hidden;
  }

  .configImage{
    height: 200px;
    background-repeat: no-repeat;
    background-position:center 0;
  }

  .wapper{
    background-color: #F2F2F2;
  }
  html,body,wrapper{
    height:100%;
    overflow:hidden;
  }
</style>



