<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div class="wapper">
    <titleBar :title-name="name" :is-visible="isTitleBarBackImageVisible" :is-save-visible="isSaveVisible"
              @clickRightButton="toSave" :isImg="isImg" :back-src="backSrc" :DesWord="DesWord"></titleBar>


    <!--蓝牙连接图标-->
    <div class="blueToothImageDivInkeyManagePage">
      <img class="blueToothConnectedImageInkeyManagePage" :src="blueToothConnectedImage"/>
    </div>

    <div class="keyManagelistInPage">
      <mu-divider/>
      <div class="keyListDataDiv" v-for="(item,index) in keyList">
        <!--钥匙图标-->
        <img class='keyNameTypeImage' :src="item.src"/>
        <div class="keyNameDiv">
          <div class="keyNameInfo">
            <!--钥匙名称-->
            <div class="keyNameEditDiv">
              <mu-text-field type="text" v-model="item.name" hintTextClass='keyNamehintClass' hintText="请输入名称"
                             underlineClass="line" v-bind:disabled="(!item.canChangeAuth)"/>
              <img class='keyNameDeleteImage' v-on:click="clearPsw(item)" src="../../../../static/image/clearPsw.png" v-show="item.canChangeAuth"/>
            </div>
            <!--授权按钮-->
            <mu-switch :label="(item.auth == '1'||item.auth==1)?'授权钥匙':'正常钥匙'" @change="changeToggle(item,index)" labelLeft
                       v-model="item.isAuth" v-bind:disabled="isSwitchDisable || (!item.canChangeAuth)" labelClass="authTxt"
                       trackClass="authSwitch"/>
          </div>
          <mu-divider/>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        devAddress: this.$route.query.devAddress,
        keyList: [],
        origionList:[],
        currentItem: {},
        //标题栏有关属性
        name: "钥匙管理",
        isSaveVisible: true,
        isTitleBarBackImageVisible: true,
        isImg: false,
        DesWord: "保存",
        backSrc: "static/image/close.png",
        //标题栏有关属性  the end
        isSwitchDisable: false,

        keyNameValue: "xxx",
        // 蓝牙是否连接
        isBlueToothConnected: false,
        blueToothConnectedImage:require("../../../../static/image/blueToothIconGrey.png"),
      }
    },
    mounted() {
      document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
      document.getElementsByClassName("TitleName")[0].style.color = "black";
      window.updateKeyconfigListFunc = this.updateKeyconfigListFunc;
      window.setWebKeyConfigList = this.setWebKeyConfigList;
      window.setBleKeyConfigList = this.setBleKeyConfigList;
      window.onBleDisconnect = this.onBleDisconnect;
    },
    created() {
      if (this.isDebug) {
        this.keyList = [{"id": 1, "hostId": 28, "type": "4", "auth": null, "name": "测试", "value": "1", "keyInNum": "0","canChangeAuth":true}
          , {"keyInNum": "0", "name": "密码_0", "value": "0", "id": 0, "hostId": 0, "auth": "0", "type": "1","canChangeAuth":false},
          {"id": 2, "hostId": 28, "type": "2", "auth": null, "name": "测试1", "value": "2", "keyInNum": "0","canChangeAuth":true}
          , {"id": 3, "hostId": 28, "type": "0", "auth": 1, "name": "测试2", "value": "2", "keyInNum": "0","canChangeAuth":true}
          , {"id": 4, "hostId": 28, "type": "1", "auth": null, "name": "测试1", "value": "2", "keyInNum": "0","canChangeAuth":true}]
        // [{"keyInNum":"0","name":"密码_0","value":"0","id":0,"hostId":0,"auth":"0","type":"1"}];
        this.initKeyList();
      } else {
        this.toLoadKeyConfigList();
      }

    },
    watch:{
      isBlueToothConnected:function(newVal,oldVal){
        if (newVal) {
          this.blueToothConnectedImage =  require("../../../../static/image/blueToothIconBlue.png")
        } else {
          this.blueToothConnectedImage =  require("../../../../static/image/blueToothIconGrey.png")
        }
      }
    },
    computed: {
      // blueToothConnectedImage: function () {
      //   if (this.isBlueToothConnected) {
      //     return require("../../../../static/image/blueToothIconBlue.png")
      //   } else {
      //     return require("../../../../static/image/blueToothIconGrey.png")
      //   }
      // },

      types: function () {
        var temp = "";
        for (var i = 0; i < this.keyList.length; i++) {
          if (i != 0) {
            temp += ",";
          }
          temp += this.keyList[i].type;
        }
        return temp;
      },
      names: function () {
        var temp = "";
        for (var i = 0; i < this.keyList.length; i++) {
          if (i != 0) {
            temp += ",";
          }
          temp += this.keyList[i].name;
        }
        return temp;
      },
      keyIds: function () {
        var temp = "";
        for (var i = 0; i < this.keyList.length; i++) {
          if (i != 0) {
            temp += ",";
          }
          temp += this.keyList[i].value;
        }
        return temp;
      },
      auths: function () {
        var temp = "";
        for (var i = 0; i < this.keyList.length; i++) {
          if (i != 0) {
            temp += ",";
          }
          temp += this.keyList[i].auth;
        }
        return temp;
      },
      keyInNums: function () {//锁的内部编号，只在保存到蓝牙锁中使用到
        var temp = "";
        for (var i = 0; i < this.keyList.length; i++) {
          if (i != 0) {
            temp += ",";
          }
          temp += this.keyList[i].keyInNum;
        }
        return temp;
      }
    },
    methods: {
      clearPsw(item) {
        // this.keyNameValue = ""
        item.name = "";
      },

      back() {
        this.$router.go(-1)
      },
      changeToggle(item, index) {
        console.log(JSON.stringify(item));
        this.currentItem = item;
        if (this.isDebug) {
          setTimeout(() => {

            // console.log(JSON.stringify(item));
            this.currentItem.auth = this.currentItem.isAuth ? 0 : 1;
            this.currentItem.isAuth = !this.currentItem.isAuth;
            // item.isAuth = !item.isAuth == true ? false : true;

            // this.keyList[index].isAuth = !this.keyList[index].isAuth
            // this.keyList[index].label = (this.keyList[index].isAuth ? "授权" : "未授权");
            // item.label = (item.isAuth ? "授权" : "未授权");
            console.log(JSON.stringify(this.keyList[index]));
          }, 500)
        } else {

          this.toChangeToggleAfterCheckBle();
        }
      },
      toLoadKeyConfigList() {
        //获取锁的配置
        Cordova.exec((data) => {
            console.log("成功：" + data)
            // this.keyList = eval("(" + data + ")");
            // this.initKeyList();
            // this.isSwitchDisable = false;
          },
          (data) => {
            console.log("失败：" + data)
            this.showNativeToast(data);
          }, "LockPlugins",
          "getLockConfig",
          [this.devAddress]);
      },
      toChangeToggleAfterCheckBle() {
        Cordova.exec((data) => {
            console.log("成功：" + data)
            this.currentItem.auth = this.currentItem.isAuth ? 1 : 0;
            console.log("===currentItem.auth：" + this.currentItem.auth + " currentItem.isAuth" + this.currentItem.isAuth);
          },
          (data) => {
            console.log("失败：" + data)
            this.showNativeToast("蓝牙没有连接，不能更改授权");
            this.currentItem.isAuth = !this.currentItem.isAuth;
          }, "LockPlugins",
          "checkBleConnectState",
          []);
      },
      checkName() {
        for (var i = 0; i < this.keyList.length; i++) {
          if (this.keyList[i].name == "") {
            return false;
          }
        }
        return true;
      },
      toSave: function () {
        if (this.isDebug) {
          console.log("types:" + this.types);
          console.log("names:" + this.names);
          console.log("keyIds:" + this.keyIds);
          console.log("auths:" + this.auths);
          if (!this.checkName()) {
            console.log("名字为空");
            return;
          }
        } else {
          if (!this.checkName()) {
            this.showNativeToast("名字不能为空");
            return;
          }
          Cordova.exec((data) => {
              console.log("成功：" + data)
              this.showNativeToast(data);
            },
            (data) => {
              console.log("失败：" + data)
              this.showNativeToast(data);
            }, "LockPlugins",
            "batchSaveConfig",
            [this.devAddress, this.blueMac, this.types, this.names, this.keyIds, this.auths, this.keyInNums])
        }
      },
      initKeyList() {
        this.computeImageInListInRelative(this.keyList)
        this.keyList.map(function (obj) {
          obj.isAuth = obj.auth == 1 || obj.auth == "1";
        });
        console.log(JSON.stringify(this.keyList));
      },
      updateKeyconfigListFunc(data) {//从后台获取数据后更新列表
        console.log("updateKyeconfigList:" + data);
        const keys = eval("(" + data + ")")
        if (keys != null && (keys instanceof Array)) {
          this.keyList.map((obj) => {
            var kk;
            for (kk in keys) {
              if (keys[kk].value == (obj.value + "") && (keys[kk].type == (obj.type+""))) {
                obj.name = keys[kk].name;
              }
            }
          });
        }
      },
      setWebKeyConfigList(data) {//只从后台返回的数据，不能编辑授权状态
        this.isBlueToothConnected = false; 
        this.keyList = eval("(" + data + ")");
        this.initKeyList();
        this.isSwitchDisable = true;
      },
      setBleKeyConfigList(data) {//
        this.isBlueToothConnected = true; 
        this.keyList = eval("(" + data + ")");
        this.initKeyList();
        this.origionList = eval(JSON.stringify(this.keyList));
        this.isSwitchDisable = false;        
      },
      onBleDisconnect(data){//在操作钥匙的过程中蓝牙断开，则将授权状态回复为最开始状态，保存只把名称保存到服务器
        this.showNativeToast("蓝牙已断开");      
        this.isBlueToothConnected = false;  
        this.keyList.map((obj)=>{
          var kk;
            for (kk in this.origionList) {
              if (this.origionList[kk].value == (obj.value + "") && (this.origionList[kk].type == (obj.type+""))) {
                obj.auth = this.origionList[kk].auth;
                obj.isAuth = (obj.auth == 1 || obj.auth == "1");
              }
            }
        });
        this.isSwitchDisable = true;
      }

    },
  }

</script>


<style>

  .blueToothImageDivInkeyManagePage {
    position: relative;
    z-index: 999999;
    height: 64px;
    width: 32px;
    float: right;
    top: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-right: 62px;
  }

  .blueToothConnectedImageInkeyManagePage {
    z-index: 9999;
    width: 24px;
    height: 24px;
    background-size: auto 24px;
    background-repeat: no-repeat;
    vertical-align: middle;
  }

  .keyNameDeleteImage {
    width: 20px;
    height: 20px;
    margin-right: 10px;
    margin-left: 20px;
  }

  .keyNameEditDiv {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  .authSwitch {
    margin-right: 20px;
  }

  .authTxt {
    font-size: small;
    white-space: nowrap;
  }

  .keyListDataDiv {
    flex-direction: row;
    align-items: center;
    margin-top: 5px;
    margin-bottom: 5px;
    display: flex;
    width: 100%;
    padding-left: 20px;
  }

  .keyNameTypeImage {
    margin-right: 10px;
    width: 25px;
    height: 25px;
  }

  .keyNameDiv {
    display: flex;
    flex-direction: column;
  }

  .keyName {
    padding: 15px;
    text-align: left;
    font-size: small;
  }

  .keyManagelistInPage {
    background-color: white;
    font-size: large;
    height: calc(100% - 64px);
    overflow: scroll;
    margin-top: 64px;
    width: 100%;
    -webkit-overflow-scrolling: touch;
  }

  .demo-switch {
    text-align: right;
    margin-right: 20px;
  }

  .mu-appbar {
    background-color: #ffffff;
    color: #000;
  }

  .mu-icon-button-close {
    background-image: url(/static/image/close.png);
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    background-position: 1% 50%;
    background-size: 25px 25px;
  }

  html, body, wrapper {
    height: 100%;
    overflow: hidden;
  }

  .wrapperPage {
    margin-top: 64px;
    font-size: large;
    height: calc(100% - 64px);
  }

  /***********临时密码输入框样式 ************/
  .keyNameInfo {
    height: auto;
    min-height: 58px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .keyNameInfo .keyNamehintClass {
    text-align: left;
    padding-right: 20px;
    font-size: small;
  }

  .keyNameInfo .mu-text-field {
    margin-bottom: 0px;
    padding-top: 8px;
    padding-bottom: 8px;
    width: 50%;
  }

  .mu-text-field-line {
    display: none;
  }

  .mu-text-field-focus-line {
    margin-left: 20px;
    background-color: #5bc8ff;
    display: none;
  }

  .keyNameInfo .mu-text-field-content {
    padding: 0px;
  }

  /***********临时密码输入框样式 the end ************/

</style>



