var weeksTextArray = [{ name: "一", value: 0 }, { name: "二", value: 0 }, { name: "三", value: 0 }, { name: "四", value: 0 }, { name: "五", value: 0 }, { name: "六", value: 0 }, { name: "日", value: 0 }];

/* harmony default export */ var scene_timer = ({
  data: function data() {
    return {
      pageType: this.$route.query.pageType,
      //标题栏有关属性
      name: "场景定时",
      isSaveVisible: true,
      isTitleBarBackImageVisible: true,
      isImg: false,
      DesWord: "保存",
      backSrc: "static/image/close.png",
      srcImg: "static/image/add_icon1.png",
      //标题栏有关属性  the end

      sceneData: [],
      fromPage: "",
      //日期组件
      hour: "",
      mins: "",
      timerData: "",
      list: []
    };
  },


  computed: {
    // 一个计算属性的 getter
    isHaveRepeat: function isHaveRepeat() {
      var num = 0;
      for (var s = 0; s < 7; s++) {
        if (this.sceneData.week[s].value == 0) {
          num++;
        }
      }

      if (num == 7) {
        return false;
      } else {
        return true;
      }
    },

    weeksText: {
      get: function get() {
        var listForMap = this.list;
        var size = listForMap.length;
        var num = 0;
        //console.log(" 数组size  " + size)

        weeksTextArray.map(function (weeksObj) {
          num = 0;
          //遍历选中的数据
          for (var t = 0; t < size; t++) {
            //匹配到
            if (weeksObj.name == listForMap[t]) {
              //console.log(" 选中 " + weeksObj.name)
              weeksObj.value = 1;
              return;
            }
            num++;
          }
          //console.log(" 不在选中的数组中num " + num)
          //不在选中的数组中，则赋值为0
          if (num == size) {
            //console.log(" 不在选中的数组中" + weeksObj.name)
            weeksObj.value = 0;
          }
        });
        //console.log("处理后的数组 " + JSON.stringify(weeksTextArray))
        return weeksTextArray;
      },

      //进入页面赋值
      set: function set(newArray) {
        var listForAdd = [];
        newArray.map(function (newArrayObj) {
          if (newArrayObj.value == 1) {
            listForAdd.push(newArrayObj.name);
          }
        });
        this.list = listForAdd;
        console.log("改变" + stringify_default()(this.list));
      }
    }

  },

  mounted: function mounted() {
    document.getElementsByClassName("TitleBar")[0].style.background = "#ffffff";
    document.getElementsByClassName("TitleName")[0].style.color = "black";
  },
  created: function created() {
    //获取传进来的场景数据
    var sceneDataTempt = this.$route.query.sceneData;
    var fromPageName = this.$route.query.fromPageName;

    if (typeof sceneDataTempt != "undefined" && typeof fromPageName != "undefined") {
      this.sceneData = sceneDataTempt;
      this.weeksText = this.sceneData.week;
      this.fromPage = fromPageName;
      //初始化时间控件
      if (this.sceneData.time == " " || this.sceneData.time == "") {
        console.log(" 初始化时间控件 空");
        this.hour = '00', this.mins = '00';
      } else {
        this.initTimerScroll();
      }
    } else {
      console.log("++++++++传入的场景数据为空++++++++");
    }
  },


  methods: {
    initTimerScroll: function initTimerScroll() {
      this.timerData = this.sceneData.time;
      console.log("++++++++传入的场景时间++++++++" + stringify_default()(this.timerData));
    },
    //从子组件内传回的数据
    changeTimerData: function changeTimerData(timerData) {
      this.hour = timerData[0];
      this.mins = timerData[1];
    },

    clickSave: function clickSave() {
      this.sceneData.time = this.hour + ":" + this.mins;
      this.sceneData.week = this.weeksText;
      //获取到时间与重复
      console.log("++++++++修改时间后的场景数据++++++++" + stringify_default()(this.sceneData));
      if (this.isHaveRepeat) {
        if (this.fromPage == "addScene") {
          console.log("addScene");
          this.$router.push({
            path: '/addScene',
            name: 'addScene',
            query: {
              sceneData: this.sceneData,
              pageType: this.pageType
            }
          });
        } else if (this.fromPage == "editScene") {
          console.log("editScene");
          this.$router.push({
            path: '/editScene',
            name: 'editScene',
            query: {
              sceneData: this.sceneData,
              pageType: this.pageType
            }
          });
        }
      } else {
        this.showNativeToast("请选择重复");
      }
    },
    back1: function back1() {

      if (this.fromPage == "addScene") {
        console.log("addScene");
        this.$router.push({
          path: '/addScene',
          name: 'addScene',
          query: {
            sceneData: this.sceneData,
            pageType: this.pageType
          }
        });
        // } else if (this.fromPage == "editScene") {
        //   console.log("editScene")
        //   this.$router.push({
        //     path: '/editScene',
        //     name: 'editScene',
        //     query: {
        //       sceneData: this.sceneData
        //     }
        //   })
      }
    },

    //选择周一至周日
    weekSelected: function weekSelected(index) {

      if (this.weeksText[index].value == 0) {
        this.weeksText[index].value = 1;
      } else {
        this.weeksText[index].value = 0;
      }
    }
  }
});
