/************定义时间控件的分钟与小时**********/
//分钟
var minutes = new Array();
for (var i = 0; i < 60; i++) {
  //显示00 01 ... 09
  if (i < 10) {
    i = "0" + i;
  }
  minutes.push("" + i + "");
}
// //小时与分钟
// var date = {};
// for (var j = 0; j < 24; j++) {
//   //显示00 01 ... 09
//   if (j < 10) {
//     date["0" + j] = minutes;
//   } else {
//     date["" + j] = minutes;
//   }
// }
// console.log("-----"+JSON.stringify(date));

//小时
var hours = new Array();
for (var i = 0; i < 24; i++) {
  //显示00 01 ... 09
  if (i < 10) {
    i = "0" + i;
  }
  hours.push("" + i + "");
}
/************时间控件**********/

/* harmony default export */ var timerSwiper = ({
  data: function data() {
    return {
      /************时间控件**********/
      addressSlots: [{
        width: '100%',
        textAlign: 'right',
        values: hours
      }, {
        width: '100%',
        textAlign: 'left',
        values: minutes
      }],
      address: ['00', '00'],
      addressProvince: '00',
      addressCity: '00',
      single: false
      /************时间控件 the end***********/
    };
  },


  //
  props: {
    timerData: ""
  },

  created: function created() {
    this.initScroll();
  },

  //
  methods: {
    initScroll: function initScroll() {
      if (this.timerData != "") {
        var strs = new Array();

        strs = this.timerData.split(":"); //字符分割
        this.addressProvince = strs[0].trim();
        this.addressCity = strs[1].trim();
        console.log("addressProvince " + this.addressProvince + " addressCity " + this.addressCity);
        //组装时间数组
        this.address = strs;
        //  this.address.push(strs[0]);
        //  this.address.push(strs[1]);
        //         this.addressSlots[1].values = [];
        //         this.addressSlots[1].values.push(strs[1])
        this.$emit('changeTimerData', this.address);
      }
    },

    addressChange: function addressChange(value, index) {
      switch (index) {
        //小时
        case 0:
          this.addressProvince = value.trim();
          // const arr = date[value]
          // this.addressSlots[1].values = arr
          // this.addressCity = arr[0]
          // console.log("小时 value"+value+ "小时 index"+index )
          break;
        //分钟
        case 1:
          this.addressCity = value.trim();
          //console.log("分钟 value"+value)
          break;
      }
      this.address = [this.addressProvince, this.addressCity];
      //暴露选择的时间数组 this.hour this.mins
      this.$emit('changeTimerData', this.address);
    }
  }
});
